import axios from "axios";
import { ActionTypes } from "./types";
import { ApplicationInterface } from "../interfaces/application";
import { API } from "../utils/api";
import { ApplicationStepsTypes } from "../containers/Application/ApplicationSteps/ApplicationSteps";
import { PositionAPI } from "../../src/interfaces/index";
import { LocationTableInterface } from "../containers/Application/ExamCenter/ChooseExamCenter";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface loadApplicationsInterface {
  type: ActionTypes.LOAD_APPLICATIONS;
  payload: {
    applications: ApplicationInterface[] | []
  };
}

export interface setApplicationStepAction {
  type: ActionTypes.SET_APPLICATION_STEP;
  payload: {
    step: ApplicationStepsTypes
  };
}

export interface removeLocationAction {
  type: ActionTypes.REMOVE_LOCATION;
  payload: {
    course_id: string
  };
}

export interface createApplicationAction {
  type: ActionTypes.CREATE_APPLICATION;
  payload: {
    user_id: string;
    application: ApplicationInterface
  };
}

export interface initialApplicationInterface {
  user_id: string, 
  selectedPosition: PositionAPI | null, 
  currentStep: ApplicationStepsTypes
  positions: PositionAPI[] | null, 
  doTestStatus: {
    status: false,
  },
  selectedLocation: [{
    courseId: string;
    courseName: string;
    courseCode: string;
    exam_id: string | null,
    location: LocationTableInterface;
  }] | []
}

export interface initiateApplicationAction {
  type: ActionTypes.INITIATE_APPLICATION,
  payload: {
    initiate_application: initialApplicationInterface
  }
}

export interface selectPositionAction {
  type: ActionTypes.SELECT_POSITION,
  payload: {
    selectedPosition: PositionAPI | null
  }
}

export interface completeApplicationAction {
  type: ActionTypes.COMPLETE_APPLICATION,
  payload: ApplicationInterface
}

export interface addLocationAction {
  type: ActionTypes.ADD_LOCATION,
  payload: {
    courseId: string;
    courseName: string;
    courseCode: string;
    exam_id: string | null,
    location: LocationTableInterface;
  }
}

export interface applicationStoreInterface {
  user_id?: string,
  applications: ApplicationInterface[],
  apply?: {
    user_id: string;
    currentStep: ApplicationStepsTypes,
    positions: PositionAPI[] | null,
    selectedPosition: PositionAPI | null,
    selectedLocation: {
      courseId: string;
      courseName: string;
      courseCode: string;
      exam_id: string | null,
      location: LocationTableInterface;
    }[] | []
    doTestStatus: {
      status: boolean;
      marks?: number;
    };
  };
}

/**
 * * ****************************** ACTIONS *****************************
 */


export const loadApplications = async (user_id: string) => {
  try {
    const res = await axios.get<ApplicationInterface[]>(`${API.APPLICATION}/applicatons/${user_id}`);
    return {
      type: ActionTypes.LOAD_APPLICATIONS,
      payload: {
        applications: res.data
      },
    };
  } catch (error: any) {
    console.log({...error});
  }
};

export const createApplication = (user_id: string, application: ApplicationInterface): createApplicationAction => {
  return { 
    type: ActionTypes.CREATE_APPLICATION,
    payload: {
      user_id: user_id,
      application: application
    },
  };
}

export const setApplicationStep = (step: ApplicationStepsTypes): setApplicationStepAction => {
  return {
    type: ActionTypes.SET_APPLICATION_STEP,
    payload: {
      step: step
    },
  };
}

export const initiateApplication = (initial: initialApplicationInterface): initiateApplicationAction => {
  return {
    type: ActionTypes.INITIATE_APPLICATION,
    payload: {
      initiate_application: initial
    },
  };
}

export const selectPosition = (position: PositionAPI | null): selectPositionAction => {
  return {
    type: ActionTypes.SELECT_POSITION,
    payload: {
      selectedPosition: position
    },
  };
}

export const addLocation = (
  course_id: string, 
  course_name: string,
  course_code: string,
  location: LocationTableInterface,
  exam_id: string): addLocationAction => {
  return {
    type: ActionTypes.ADD_LOCATION,
    payload: {
      courseId: course_id,
      courseName: course_name,
      courseCode: course_code,
      location: location,
      exam_id: exam_id
    },
  };
}

export const removeLocation = (course_id: string): removeLocationAction => {
  return {
    type: ActionTypes.REMOVE_LOCATION,
    payload: { course_id: course_id },
  };
}