import React, { Fragment, useState } from "react";
import { NavLink } from "react-router-dom";
import Logo from "./mineduc.svg";
import { PUBLIC, NON_AUTHENTICATED } from "../../config/AppNavigations";

import {
  MdMenu,
  MdNotifications,
  MdAccountCircle,
  MdClose,
  MdFolderSpecial,
  MdPeople,
  MdLocationCity,
  MdSettings,
} from "react-icons/md";
import { RiFolderSettingsFill } from "react-icons/ri";
import UserMenu from "./UserMenu";
import { Auth, FC_Access } from "../../actions";
import { UserAccessList } from "../../utils/auth_access";
import { IoMdDocument, IoMdPaper, IoMdSettings } from "react-icons/io";
import { RiFolderOpenFill } from "react-icons/ri";
import { SiOpenaccess } from "react-icons/si";
import { FaUsersCog } from "react-icons/fa";

interface NavigationProps {
  isAuthenticated: boolean;
  Logout: Function;
  fname: string | undefined;
  lname: string | undefined;
  email: string | undefined;
  NavToogle: Function;
  navActive: boolean;
  auth: Auth;
  role: string;
}

const Navigation = ({
  isAuthenticated,
  email,
  fname,
  lname,
  Logout,
  NavToogle,
  navActive,
  auth,
  role,
}: NavigationProps) => {
  const [accountModal, setaccountModal] = useState(false);

  let SideNavLinks = (
    <Fragment>
      {FC_Access(auth, UserAccessList.MY_APPLICATION).view && (
        <NavLink
          exact
          to="/applications"
          key="Applications"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <MdFolderSpecial className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Applic..</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.QUESTION_BANK).view && (
        <NavLink
          exact
          to="/question-bank"
          key="Questions"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <IoMdPaper className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">QBank</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.QUESTION_BANK).view && (
        <NavLink
          exact
          to="/question-bank-settings"
          key="question-bank-settings"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <IoMdSettings className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Settin...</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.DOCUMENT_CHECKING).view && (
        <NavLink
          exact
          to="/document-assessment"
          key="Docs"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <RiFolderOpenFill className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">DCheck</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.SYSTEM_SETTINGS).view && (
        <NavLink
          exact
          to="/document-category"
          key="Document Category"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <RiFolderSettingsFill className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Docs</span>
        </NavLink>
      )}
      {FC_Access(auth, UserAccessList.USER_MANAGEMENT).view && (
        <NavLink
          exact
          to="/users"
          key="Users"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <FaUsersCog className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Users</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.APPLICATION_OVERRIDE).view && (
        <NavLink
          exact
          to="/application-assessment"
          key="Application Assessment"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <MdPeople className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Appl</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.ROLE_MANAGEMENT).view && (
        <NavLink
          exact
          to="/roles"
          key="roles"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <SiOpenaccess className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">roles</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.SYSTEM_SETTINGS).view && (
        <NavLink
          exact
          to="/settings"
          key="setting"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <MdSettings className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Settings</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.LOCATION_MANAGEMENT).view && (
        <NavLink
          exact
          to="/locations"
          key="Locations"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <MdLocationCity className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Location</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.SCHOOL_MANAGEMENT).view && (
        <NavLink
          exact
          to="/schools"
          key="Schools"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <RiFolderOpenFill className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Schools</span>
        </NavLink>
      )}

      {FC_Access(auth, UserAccessList.DOCUMENT_CHECKING).view && (
        <NavLink
          exact
          to="/applications-list"
          key="applications-list"
          className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
        >
          <IoMdDocument className="text-4xl block mx-auto m-0 -mb-1" />
          <span className="text-xs">Report</span>
        </NavLink>
      )}
    </Fragment>
  );

  return (
    <Fragment>
      <nav className="top-navigation bg-white shadow-md fixed inset-x-0 top-0 z-50">
        <div
          className={`${
            isAuthenticated === true
              ? "mx-auto px-3"
              : "max-w-7xl sm:px-6 lg:px-24 mx-auto"
          }`}
        >
          <div className="relative flex items-center justify-between h-16">
            <div className="px-2 flex items-center justify-center sm:items-stretch sm:justify-start">
              <div className="flex items-center">
                {isAuthenticated === true && (
                  <button
                    className="mr-3 sm:block lg:hidden"
                    aria-controls="mobile-menu"
                    aria-expanded="false"
                    onClick={() => NavToogle()}
                  >
                    {navActive === true ? (
                      <MdClose className="text-3xl animate__animated animate__rotateIn animate__faster" />
                    ) : (
                      <MdMenu className="text-3xl animate__animated animate__rotateIn animate__faster" />
                    )}
                    <span className="sr-only">Open main menu</span>
                  </button>
                )}
                {/* Left items */}
                <span>
                  <img className="w-8" src={Logo} alt="" />
                </span>
                <span className="text-2xl ml-2 font-bold uppercase text-gray-500">
                  MINEDUC
                </span>
              </div>
            </div>
            <div className="absolute inset-y-0 right-0 flex items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0">
              {/* Right side menus */}
              <div className="hidden sm:block sm:ml-6 mr-6">
                <div className="flex space-x-4 items-center justify-end text-gray-500 ">
                  {isAuthenticated === false && (
                    <Fragment>
                      {PUBLIC.map((link, i) => (
                        <NavLink
                          key={i}
                          exact
                          to={link.url}
                          className={`main-navigation`}
                        >
                          {link.title}
                        </NavLink>
                      ))}

                      {!isAuthenticated &&
                        NON_AUTHENTICATED.map((link, i) => (
                          <NavLink
                            key={i}
                            exact
                            to={link.url}
                            className={`main-navigation`}
                          >
                            {link.title}
                          </NavLink>
                        ))}
                    </Fragment>
                  )}
                </div>
              </div>

              {isAuthenticated === true && (
                <Fragment>
                  <NavLink to="#" className="ml-6 hidden sm:block">
                    <MdNotifications className="text-2xl text-gray-600" />
                  </NavLink>
                  {/* Profile dropdown */}
                  <div className="ml-6 relative">
                    <div>
                      <button
                        type="button"
                        className="flex text-sm rounded-full focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-800 focus:ring-white"
                        id="user-menu-button"
                        aria-expanded="false"
                        aria-haspopup="true"
                        onClick={() => setaccountModal(!accountModal)}
                      >
                        <span className="sr-only">Open user menu</span>
                        <MdAccountCircle className="text-3xl text-gray-600" />
                      </button>
                    </div>
                    {/* Profile menus */}
                    {accountModal === true && (
                      <>
                        <div
                          className="fixed top-0 right-0 left-0 bottom-0"
                          onClick={() => setaccountModal(false)}
                        ></div>
                        <UserMenu
                          email={email === undefined ? "" : email}
                          fname={fname === undefined ? "" : fname}
                          lname={lname === undefined ? "" : lname}
                          Logout={Logout}
                          close={() => setaccountModal(false)}
                          role={role}
                        />
                      </>
                    )}
                  </div>
                </Fragment>
              )}
            </div>
          </div>
        </div>
        {/* Mobile menu, show/hide based on menu state. */}
      </nav>

      {isAuthenticated === true && (
        <div
          className={`side-nav-container fixed w-16 pl-0 text-gray-700 bg-white  ${
            navActive === true ? "lg:left-0  left-0" : " -left-72 lg:left-0"
          } top-0 bottom-0 pt-20 z-40 border-r-2 border-gray-300  transition-all duration-200 ease-in-out`}
        >
          <NavLink
            exact
            to="/profile"
            key="Profile details"
            className={`block py-1 rounded-md- px-3 hover:bg-blue-500 hover:text-white text-center  border-b- border-gray-200 `}
          >
            <MdAccountCircle className="text-4xl block mx-auto m-0 -mb-1" />
            <span className="text-xs">Profile</span>
          </NavLink>

          {SideNavLinks}
        </div>
      )}

      {navActive === true && (
        <div
          onClick={() => NavToogle()}
          className="z-10 bg-black sm:block lg:hidden bg-opacity-20 fixed top-0 right-0 left-0 bottom-0"
        ></div>
      )}
    </Fragment>
  );
};

export default Navigation;
