import React, { Fragment, useState } from "react";
import { FaRegCheckCircle, FaRegUserCircle } from "react-icons/fa";
import { IoDocumentOutline } from "react-icons/io5";
import { MdInfo, MdRemoveCircleOutline } from "react-icons/md";
import { ImFilesEmpty } from "react-icons/im";
import { HiOutlineDuplicate } from "react-icons/hi";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import {
  getDocumentCategory,
  getDocumentSubCategory,
} from "../../actions/document";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  ApplicationAssessmentInterface,
  CandidateDetailsInterface,
  PositionRequirementInterface,
} from "../../shared/interfaces";
import { countByStatus } from "../DocumentChecking/DocumentChecking";
import { BiDetail } from "react-icons/bi";
import { RiListSettingsLine } from "react-icons/ri";
import { capitalizeFirstLetter } from "../../shared/dataTest";
import { reAssessApplication } from "../../actions/applicationAssessment";
import axios from "axios";
import { API } from "../../utils/api";
import { Auth, PushPopupAlert } from "../../actions";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import { ApplicationStates } from "../../interfaces/application";

interface AppProps {
  auth: Auth;
  documentCategories: DocumentReducerInterface;
  thisApplication: ApplicationAssessmentInterface;
  setThisApplication: React.Dispatch<
    React.SetStateAction<ApplicationAssessmentInterface | null>
  >;
  reAssessApplication: Function;
  setDoc: React.Dispatch<
    React.SetStateAction<CandidateDetailsInterface | null>
  >;
  setDocTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocSubTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldTitle: React.Dispatch<React.SetStateAction<string>>;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const UserDocuments: React.FC<AppProps> = ({
  auth,
  thisApplication,
  documentCategories,
  reAssessApplication,
  setDoc,
  setDocTitle,
  setDocSubTitle,
  setDocFieldTitle,
  PushPopupAlert,
}) => {
  // setCandidateCheckData(candidate);
  const [tabsController, setTabsController] = useState<
    "waiting" | "approved" | "rejected"
  >("approved");
  const [tabsCategory, setTabsCategory] = useState<"profile" | "requirements">(
    "requirements"
  );
  const [override, setOverride] = useState<"" | ApplicationStates>("");
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);

  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [reason, setReason] = useState<string>("");

  const isRequired = (
    requirements: PositionRequirementInterface[],
    filterValue: string
  ): boolean => {
    return requirements.filter(
      (item) => item.user_detail_category_id === filterValue
    ).length > 0
      ? true
      : false;
  };

  const countById = (
    userDocs: CandidateDetailsInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus",
    filterValue: string
  ): number => {
    console.log({ userDocs });
    let result: CandidateDetailsInterface[] = userDocs;
    // result = userDocs.filter(
    //   (details) => details.validation_status === docStatus && details.files
    // );
    if (filterType === "docCategory") {
      result = userDocs.filter(
        (details) =>
          details.category_id === filterValue && details.files.length > 0
      );
    } else if (filterType === "docSubCategory") {
      result = userDocs.filter(
        (details) =>
          details.sub_category_id === filterValue && details.files.length > 0
      );
    }
    // if (docStatus === "approved") {
    //   result = userDocs.filter(
    //     (details) => details.validation_status !== "waiting"
    //   );
    // }
    return result.length;
  };

  const overrideDecision = async () => {
    if (reason === "") {
      return setFormError({
        element: "comment",
        msg: "Please fill the reason of override",
      });
    }

    setLoadingRequest(true);

    try {
      const res = await axios.patch(
        `${API.APPLICATION}/tolerateappl/${thisApplication.application_id}`,
        {
          user_id: auth.user.user_id,
          comment: reason,
          application_status: override,
        }
      );

      res.data &&
        reAssessApplication({
          application_id: thisApplication.application_id,
          application_status: override,
        });
      setOverride("");
      setReason("");

      // modalState && application;
      // setModalState("")
      // setApplication("")

      PushPopupAlert({
        title: `Overriding application done successfully`,
        theme: PopupAlertTheme.success,
      });
    } catch (error) {
      console.error({ code_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      // errorToText(error);
    }
    setLoadingRequest(false);
  };

  return (
    <div className="bg-gray-100 py-2 px-4">
      {/* tabs status */}
      <div className="w-full mx-auto mt-1 bg-white flex items-center justify-evenly border border-gray-200 rounded">
        <section
          className="cursor-pointer"
          onClick={() => setTabsCategory("requirements")}
        >
          <div
            className={`flex items-center py-4 px-2 text-gray-700 gap-1 ${
              tabsCategory === "requirements" && "border-b-4 border-gray-700"
            }`}
          >
            <RiListSettingsLine className="w-6 h-6" />
            <span>Position Requirements</span>
          </div>
        </section>
      </div>
      {/* end tabs status */}
      <div className="h-full overflow-y-auto">
        {/* ================================================================= */}
        <div className="w-full flex flex-col md:items-start overflow-y-auto">
          {/* ======================================================= */}
          {/* POSITION for postion info */}
          {tabsCategory === "requirements" && (
            <section className="h-full w-full mx-auto pt-2">
              {/* position description */}
              <div className="flex items-center font-semibold uppercase text-xl">
                <HiOutlineDuplicate className="font-semibold w-6 h-6 mr-1" />
                Position Description
              </div>
              <div className="bg-white border border-gray-200 rounded my-3">
                <section className="border-gray-200 -mt-1 pt-2 mb-5">
                  <div className="flex flex-col mx-2 gap-1 my-1">
                    <span className="text-sm text-gray-500 uppercase whitespace-no-wrap">
                      Title:
                    </span>
                    {console.log({ thisApplication })}
                    <p className="font-semibold">
                      {thisApplication.position.position_title}
                    </p>
                  </div>

                  <div className="flex flex-col mx-2 gap-1 my-1">
                    <span className="text-sm text-gray-500 uppercase whitespace-no-wrap">
                      Description:
                    </span>
                    <p className="font-medium">
                      {thisApplication.position.description}
                    </p>
                  </div>
                </section>
              </div>
              {/* end position description */}

              {/* position Requirements */}
              <div className="my-3">
                <div className="flex items-center font-semibold uppercase text-xl">
                  <BiDetail className="font-semibold w-6 h-6 mr-1" />
                  Position Requirements
                </div>

                <section className="bg-white border rounde border-gray-200 mt-2 pt-2 mb-2">
                  {documentCategories.documentCategory.map(
                    (category, index) => (
                      <Fragment key={index + category.user_detail_category_id}>
                        {isRequired(
                          thisApplication.position.requirements,
                          category.user_detail_category_id
                        ) &&
                          countById(
                            thisApplication.details,
                            "docCategory",
                            category.user_detail_category_id
                          ) > 0 && (
                            <section className="px-3">
                              <section
                                className={`bg-blue-100 text-blue-800 mb-1 hover:font-bold py-2 --font-bold px-2 text-2xl rounded-md flex items-center`}
                              >
                                <section className="flex-1">
                                  <span>{category.title}</span>
                                </section>

                                {/* {count > 0 ? ( */}
                                <span className="px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full">
                                  {countById(
                                    thisApplication.details,
                                    "docCategory",
                                    category.user_detail_category_id
                                  )}
                                </span>
                                {/* ) : null} */}
                              </section>

                              {/* list of docs */}
                              <section className="w-full h-full my-3">
                                {/* doc card */}
                                {thisApplication.details.filter(
                                  (itm) => itm.files.length > 0
                                ).length > 0 ? (
                                  thisApplication.details
                                    .filter((itm) => itm.files.length > 0)
                                    .map((item, index) => (
                                      <div
                                        key={index + item.doc_id}
                                        className="bg-white border border-gray-200 rounded mb-3 p-4 flex justify-between items-start"
                                      >
                                        {/* doc title */}
                                        <div>
                                          <h2 className="text-balck font-semibold text-xl">
                                            {item.sub_category_id
                                              ? documentCategories.documentSubCategory.filter(
                                                  (c) =>
                                                    c.user_detail_subcategory_id ===
                                                    item.sub_category_id
                                                )[0].title
                                              : item.category_id
                                              ? documentCategories.documentCategory.filter(
                                                  (doc) =>
                                                    doc.user_detail_category_id ===
                                                    item.category_id
                                                )[0].title
                                              : item.title
                                              ? item.title
                                              : "No Category or Title Available"}
                                          </h2>
                                          {/* status */}
                                          <div className="flex items-center gap-2">
                                            <span className="text-gray-500 italic">
                                              Status:
                                            </span>
                                            <span
                                              className={`text-${
                                                item.validation_status ===
                                                "waiting"
                                                  ? "yellow"
                                                  : item.validation_status ===
                                                    "approved"
                                                  ? "blue"
                                                  : "gray"
                                              }-800`}
                                            >
                                              {item.validation_status ===
                                              "waiting"
                                                ? "Pending"
                                                : item.validation_status ===
                                                  "approved"
                                                ? "Approved"
                                                : "Rejected"}
                                            </span>
                                          </div>
                                          {/* end status */}
                                        </div>
                                        {/* end doc title */}
                                        <button
                                          className="bg-blue-600 p-2 text-white flex rounded items-center space-x-2 h-max focus:outline-none"
                                          onClick={() => {
                                            setDoc(item);
                                            setDocTitle(
                                              item.category_id
                                                ? documentCategories.documentCategory.filter(
                                                    (doc) =>
                                                      doc.user_detail_category_id ===
                                                      item.category_id
                                                  )[0].title
                                                : item.title
                                                ? item.title
                                                : "No Category or Title Available"
                                            );
                                            setDocSubTitle(
                                              item.sub_category_id
                                                ? documentCategories.documentSubCategory.filter(
                                                    (c) =>
                                                      c.user_detail_subcategory_id ===
                                                      item.sub_category_id
                                                  )[0].title
                                                : ""
                                            );
                                            console.log({
                                              fields:
                                                documentCategories.documentFields,
                                              fieldId:
                                                item.category_subcategory_data,
                                            });
                                            setDocFieldTitle(
                                              item.category_subcategory_data
                                                ? documentCategories.documentFields.filter(
                                                    (c) =>
                                                      c._id ===
                                                      item.category_subcategory_data
                                                  ).length > 0
                                                  ? documentCategories.documentFields.filter(
                                                      (c) =>
                                                        c._id ===
                                                        item.category_subcategory_data
                                                    )[0].title
                                                  : ""
                                                : ""
                                            );
                                          }}
                                        >
                                          <IoDocumentOutline className="w-5 h-5" />
                                          View doc
                                        </button>
                                      </div>
                                    ))
                                ) : (
                                  <div className="bg-white border border-gray-200 rounded mb-3 p-4 text-gray-400 text-center">
                                    <ImFilesEmpty className="w-28 h-28 mx-auto mb-3" />
                                    <span className="text-2xl font-mono font-semibold tracking-tighter">
                                      No Data Found
                                    </span>
                                  </div>
                                )}

                                {/* end doc card */}
                              </section>
                              {/* end list of docs */}
                            </section>
                          )}
                      </Fragment>
                    )
                  )}
                </section>
              </div>
              {/* end position Requirements */}
            </section>
          )}
          {/* end POSITION for postion info */}

          {/* ==================================================================== */}
          {/* PROFILE MANAGEMENT */}
          {tabsCategory === "profile" && (
            <section className="w-full h-full my-3 mx-auto">
              {/* tabs status */}
              <div className="w-full bg-white flex items-center justify-evenly border border-gray-200 rounded">
                <section
                  className="cursor-pointer"
                  onClick={() => setTabsController("approved")}
                >
                  <div
                    className={`flex items-center py-4 px-2 text-blue-700 gap-1 ${
                      tabsController === "approved" &&
                      " border-b-4 border-blue-700"
                    }`}
                  >
                    <FaRegCheckCircle className="w-6 h-6" />
                    <span>Approved</span>
                    <span>
                      ({countByStatus(thisApplication.details, "approved")})
                    </span>
                  </div>
                </section>
                <section
                  className="cursor-pointer"
                  onClick={() => setTabsController("rejected")}
                >
                  <div
                    className={`flex items-center py-4 px-2 text-gray-700 gap-1 ${
                      tabsController === "rejected" &&
                      "border-b-4 border-gray-700"
                    }`}
                  >
                    <MdRemoveCircleOutline className="w-6 h-6" />
                    <span>Rejected</span>
                    <span>
                      ({countByStatus(thisApplication.details, "rejected")})
                    </span>
                  </div>
                </section>
                <section
                  className="cursor-pointer"
                  onClick={() => setTabsController("waiting")}
                >
                  <div
                    className={`flex items-center py-4 px-2 text-yellow-800 gap-1 ${
                      tabsController === "waiting" &&
                      "border-b-4 border-yellow-800"
                    }`}
                  >
                    <MdInfo className="w-6 h-6" />
                    <span>Pending</span>
                    <span>
                      ({countByStatus(thisApplication.details, "waiting")})
                    </span>
                  </div>
                </section>
              </div>
              {/* end tabs status */}

              {/* ============================================================================= */}

              {/* list of docs */}
              <section className="w-full h-full my-3">
                {/* doc card */}
                {thisApplication.details.filter(
                  (itm) =>
                    itm.validation_status === tabsController &&
                    itm.files.length > 0
                ).length > 0 ? (
                  thisApplication.details
                    .filter(
                      (itm) =>
                        itm.validation_status === tabsController &&
                        itm.files.length > 0
                    )
                    .map((item, index) => (
                      <div
                        key={index + item.doc_id}
                        className="bg-white border border-gray-200 rounded mb-3 p-4 flex justify-between items-start"
                      >
                        {/* doc title */}
                        <div>
                          <h2 className="text-balck font-semibold text-xl">
                            {item.sub_category_id
                              ? documentCategories.documentSubCategory.filter(
                                  (c) =>
                                    c.user_detail_subcategory_id ===
                                    item.sub_category_id
                                )[0].title
                              : item.category_id
                              ? documentCategories.documentCategory.filter(
                                  (doc) =>
                                    doc.user_detail_category_id ===
                                    item.category_id
                                )[0].title
                              : item.title
                              ? item.title
                              : "No Category or Title Available"}
                          </h2>
                          {/* status */}
                          <div className="flex items-center gap-2">
                            <span className="text-gray-500 italic">
                              Status:
                            </span>
                            <span
                              className={`text-${
                                item.validation_status === "waiting"
                                  ? "yellow"
                                  : item.validation_status === "approved"
                                  ? "blue"
                                  : "gray"
                              }-800`}
                            >
                              {item.validation_status === "waiting"
                                ? "Pending"
                                : item.validation_status === "approved"
                                ? "Approved"
                                : "Rejected"}
                            </span>
                          </div>
                          {/* end status */}
                        </div>
                        {/* end doc title */}
                        <button
                          className="bg-blue-600 p-2 text-white flex rounded items-center space-x-2 h-max focus:outline-none"
                          onClick={() => {
                            // setDoc(item);
                            // setDocTitle(
                            //   item.category_id
                            //     ? documentCategories.documentCategory.filter(
                            //         (doc) =>
                            //           doc.user_detail_category_id === item.category_id
                            //       )[0].title
                            //     : item.title
                            //     ? item.title
                            //     : "No Category or Title Available"
                            // );
                            // setDocSubTitle(
                            //   item.sub_category_id
                            //     ? documentCategories.documentSubCategory.filter(
                            //         (c) =>
                            //           c.user_detail_subcategory_id ===
                            //           item.sub_category_id
                            //       )[0].title
                            //     : ""
                            // );
                            // setDocIndex(index);
                          }}
                        >
                          <IoDocumentOutline className="w-5 h-5" />
                          View doc
                        </button>
                      </div>
                    ))
                ) : (
                  <div className="bg-white border border-gray-200 rounded mb-3 p-4 text-gray-400 text-center">
                    <ImFilesEmpty className="w-28 h-28 mx-auto mb-3" />
                    <span className="text-2xl font-mono font-semibold tracking-tighter">
                      No Data Found
                    </span>
                  </div>
                )}

                {/* end doc card */}
              </section>
              {/* end list of docs */}
            </section>
          )}
          {/* end PROFILE MANAGEMENT */}
        </div>
        {/* ================================================================= */}
        {/* footer buttons */}
        <div className="w-full mx-auto mt-1 py-3 bg-white flex items-center justify-between border border-gray-200 rounded">
          <div className="flex items-center px-3">
            <span className="mr-1 text-gray-600">Status:</span>
            <span
              className={`text-xl ${
                thisApplication.application_status === "rejected"
                  ? "text-red-700"
                  : thisApplication.application_status === "initiated"
                  ? "text-gray-700"
                  : "text-blue-700"
              }`}
            >
              {capitalizeFirstLetter(thisApplication.application_status)}
            </span>
          </div>
          <div className="mr-3">
            {override ? (
              <div className="flex items-center">
                <div className="max-w-sm mx-auto p-1 pr-0 flex items-start">
                  <div>
                    <textarea
                      placeholder="Reason of your decision"
                      className={`flex-1 appearance-none rounded border-2 px-3 py-1 text-grey-dark mr-2 focus:outline-none focus:bg-gray-50 ${
                        formError !== null && formError.element === "name"
                          ? "border-red-500"
                          : "border-gray-700"
                      }`}
                      onChange={(e) => setReason(e.target.value)}
                      value={reason}
                      style={{ resize: "none" }}
                    ></textarea>
                  </div>
                  <button
                    type="button"
                    className="appearance-none btn btn-blue text-white text-base font-semibold tracking-wide p-3 rounded shadow hover:bg-indigo-light focus:outline-none"
                    onClick={() => !loadingRequest && overrideDecision()}
                  >
                    {loadingRequest ? "Loading..." : "Confirm"}
                  </button>
                </div>
              </div>
            ) : (
              <div className="flex items-center">
                {thisApplication.application_status === "rejected" ? (
                  <button
                    className="btn btn-blue focus:outline-none"
                    onClick={() =>
                      !loadingRequest &&
                      setOverride(ApplicationStates.Shortlisted)
                    }
                  >
                    Short List
                  </button>
                ) : (
                  <button
                    className="btn bg-red-700 text-white focus:outline-none"
                    onClick={() =>
                      !loadingRequest && setOverride(ApplicationStates.Rejected)
                    }
                  >
                    Reject
                  </button>
                )}
              </div>
            )}
          </div>
        </div>
        {/* end footer buttons */}
      </div>
    </div>
  );
};

const mapStateToProps = ({
  auth,
  document,
}: StoreState): {
  auth: Auth;
  documentCategories: DocumentReducerInterface;
} => {
  return { auth, documentCategories: document };
};

export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  reAssessApplication,
  PushPopupAlert,
})(UserDocuments);
