import React, { Fragment, useEffect, useState } from "react";
import { MdErrorOutline, MdClose, MdLock } from "react-icons/md";
import Table from "../../components/Table/Table";

import { capitalizeFirstLetter, getRoleById } from "../../shared/dataTest";
import { connect } from "react-redux";
import {
  AccessTaskInterface,
  DistrictInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
  SchoolsInterface,
  SystemUserRoleInterface,
  UserInterface,
} from "../../shared/interfaces";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import profile from "../../shared/blank_profile.png"; // with im
import UserAccess from "./UserAccess";
import UserRole from "./UserRole";
import {
  blockUserToSystem,
  unBlockUserToSystem,
  resetUserAccess,
} from "../../actions/users";
import { errorToText } from "../../utils/errors";
import axios from "axios";
import { API } from "../../utils/api";
import Loading from "../../components/Loading/Loading";

export enum ModalState {
  View_User = "View User Info",
  Add_Access = "Add Access To User",
  Update_Access = "Update User Access",
  Close_Modal = "Close Modal",
}

interface AppProps {
  users: UserInterface[];
  roleList: SystemUserRoleInterface[];
  userTypes: "all" | "active" | "blocked";
  district: DistrictInterface[];
  blockUserToSystem: Function;
  unBlockUserToSystem: Function;
  resetUserAccess: Function;
}

const UserList: React.FC<AppProps> = ({
  users,
  roleList,
  userTypes,
  district,
  blockUserToSystem,
  unBlockUserToSystem,
  resetUserAccess,
}) => {
  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>({ type: "", message: "" });
  const [manageUser, setManageUser] = useState<UserInterface | null>();
  const [manageUserId, setManageUserId] = useState<string>("");
  const [changeRole, setChangeRole] = useState<boolean>(false);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [schoolList, setSchoolList] = useState<SchoolsInterface[] | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [roomList, setRoomList] = useState<RoomLocationInterface[] | null>(
    null
  );
  const [roomTable, setRoomTables] = useState<
    LocationRoomTableInterface[] | null
  >(null);
  const [userAccess, setUserAccess] = useState<AccessTaskInterface[] | null>(
    null
  );
  const [currentAccess, setCurrentAccess] =
    useState<AccessTaskInterface | null>(null);

  const tableData = (): JSX.Element[] => {
    let data: UserInterface[] = users;
    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={8}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((user, index) => (
      <tr key={index} className="border-b border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>{user.user_info.fname + " " + user.user_info.lname}</span>
        </td>
        <td className="p-3 text-left">{user.email}</td>
        {/* <td className="p-3 text-left">{user.phone_number}</td> */}
        <td className="p-3 text-left">
          {user.user_info.sex === "M" ? "Male" : "Female"}
        </td>
        <td className="p-3 text-left text-blue-700 text-base font-medium">
          {user.default_access &&
            user.default_access.system_user_role_id &&
            user.default_access.system_user_role_id && (
              <span
                className={`px-2 py-1 font-semibold rounded text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100`}
              >
                {getRoleById(roleList, user.default_access.system_user_role_id)
                  ?.role &&
                  capitalizeFirstLetter(
                    getRoleById(
                      roleList,
                      user.default_access.system_user_role_id
                    )?.role
                  )}
              </span>
            )}
        </td>
        {/* {userTypes === "all" && (
          <td>
            {user.is_active ? (
              <span className={`px-2 py-1 font-semibold text-green-700`}>
                Active
              </span>
            ) : (
              <span className={`px-2 py-1 font-semibold text-red-700`}>
                Blocked
              </span>
            )}
          </td>
        )} */}
        <td className="px-3 text-center">
          <button
            className="btn bg-gray-200 hover:bg-blue-700 hover:text-white focus:outline-none"
            title="Manage User Access"
            onClick={() => {
              setManageUser(user);
              setManageUserId(user.user_id);
            }}
          >
            Manage
          </button>
        </td>
      </tr>
    ));
  };

  const blockUser = (
    user_id: string,
    actionType: "block" | "un-block" = "block"
  ): void => {
    setFeedBack({
      type: "",
      message: "",
    });
    if (!user_id) {
      setFeedBack({
        type: "warning",
        message: "User Access is required doesn't exist",
      });
    }

    setLoadingRequest(true);

    if (!window.confirm(`Are sure you want to ${actionType} this user?`)) {
      return setLoadingRequest(false);
    }
    actionType === "block"
      ? blockUserToSystem(user_id)
      : unBlockUserToSystem(user_id);

    setFeedBack({
      type: "success",
      message: `User has been ${
        actionType === "block" ? "block" : "unblock"
      }ed!`,
    });

    setLoadingRequest(false);
    user_id = "";
  };

  const restUserAccess = async (user_id: string) => {
    setFeedBack({
      type: "",
      message: "",
    });
    if (!user_id) {
      setFeedBack({
        type: "warning",
        message: "User Access is required doesn't exist",
      });
    }

    setLoadingRequest(true);

    try {
      const res = await axios.delete(`${API.AUTH}/custaccess/user/${user_id}`);
      res && resetUserAccess(user_id);
      let resetedAccess: UserInterface = users.filter(
        (user) => user.user_id === user_id
      )[0];
      resetedAccess.default_access &&
        setUserAccess(resetedAccess.default_access.access);
      setFeedBack({
        type: "success",
        message: `Reseting User Access complete successfully`,
      });
      setCurrentAccess(null);
    } catch (error) {
      console.error({ reseting_user_access: error });
      setFeedBack({
        type: "danger",
        message: errorToText(error),
      });
    }

    setLoadingRequest(false);
    user_id = "";
  };

  // Tobe replaced with Emmy's function
  const LocationTable = (
    LocationData: RoomLocationInterface[]
  ): LocationRoomTableInterface[] | [] => {
    let locationTemp: LocationRoomTableInterface[] = [];
    for (let p in LocationData) {
      let districts = LocationData[p].districts;
      for (let d in districts) {
        let centers = districts[d].centers;
        for (let c in centers) {
          let rooms = centers[c].rooms;
          for (let r in rooms) {
            locationTemp.push({
              province_id: LocationData[p].province_id,
              province_name: LocationData[p].province_name,
              district_id: districts[d].district_id,
              district_name: districts[d].district_name,
              center_id: centers[c].examination_center_id,
              center_name: centers[c].center_name,
              room_id: rooms[r].examination_center_room_id,
              room_name: rooms[r].room_number.toString(),
              capacity: rooms[r].capacity,
              vacants: Math.round(Math.random() * 10),
            });
          }
        }
      }
    }
    return locationTemp;
  };

  const loadSchools = async () => {
    console.log("jk");
    setFeedBack({ type: "", message: "" });
    setLoading(true);
    try {
      const res = await axios.get<SchoolsInterface[]>(
        `${API.APPLICATION}/schools`
      );
      setSchoolList(res.data);
      setLoading(false);
    } catch (error) {
      setFeedBack({ type: "danger", message: errorToText(error) });
    }
  };

  useEffect(() => {
    const loadRooms = async () => {
      setFeedBack({ type: "", message: "" });
      setLoading(true);

      try {
        const res = await axios.get<RoomLocationInterface[]>(
          `${API.APPLICATION}/roomlocation`
        );
        setRoomList(res.data);
        setRoomTables(LocationTable(res.data));
        setLoading(false);
      } catch (error) {
        setFeedBack({ type: "danger", message: errorToText(error) });
      }
    };

    loadSchools();
    loadRooms();
  }, []);

  if (loading === true) {
    return <Loading />;
  }

  return (
    <Fragment>
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div className="grid md:grid-cols-5 gap-2">
        <div className={`md:col-span-5 rounded`}>
          <div className="my-2 flex sm:flex-row flex-col justify-between">
            <div className="flex flex-col md:flex-row gap-1 items-center mx-1.5">
              {/* <span className="text-lg">All User</span>
              <MdChevronRight className="w-7 h-7" />
              <span className="text-lg underline">All User</span> */}
            </div>
            {/* will be used later */}
            {/* <div className="flex flex-row items-center gap-1">
              <button className="btn flex items-center p-1 bg-red-700 hover:bg-red-800 text-gray-100">
                <AiFillFilePdf className="h-6 w-6" />
                <p className=" text-lg font-medium ml-2 ">PDF</p>
              </button>

              <button className="btn flex items-center p-1 bg-green-700 hover:bg-green-800 text-gray-100">
                <RiFileExcel2Fill className="w-6 h-6" />
                <div>
                  <p className=" text-lg font-medium ml-2 ">EXCEL</p>
                </div>
              </button>
            </div> */}
          </div>
          {users && (
            <Table
              columns={[
                { columnTitle: "#", alignText: "left", padding: "p-3" },
                { columnTitle: "Names", alignText: "left", padding: "p-3" },
                { columnTitle: "Email", alignText: "left", padding: "p-3" },
                { columnTitle: "Gender", alignText: "left", padding: "p-3" },
                { columnTitle: "Role", alignText: "left", padding: "p-3" },
                // { columnTitle: "Status", alignText: "left", padding: "p-3" },
                { columnTitle: "", alignText: "left", padding: "p-3" },
              ]}
              rows={tableData}
              pagination={true}
              search={true}
              thColor="gray"
            />
          )}
        </div>
      </div>

      {/* modal control */}
      {manageUser && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setManageUser(null);
            setChangeRole(false);
            setFeedBack({ message: "", type: "" });
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="text-gray-600 font-bold text-3xl md:ml-4">
              {manageUser.user_info.fname}'s access setting
            </div>
          }
          widthSizeClass={ModalSize.maxWidth}
          padding={{ title: true, body: false, footer: false }}
        >
          {/* User Access Select */}
          <div className="bg-gray-100 h-auto px-12 py-3">
            {feedBack && feedBack.type !== "" && (
              <div
                className={`alert-${feedBack.type} flex flex-row items-center relative mb-4`}
              >
                <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
                  <MdClose
                    className="h-5 w-5 text-gray-700 hover:font-bold"
                    title="Close"
                    onClick={() => setFeedBack({ type: "", message: "" })}
                  />
                </span>
                <MdErrorOutline className="h-10 w-10" />
                <div className="alert-content ml-4">
                  <div className="alert-title font-semibold text-lg">
                    {feedBack.type === "success" ? "Success" : "Error"}
                  </div>
                  <div className="alert-description text-sm">
                    {feedBack.message}
                  </div>
                </div>
              </div>
            )}

            {/* header */}
            <div className="bg-white shadow-md px-4 py-2 flex flex-col md:flex-row items-center gap-x-14">
              {users
                .filter((user) => user.user_id === manageUserId)
                .map((userItem) => (
                  <div
                    key={userItem.user_id}
                    className="flex items-center gap-3"
                  >
                    <img
                      src={
                        userItem.user_info.photo !== "pic"
                          ? userItem.user_info.photo
                          : profile
                      }
                      alt=""
                      className="rounded-full w-32 h-32"
                    />
                    <div className="flex flex-col gap-1">
                      <span className="text-2xl font-semibold">
                        {userItem.user_info.fname +
                          " " +
                          userItem.user_info.lname}
                      </span>
                      <div className="flex items-center">
                        <MdLock className="w-5 h-5" />
                        <span className="text-lg font-medium">
                          {userItem.default_access &&
                            getRoleById(
                              roleList,
                              userItem.default_access.system_user_role_id
                            ).role}
                        </span>
                      </div>
                      <div className="flex items-center gap-1">
                        <span className="text-gray-500">Level:</span>
                        <span className="text-base font-medium">
                          {userItem.default_access &&
                          getRoleById(
                            roleList,
                            userItem.default_access.system_user_role_id
                          ).district_required
                            ? "District"
                            : userItem.default_access &&
                              getRoleById(
                                roleList,
                                userItem.default_access.system_user_role_id
                              ).school_required
                            ? "School"
                            : userItem.default_access &&
                              getRoleById(
                                roleList,
                                userItem.default_access.system_user_role_id
                              ).room_required
                            ? "Room"
                            : "-"}
                        </span>
                      </div>
                      {userItem.default_access &&
                      getRoleById(
                        roleList,
                        userItem.default_access.system_user_role_id
                      ).district_required ? (
                        <div className="flex items-center gap-1">
                          <span className="text-gray-500">District:</span>
                          <span className="text-base font-medium">
                            {userItem.default_access &&
                              district &&
                              district.filter(
                                (dist) =>
                                  dist.district_id ===
                                  userItem.default_access?.district_id
                              )[0].district_name}
                          </span>
                        </div>
                      ) : userItem.default_access &&
                        getRoleById(
                          roleList,
                          userItem.default_access.system_user_role_id
                        ).school_required ? (
                        <div className="flex items-center gap-1">
                          <span className="text-gray-500">School:</span>
                          <span className="text-base font-medium">
                            {schoolList &&
                              schoolList.find(
                                (school) =>
                                  school._id ===
                                  userItem.default_access?.school_id
                              )?.school_name}
                          </span>
                        </div>
                      ) : (
                        userItem.default_access &&
                        getRoleById(
                          roleList,
                          userItem.default_access.system_user_role_id
                        ).room_required && (
                          <div className="flex items-center gap-1">
                            <span className="text-gray-500">Room:</span>
                            <span className="text-base font-medium">
                              {roomTable &&
                                roomTable.find(
                                  (room) =>
                                    room.room_id ===
                                    userItem.default_access?.room_id
                                )?.room_name}
                            </span>
                          </div>
                        )
                      )}
                    </div>
                  </div>
                ))}
              {users
                .filter((user) => user.user_id === manageUserId)
                .map((userItem) => (
                  <div key={userItem.user_id} className="flex flex-col">
                    <div className="flex items-center gap-1">
                      <span className="text-gray-500">NID:</span>
                      <span className="text-base font-medium">
                        {userItem.n_id}
                      </span>
                    </div>
                    <div className="flex items-center gap-1">
                      <span className="text-gray-500">Email:</span>
                      <span className="text-base font-medium">
                        {userItem.email}
                      </span>
                    </div>
                    <div className="flex items-center gap-1">
                      <span className="text-gray-500">Phone:</span>
                      <span className="text-base font-medium">
                        {userItem.phone_number}
                      </span>
                    </div>
                    <div className="flex items-center gap-1">
                      <span className="text-gray-500">Status:</span>
                      <span className="text-base font-medium">
                        {userItem.is_active === false ? "Blocked" : "Active"}
                      </span>
                    </div>
                    <div className="flex items-center gap-1">
                      {userItem.custome_access && (
                        <button
                          className="bg-gray-600 px-2 py-1 font-semibold text-gray-50 inline-flex items-center rounded"
                          onClick={() => restUserAccess(userItem.user_id)}
                          disabled={loadingRequest}
                        >
                          <MdLock className="w-4 h-4 fill-current" />
                          <span>Reset Access</span>
                        </button>
                      )}
                      {!changeRole && (
                        <button
                          className="bg-yellow-600 px-2 py-1 font-semibold text-black inline-flex items-center rounded"
                          onClick={() => setChangeRole(true)}
                        >
                          <MdLock className="w-4 h-4 fill-current" />
                          <span>Change Role</span>
                        </button>
                      )}
                      {userItem.is_active ? (
                        <button
                          className="bg-red-700 px-2 py-1 font-semibold text-gray-100 inline-flex items-center rounded"
                          onClick={() =>
                            !loadingRequest &&
                            manageUser &&
                            blockUser(userItem.user_id)
                          }
                          disabled={loadingRequest}
                        >
                          <MdLock className="w-4 h-4 fill-current" />
                          <span>Block</span>
                        </button>
                      ) : (
                        <button
                          className="bg-yellow-700 px-2 py-1 font-semibold hidden text-gray-100 inline-flex items-center rounded"
                          onClick={() =>
                            !loadingRequest &&
                            manageUser &&
                            blockUser(userItem.user_id)
                          }
                          disabled={loadingRequest}
                        >
                          <MdLock className="w-4 h-4 fill-current" />
                          <span>Unblock</span>
                        </button>
                      )}
                    </div>
                  </div>
                ))}
            </div>
            {/* end header */}

            {/* content */}
            {manageUser && changeRole ? (
              <UserRole
                userInfo={manageUser}
                roleList={roleList}
                district={district}
                feedBack={feedBack}
                setFeedBack={setFeedBack}
                schoolList={schoolList}
                roomList={roomList}
                setChangeRole={setChangeRole}
              />
            ) : (
              <UserAccess
                userInfo={
                  users.filter((user) => user.user_id === manageUserId)[0]
                }
                manageUserId={manageUserId}
                feedBack={feedBack}
                setFeedBack={setFeedBack}
                setUserAccess={setUserAccess}
                userAccess={userAccess}
                setCurrentAccess={setCurrentAccess}
                currentAccess={currentAccess}
              />
            )}
            {/* end content */}
          </div>
        </Modal>
      )}
      {/* modal control */}
    </Fragment>
  );
};

// const mapStateToProps = ({ users }: StoreState): { users: UserInterface[] } => {
//   return { users: users };
// };

export default connect(null, {
  blockUserToSystem,
  unBlockUserToSystem,
  resetUserAccess,
})(UserList);
