import React, { Component } from 'react'
import { FaRegUserCircle } from 'react-icons/fa'
import { GrStatusInfo } from 'react-icons/gr'
import { AdminApplicationsList, ApplicationStatesDescription } from '../../../interfaces/application';
import noImage from '../../../assets/profile.png';

enum tabs {
  profile = "profile",
  position = "position",
}
interface ApplicationDetailsProps {
  selectedApplication: AdminApplicationsList
}

interface ApplicationDetailsState {
  loading: boolean;
  tab: tabs,
}

class ApplicationDetails extends Component<ApplicationDetailsProps, ApplicationDetailsState> {
  constructor(props: ApplicationDetailsProps) {
		super(props);

		this.state = {
      loading: false,
      tab: tabs.profile,
		};
	}
  tabStatusClass = (status: boolean) => {
    return status === true ?
    "col-span-6 bg-white hover:bg-blue-100 py-4 px-3 cursor-pointer text-lg border-b-4 border-blue-600 text-blue-600 font-bold flex flex-row items-center justify-start bg-blue-50 gap-3 animate__animated animate__pulse" :
    "col-span-6 bg-white hover:bg-blue-50 py-4 px-3 cursor-pointer text-lg border-b-4 border-white hover:border-blue-200 text-gray-600 hover:text-blue-700 flex flex-row items-center justify-start gap-3"
  }
  render() {
    return (
      <div className="px-2 py-2 border-t">
        {/* Tabs */}
        <div className="rounded grid md:grid-cols-12">
          <div onClick={() => this.setState({ tab: tabs.profile })} className={`${this.state.tab === tabs.profile ? this.tabStatusClass(true) : this.tabStatusClass(false)}`}>
            <FaRegUserCircle className="text-3xl" />
            Applicant profile
          </div>
          <div onClick={() => this.setState({ tab: tabs.position })} className={`${this.state.tab === tabs.position ? this.tabStatusClass(true) : this.tabStatusClass(false)}`}>
            <GrStatusInfo className="text-3xl" />
            Position requirements
          </div>
        </div>
        {/* Tab contents */}
        <div className="border rounded px-4 py-4 mt-3">
          {this.state.tab === tabs.profile ? 
          <div className="grid md:grid-cols-12">
            <div className="col-span-6">
              <div className="text-3xl text-blue-600 font-bold mb-6">General information</div>
              <div className="">
                <div className="px-2 py-2">
                  <img src={noImage} alt="" className="h-28 w-28 rounded-full mb-2" />
                </div>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">First name: </span>
                <span className="font-bold">{this.props.selectedApplication.fname}</span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Last name: </span>
                <span className="font-bold">{this.props.selectedApplication.lname}</span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">National ID: </span>
                <span className="font-bold">{this.props.selectedApplication.n_id}</span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Gender: </span>
                <span className="font-bold">{this.props.selectedApplication.sex}</span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Phone number: </span>
                <span className="font-bold">{this.props.selectedApplication.phone_number}</span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Email: </span>
                <span className="font-bold">{this.props.selectedApplication.email}</span>
              </div>
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">District: </span>
                <span className="font-bold">{this.props.selectedApplication.district}</span>
              </div>
            </div>
          </div> : 
          <div>
              <div className="text-2xl text-blue-600 font-bold mb-3">Application details</div>
              <div className="border px-3 py-3 rounded mb-6">
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Application period: </span>
                  <span className="font-bold">{this.props.selectedApplication.application_session.application_session_name} - {this.props.selectedApplication.application_session.period}</span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Application date: </span>
                  <span className="font-bold">{this.props.selectedApplication.application_session.createdAt}</span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Application status: </span>
                  <span className="font-bold">{ApplicationStatesDescription.find(itm => itm.key === this.props.selectedApplication.application_status)?.caption}</span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Application history: </span>
                  <span className="font-bold">
                    <ol className="text-gray-500 text-sm">
                      {this.props.selectedApplication.application_history.map((item, i) => (
                        <li key={i + 1}className="">{i + 1}) {item.date} ({item.comment})</li>
                      ))}
                    </ol>
                  </span>
                </div>
              </div>
              <div className="text-2xl text-blue-600 font-bold mb-3">Position details</div>
              <div className="border px-3 py-3 rounded">
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Position name: </span>
                  <span className="font-bold">{this.props.selectedApplication.position.position_title}</span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Position description: </span>
                  <span className="font-bold">{this.props.selectedApplication.position.description}</span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500 italic">Position exams: </span>
                  <span className="font-bold">
                    <ol>
                      {this.props.selectedApplication.position.courses.map((item, i) => (
                        <li key={i + 1}>({i + 1}) Course name: {item.course_name} - {item.course_code}</li>
                      ))}
                    </ol>
                  </span>
                </div>
              </div>
          </div>}
        </div>
      </div>
    )
  }
}

export default ApplicationDetails
