import { ActionTypes } from "../actions";
import { AccessRoleActionType } from "../actions/access";
import { SystemUserRoleInterface } from "../shared/interfaces";

export interface AccessType {
  accessRoles: SystemUserRoleInterface[];
}

// default state
const initialState = {
  accessRoles: [],
};

export const accessReducer = (
  state: AccessType = initialState,
  action: AccessRoleActionType
) => {
  switch (action.type) {
    case ActionTypes.FETCH_ACCESS_ROLES:
      return {
        ...state,
        accessRoles: action.payload,
      };
    case ActionTypes.CREATE_ACCESS_ROLE:
      return {
        ...state,
        accessRoles: [...state.accessRoles, action.payload],
      };

    case ActionTypes.UPDATE_ACCESS_ROLE:
      let data: SystemUserRoleInterface[] = [...state.accessRoles];
      let index = state.accessRoles.findIndex(
        (role) =>
          role.system_user_role_id === action.payload.system_user_role_id
      );
      if (index >= 0) {
        data[index] = action.payload;
      }
      return {
        ...state,
        accessRoles: data,
      };
    default:
      return state;
  }
};
