import React, { useState } from 'react'
import { ImCheckboxChecked, ImCheckboxUnchecked } from 'react-icons/im';
import { LanguageInterface, QuestionAnswerInterface, QuestionItemInterface, questionTypeElementsType } from '../../../../../interfaces/question';

interface QuestionAnswerProps {
  selectedQuestionType: questionTypeElementsType | null;
  selectedQuestionItem: QuestionItemInterface;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  addAnswerSingleMultiple: (question: QuestionItemInterface, addedAnswer: QuestionAnswerInterface) => void;
}

const QuestionAnswer = (props: QuestionAnswerProps) => {
  const [addedAnswer, setAddedAnswer] = useState<QuestionAnswerInterface | null>(null);
  const showAddAnswer = () => {
    setAddedAnswer({ value: "", status: false });
  }
  const AddAnswer = () => {
    if (addedAnswer !== null) {
      if (addedAnswer.value === "") {
        alert("Please fill question answer!");
      } else {
        props.addAnswerSingleMultiple(props.selectedQuestionItem, addedAnswer);
        setAddedAnswer(null);
      }
    }
  }
  return (
    <div className="flex flex-col mb-3 text-sm gap-2 bg-gray-50 px-2 py-2 rounded">
      {addedAnswer !== null && 
        <label className="text-gray-600 font-bold">Add question Answer</label>
      }
      
      {addedAnswer === null ?
      <div className="flex flex-row items-center gap-3 animate__animated animate__fadeIn animate__fast">
        <div onClick={() => showAddAnswer()} className="btn bg-blue-50 text-blue-600 cursor-pointer hover:bg-blue-200 hover:text-blue-800 border border-blue-200">Add answer</div>
        <label className="text-gray-600 font-bold">Question Answer</label>
      </div>
      :
      <div className="flex flex-row items-center justify-between mb-2 gap-3 px-3 bg-gray-100 border py-3 rounded animate__animated animate__bounceIn animate__fast">
        <input type="text" className="input-white border-2 border-blue-600" />
        <div 
        onClick={() => setAddedAnswer({
          value: addedAnswer.value,
          status: !addedAnswer.status
        })}
        className={`flex flex-row items-center gap-2 cursor-pointer ${addedAnswer.status === true ? "text-blue-600" : "text-gray-600"} hover:text-blue-600`}>
          {addedAnswer.status === false ?
          <ImCheckboxUnchecked className="text-2xl cursor-pointer" /> :
          <ImCheckboxChecked className="text-2xl cursor-pointer" />}
          <span className="font-bold text-xl">{addedAnswer.status === true ? "True" : "False"}</span>
        </div>
        <div onClick={() => AddAnswer()} className="btn bg-blue-600 text-white hover:bg-blue-800 cursor-pointer w-24 text-center">Add</div>
        <div onClick={() => setAddedAnswer(null)} className="btn bg-gray-500 text-white hover:bg-gray-700 cursor-pointer w-24 text-center">Cancel</div>
      </div>}
      <div className="rounded bg-white px-2 py-2">
        {props.selectedQuestionType === questionTypeElementsType.SingleMultiple && 
        props.selectedQuestionItem.answer.map((item, i) => (
          <div className="flex flex-row items-center justify-between mb-2 gap-3">
            <input
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
                if (check === undefined) {
                  check = props.wholeQuestion[0];
                }
                props.updateAnswerSingleMultiple(
                  check, item.value, {
                    value: e.target.value,
                    status: item.status
                  }
                )
              }}
              key={i + 1}
              value={`${item.value}`}
              type="text" 
              className="input-white border-2 border-gray-500" />
              <div className="btn bg-red-50 text-red-600 hover:bg-red-200 hover:text-red-800 cursor-pointer">Remove</div>
          </div>
        ))
        }
      </div>
    </div>
  )
}

export default QuestionAnswer
