import React from 'react'
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from 'react-icons/md';
import { question_answer } from '../../../interfaces/index';
import { newAnswer } from '../DoExam';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  examDataInterface
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { exam_question } from '../../../interfaces/exam_question';
import UK_ICON from "../../../assets/uk-flag.jpg";
import { VscTriangleDown } from 'react-icons/vsc';

interface QuestionDetailsProps {
  // currentPosition: Interfaces.PositionAPI | null;
  // setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  // setApplicationStep: (step: ApplicationStepsTypes) => void;
  selectedQuestion: exam_question | null;
  setSelectedQuestion: (question: exam_question) => void;
  answerQuestionFn: (question_id: string, answer: question_answer) => void;
  questionAnswers: newAnswer[] | undefined;
  auth: Auth;
  doExam: examDataInterface;
}

const _QuestionDetails = (props: QuestionDetailsProps) => {
  return (
    <div className="bg-gray-800 h-full">
      <div className="bg-gray-200 px-3 py-2 rounded my-2 mr-2 h-full overflow-y-auto pb-80 md:pb-60">
          <div className="flex flex-row gap-2 mt-2">
            <div className="h-8 w-8 bg-gray-500 flex justify-center items-center text-white rounded-full px-3 py-2">{props.selectedQuestion !== null && props.selectedQuestion.question_id}</div>
              <div className="flex flex-row justify-between w-full">
                <div className="text-md flex flex-col">
                  <span className="font-bold">{props.selectedQuestion !== null && props.selectedQuestion.question_description}</span>
                  <span className="italic text-xs">Choose the correct answer
                  </span>
                </div>
                <div className="flex flex-col bg-white px-3 py-1 justify-center items-center rounded hover:bg-blue-100 border border-white hover:border-blue-500 cursor-pointer">
                  <div className="flex flex-row gap-2 items-center">
                    <span className="font-bold text-blue-600">ENG </span>
                    <span><img src={UK_ICON} alt="" className="h-4" /></span>
                    <VscTriangleDown />
                  </div>
                </div>
              </div>
          </div>
          <div className="my-4 mx-3 py-2 px-2 bg-white rounded">
            {props.selectedQuestion !== null &&
            props.selectedQuestion.question_answers.map((item) => (
              <div onClick={() => props.answerQuestionFn(props.selectedQuestion!.question_id, item)} className={`px-2 py-2 border-b ${props.questionAnswers !== undefined && props.questionAnswers.length > 0 ? props.questionAnswers.find(itm => itm.question_id === props.selectedQuestion?.question_id)?.user_answers?.answer === item.answer ? "bg-blue-600 border-blue-500 text-blue-100 hover:bg-blue-800" : "bg-white border-gray-100 hover:bg-gray-300" : ""} flex flex-row justify-left items-center rounded cursor-pointer`}>
                {props.questionAnswers !== undefined && props.questionAnswers.length > 0 ? props.questionAnswers.find(itm => itm.question_id === props.selectedQuestion?.question_id)?.user_answers?.answer === item.answer ? <MdRadioButtonChecked className="text-2xl" /> : <MdRadioButtonUnchecked className="text-2xl" /> : <MdRadioButtonUnchecked className="text-2xl" />}
                <span className="ml-2">{item.answer}</span>
              </div>
            ))}
          </div>
      </div>
    </div>
  )
}

const mapStateToProps = ({
  todos,
  auth,
  doExam,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface } => {
  return { todos: todos, auth: auth, doExam: doExam };
};

const QuestionDetails = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
})(_QuestionDetails);

export default QuestionDetails;