import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { ApplicationAssessmentInterface } from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { ApplicationStatus as ApplicationStatusInterface } from "../interfaces/application";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface GetApplicationToAssessAction {
  type: ActionTypes.GET_APPLICATIONS_TO_ASSESS;
  payload: ApplicationAssessmentInterface[];
}

export interface ReAssessApplicationAction {
  type: ActionTypes.RE_ASSESS_APPLICATION;
  payload: {
    application_id: string;
    application_status: string;
  };
}

export type ApplicationAssessmentActionType =
  | GetApplicationToAssessAction
  | ReAssessApplicationAction;
/**
 * * ****************************** ACTIONS *****************************
 */

export const getAllApplicationToAssess = (
  applicationStatus: ApplicationStatusInterface,
  positionId: string,
  periodId: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      setAxiosToken();
      const res = await axios.get<ApplicationAssessmentInterface[]>(
        `${API.APPLICATION}/application/${applicationStatus}/${positionId}/${periodId}`
      );
      console.log(res);
      await dispatch<GetApplicationToAssessAction>({
        type: ActionTypes.GET_APPLICATIONS_TO_ASSESS,
        payload: res.data,
      });
    } catch (error) {
      console.log({ applicationAssess_error: error });
    }
    callback(false);
  };
};

export const reAssessApplication = (data: {
  application_id: string;
  application_status: string;
}) => {
  console.log({ ApplicationAssessmentInterface: data });
  return async (dispatch: Dispatch) => {
    dispatch<ReAssessApplicationAction>({
      type: ActionTypes.RE_ASSESS_APPLICATION,
      payload: data,
    });
  };
};
