export const isEmptyOrSpaces = (str: string): boolean => {
  return str === null || str.match(/^ *$/) !== null;
};

export const getUserTitle = (
  accessRoles: SystemUserRoleInterface[],
  roleId: string
): string => {
  const result = accessRoles.find((e) => e.system_user_role_id === roleId);
  return result ? result.role : "";
};

export const getRoleById = (
  roles: SystemUserRoleInterface[],
  roleId: string
): SystemUserRoleInterface => {
  return roles.filter((role) => role.system_user_role_id === roleId)[0];
};

export const getPositionById = (
  positions: PositionInterface[],
  position_id: string
): PositionInterface => {
  return positions.filter(
    (position) => position.position_id === position_id
  )[0];
};

export const getSchoolById = (
  schools: SchoolInfoInterface[],
  school_id: string
): SchoolInfoInterface => {
  return schools.filter((school) => school.school_id === school_id)[0];
};

export const getPostById = (
  posts: PostSchoolInterface[],
  post_id: string
): PostSchoolInterface => {
  return posts.filter((post) => post.post_at_school_id === post_id)[0];
};

export const getPostsBySchoolId = (
  posts: PostSchoolInterface[],
  school_id: string
): PostSchoolInterface[] => {
  return posts.filter((post) => post.school_id === school_id);
};

export const getPostsByIds = (
  posts: PostSchoolInterface[],
  school_id: string,
  position_id: string
): PostSchoolInterface[] => {
  return posts.filter(
    (post) => post.school_id === school_id && post.position_id === position_id
  );
};

export const displayDate = (dateToDisplay: Date): string => {
  return new Intl.DateTimeFormat("en-GB", {
    year: "numeric",
    month: "long",
    day: "2-digit",
  }).format(dateToDisplay);
};

