import axios from "axios";
import React, { useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentSubCategory,
  updateDocumentSubCategory,
} from "../../actions/document";
import { MdClose, MdErrorOutline } from "react-icons/md";

interface AppProps {
  thisCategory: DocumentCategoryInterface;
  setSubCategoryId: React.Dispatch<React.SetStateAction<string>>;
  subCategoryId: string;
  setSubCategoryTitle: React.Dispatch<React.SetStateAction<string>>;
  subCategoryTitle: string;
  setSubCategoryDescription: React.Dispatch<React.SetStateAction<string>>;
  subCategoryDescription: string;
  setActionType: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
    >
  >;
  actionType: string;
  setFeedBack: React.Dispatch<
    React.SetStateAction<
      | {
          type: "success" | "danger" | "warning" | "";
          message: string;
        }
      | undefined
    >
  >;
  feedBack:
    | {
        type: "success" | "danger" | "warning" | "";
        message: string;
      }
    | undefined;
  createDocumentSubCategory: Function;
  updateDocumentSubCategory: Function;
}
const CreateSubDocument: React.FC<AppProps> = ({
  thisCategory,
  setActionType,
  setSubCategoryId,
  subCategoryId,
  setSubCategoryTitle,
  subCategoryTitle,
  setSubCategoryDescription,
  subCategoryDescription,
  actionType,
  setFeedBack,
  feedBack,
  createDocumentSubCategory,
  updateDocumentSubCategory,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFeedBack({ type: "", message: "" });
    setLoadind(true);
    try {
      if (actionType === "CREATE_SUB") {
        const res = await axios.post<DocumentSubCategoryInterface>(
          `${API.AUTH}/detailsubcategory`,
          {
            category_id: thisCategory.user_detail_category_id,
            title: subCategoryTitle,
            description: subCategoryDescription,
          }
        );
        createDocumentSubCategory(res.data);
        setFeedBack({
          type: "success",
          message: `Sub Category has been created!`,
        });
        setLoadind(false);
        setSubCategoryTitle("");
        setSubCategoryDescription("");
      } else {
        const formData: DocumentSubCategoryInterface = {
          user_detail_subcategory_id: subCategoryId,
          category_id: thisCategory.user_detail_category_id,
          title: subCategoryTitle,
          description: subCategoryDescription,
        };
        const res = await axios.patch<DocumentSubCategoryInterface>(
          `${API.AUTH}/detailsubcategory`,
          formData
        );
        if (res.data) {
          updateDocumentSubCategory(formData);
          setFeedBack({
            type: "success",
            message: `Sub Category has been updated!`,
          });
          setSubCategoryId("");
          setSubCategoryTitle("");
          setSubCategoryDescription("");
          setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      setLoadind(false);
    }
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div className="divide-y divide-gray-200 px-5">
        <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
          <div className="flex flex-row items-center">
            <span className="mr-2">Category:</span>
            <span className="text-xl font-semibold">{thisCategory.title}</span>
          </div>
          <div className="flex flex-col">
            <label>Title</label>
            <input
              type="text"
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Sub Category Title"
              value={subCategoryTitle}
              onChange={(e) => setSubCategoryTitle(e.target.value)}
              required
              disabled={loading}
            />
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Sub Category Description"
              style={{ resize: "none" }}
              value={subCategoryDescription}
              onChange={(e) => setSubCategoryDescription(e.target.value)}
              rows={2}
              required
              disabled={loading}
            ></textarea>
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              setActionType("");
              setSubCategoryId("");
              setSubCategoryTitle("");
              setSubCategoryDescription("");
            }}
            className="btn bg-gray-400 text-white hover:bg-gray-600"
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className="btn bg-primary text-white hover:bg-blue-600"
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : actionType === "CREATE_SUB"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default connect(null, {
  createDocumentSubCategory,
  updateDocumentSubCategory,
})(CreateSubDocument);
