import { Dispatch } from "redux";
import { PopupAlertData } from "../components/Alert/Popup";
import { ActionTypes } from "./types";
/**
 * * ****************************** INTERFACES *****************************
 */

export interface System {
  navigation: boolean;
  page_has_sub_nav: boolean;
  PopupAlerts: PopupAlertData[] | null;
}

//* ********************** ACTION TYPE INTERCACES ********************** */

export interface NavToogleInterface {
  type: ActionTypes.NAVIGATION_TOOGLE;
}

export interface PageHasSubNavInterface {
  type: ActionTypes.PAGE_HAS_SUB_NAV;
  payload: boolean;
}

// Emmy:: Popup alert
export interface PushPopupAlertAction {
  type: ActionTypes.PUSH_POPUP_ALERT;
  payload: PopupAlertData
}

export interface RemovePopupAlertAction {
  type: ActionTypes.REMOVE_POPUP_ALERT;
}

/**
 * * ****************************** ACTIONS *****************************
 */

/**
 * @description Activate/desactivate navigation
 * @returns null
 */
export const NavToogle = () => {
  return (dispatch: Dispatch) => {
    dispatch<NavToogleInterface>({
      type: ActionTypes.NAVIGATION_TOOGLE,
    });
  };
};

/**
 * @description Activate/desactivate navigation
 * @returns null
 */
export const PageHasSubNav = (status: boolean) => {
  return (dispatch: Dispatch) => {
    dispatch<PageHasSubNavInterface>({
      type: ActionTypes.PAGE_HAS_SUB_NAV,
      payload: status,
    });
  };
};

/**
 * @description Popup alert
 * @returns null
 */

 export const PushPopupAlert = (alert: PopupAlertData) => {
  return (dispatch: Dispatch) => {
    dispatch<PushPopupAlertAction>({
      type: ActionTypes.PUSH_POPUP_ALERT,
      payload: alert,
    });
  };
};

export const RemovePopupAlert = () => {
  return async (dispatch: Dispatch) => {
    dispatch<RemovePopupAlertAction>({
      type: ActionTypes.REMOVE_POPUP_ALERT,
    });
  };
};