import { Action, ActionTypes } from "../actions";
import { questionTypesStoreInterface } from "../actions/questionTypes";

// default state
const defaultState: questionTypesStoreInterface = {
  questionTypes: null,
  selectedQuestionType: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const questionTypesReducer = (state: questionTypesStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.GET_QUESTION_TYPES:
      if (state.questionTypes !== null) {
        state.questionTypes.length = 0;
      }
      state.questionTypes = action.payload;
      return state;
    case ActionTypes.SET_SELECTED_QUESTION_TYPE:
      state.selectedQuestionType = action.payload;
      return state;
    default:
      return state;
  }
};