import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { PositionAPI } from "../../src/interfaces/index";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface loadAllPositionsActions {
  type: ActionTypes.LOAD_POSITIONS,
  payload: PositionAPI[]
}

export interface setSelectedPositionActions {
  type: ActionTypes.SELECTED_POSITION,
  payload: PositionAPI
}

export interface positionsStoreInterface {
  positions_list: PositionAPI[] | null;
  selected_position: PositionAPI | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadAllPositions = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<PositionAPI[]>(`${API.APPLICATION}/position`);
      dispatch<loadAllPositionsActions>({
        type: ActionTypes.LOAD_POSITIONS,
        payload: res.data,
      });
      console.log("Positions: ", res.data);
    } catch (error) {
      console.log("Err: ", error);
    }
    callback(false);
  };
};

export const setSelectedPosition = (position: PositionAPI): setSelectedPositionActions => {
  return {
    type: ActionTypes.SELECTED_POSITION,
    payload: position,
  };
}