import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { education_category, school_level } from "../../src/interfaces/index";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface loadEducationCategoryActions {
  type: ActionTypes.LOAD_EDUCATION_CATEGORIES,
  payload: education_category[]
}

export interface setSelectedEducationCategoryActions {
  type: ActionTypes.SET_SELECTED_EDUCATION_CATEGORY,
  payload: education_category
}

export interface loadSchoolLevelActions {
  type: ActionTypes.LOAD_SCHOOL_LEVELS,
  payload: school_level[]
}

export interface setSelectedSchoolLevelActions {
  type: ActionTypes.SET_SELECTED_SCHOOL_LEVELS,
  payload: school_level
}


export interface schoolLevelsStoreInterface {
  education_categories: education_category[] | null;
  school_levels: school_level[] | null;
  selectedEducationCategory: education_category | null;
  selectedSchoolLevel: school_level | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadEducationCategories = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<education_category[]>(`${API.APPLICATION}/educationinstitute`);
      dispatch<loadEducationCategoryActions>({
        type: ActionTypes.LOAD_EDUCATION_CATEGORIES,
        payload: res.data,
      });
    } catch (error) {
      console.log("Err: ", {...error});
    }
    callback(false);
  };
};

export const loadSchoolLevels = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<school_level[]>(`${API.APPLICATION}/schoollevel`);
      dispatch<loadSchoolLevelActions>({
        type: ActionTypes.LOAD_SCHOOL_LEVELS,
        payload: res.data,
      });
    } catch (error) {
      console.log("Err: ", {...error});
    }
    callback(false);
  };
};

export const setSelectedEducationCategory = (education_category: education_category): setSelectedEducationCategoryActions => {
  return {
    type: ActionTypes.SET_SELECTED_EDUCATION_CATEGORY,
    payload: education_category,
  };
}

export const setSelectedSchoolLevel = (education_category: school_level): setSelectedSchoolLevelActions => {
  return {
    type: ActionTypes.SET_SELECTED_SCHOOL_LEVELS,
    payload: education_category,
  };
}