import { ActionTypes } from "../actions";
import { PositionActionType } from "../actions/positionRequests";
import { PositionInterface } from "../shared/interfaces";

// default state
// const initialState: PositionInterface[] = [];

export const positionReducer = (
  state: PositionInterface[] = [],
  action: PositionActionType
) => {
  switch (action.type) {
    case ActionTypes.FETCH_POSITIONS:
      return action.payload;
    case ActionTypes.CREATE_POSITION:
      return [...state, action.payload];
    default:
      return state;
  }
};
