import { ActionTypes } from "../actions";
import { AccessUserActionType } from "../actions/users";
import { UserInterface } from "../shared/interfaces";

// default state

export const userReducer = (
  state: UserInterface[] = [],
  action: AccessUserActionType
) => {
  let index;
  switch (action.type) {
    case ActionTypes.FETCH_USERS_ACCESS:
      return action.payload;
    case ActionTypes.BLOCK_USER_TO_SYSTEM:
    case ActionTypes.UNBLOCK_USER_TO_SYSTEM:
      index = state.findIndex((user) => user.user_id === action.payload); //finding index of the item
      if (index >= 0) {
        state[index].is_active = !state[index].is_active;
      }
      return state;
    // case ActionTypes.UNBLOCK_USER_TO_SYSTEM:
    //   index = state.findIndex((user) => user.id === action.payload); //finding index of the item
    //   if (index >= 0) {
    //     state[index].user_status = 1;
    //   }
    // return state;
    case ActionTypes.ASSIGN_ROLE_TO_USER:
      let data: UserInterface[] = [...state];
      index = state.findIndex(
        (user) => user.user_id === action.payload.user_id
      ); //finding index of the item
      if (index >= 0) {
        data[index] = { ...data[index], default_access: action.payload.data };
        data[index] = { ...data[index], custome_access: null };
        // this.setState({ data });
        // state[index].default_access = action.payload.data;
        // state[index].custome_access = null;
      }
      return data;
    case ActionTypes.CUSTOMIZE_USER_ACCESS:
      index = state.findIndex(
        (user) => user.user_id === action.payload.user_id
      ); //finding index of the item
      if (index >= 0) {
        state[index].custome_access = action.payload;
      }
      return state;
    case ActionTypes.RESET_USER_ACCESS:
      const _index: number = state.findIndex(
        (user) => user.user_id === action.payload
      );
      if (_index >= 0) {
        state[_index].custome_access = null;
      }
      return state;
    // case ActionTypes.VERIFY_DOCUMENT:
    //   let result: UserInterface[] = [...state];
    //   const user_index: number = result.findIndex(
    //     (user) => user.user_id === action.payload.user_id
    //   );

    //   if (user_index >= 0) {
    //     index = result[user_index].details.findIndex(
    //       (doc) => doc.doc_id === action.payload.doc_id
    //     );

    //     if (index && index >= 0) {
    //       result[user_index].details[index].validation_status =
    //         action.payload.validation_status;
    //       // action.payload.validated_by.validation_status &&
    //       //   (result[user_index].details[index].validated_by.validation_status =
    //       //     action.payload.validated_by.validation_status);
    //       // result[user_index].details[index].validated_by.user_id =
    //       //   action.payload.validated_by.user_id;
    //       // result[user_index].details[index].validated_by.commet =
    //       //   action.payload.validated_by.comment;
    //       // result[user_index].details[index].validated_by.date = "";
    //     }
    //   }

    //   return state;
    default:
      return state;
  }
};
