import { Action, ActionTypes } from "../actions";
import { applicationStoreInterface } from "../actions/application";

// default state
const defaultState: applicationStoreInterface = {
  applications: []
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const applicationReducer = (state: applicationStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOAD_APPLICATIONS:
      state.applications.length = 0;
      state.applications = action.payload.applications
      return {
        ...state
      }
    case ActionTypes.CREATE_APPLICATION:
      state.user_id = action.payload.user_id;
      state.applications.push(action.payload.application);
      state.apply = undefined;
      return {
        ...state,
      }
    case ActionTypes.INITIATE_APPLICATION:
      state.apply = action.payload.initiate_application;
      return {
        ...state
      }
    case ActionTypes.SELECT_POSITION:
      if (state.apply !== undefined) {
        state.apply.selectedPosition = action.payload.selectedPosition;
      }
      return {
        ...state,
      }
    case ActionTypes.SET_APPLICATION_STEP:
      if (state.apply !== undefined) {
        state.apply.currentStep = action.payload.step;
      }
      return {
        ...state,
      }
    case ActionTypes.ADD_LOCATION:
      if (state.apply !== undefined) {
        if (action.payload !== null) {
          state.apply.selectedLocation = state.apply.selectedLocation.filter(itm => itm.courseId !== action.payload.courseId);
          state.apply.selectedLocation.push(action.payload);
        }
      }
      return {
        ...state,
      }
    case ActionTypes.REMOVE_LOCATION:
      if (state.apply !== undefined) {
        if (action.payload !== null) {
          state.apply.selectedLocation = state.apply.selectedLocation.filter(itm => itm.courseId !== action.payload.course_id);
        }
      }
      return {
        ...state,
      }
    default:
      return state;
  }
};
