export interface PositionSubcategoryData {
	category_id: string;
	subcategory_id: string;
	description: string;
	required: boolean;
	title: string;
  others: { key: string; value: string }[];
	_id: string;
  __v?: string;
}

export const validateDocCategory = (requirement: PositionRequirements, userDocuments: DocumentUser[]) => {
	if (userDocuments.find(item => item.category_id === requirement.category.user_detail_category_id)?.category_id === undefined) {
		return requirement.category.user_detail_category_id;
	} else {
		return null;
	}
}

