export const validateDocSubcategory = (requirement: PositionRequirements, userDocuments: DocumentUser[]) => {
	let result: {
		category_subcat_id: string | null;
		category_id: string | null;
		user_detail_subcategory_id: string | null;
	}[] = [];
	if (requirement.position_requirement.length > 0) {
		for (let i in requirement.position_requirement) {
			let filterData = userDocuments.find(item => item.category_subcategory_data && item.category_subcategory_data.category_subcategory_Data_id === requirement.position_requirement[i]._id)?.category_subcategory_data;
			if ((filterData === undefined || filterData === null) && requirement.position_requirement[i].required === true) {
				result.push({
					category_subcat_id: requirement.position_requirement[i]._id,
					category_id: requirement.category.user_detail_category_id,
					user_detail_subcategory_id: requirement.subcategory.user_detail_subcategory_id
				});
			}
		}
	}
	console.log("Returned: ", result)
	return result;
}

export const returnValidatePosition = (requirements: PositionRequirements[], documents: DocumentUser[]) => {
	let status = true;
	for (let i in requirements) {
		if (requirements[i].position_requirement.length === 0) {
			if (requirements[i] !== null && documents !== null && validateDocCategory(requirements[i], documents) !== null && validateDocCategory(requirements[i], documents) !== undefined) {
				status = false;
			}
		} else {
			if (validateDocSubcategory(requirements[i], documents).length !== 0) {
				status = false;
			}
		}
	}
	return status;
}