import React, { useState } from 'react'
import { newAnswer } from '../DoExam'
import { psychometric_questions_list } from '../QuestionsList/QuestionsList';
import { Link, Redirect } from 'react-router-dom';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { findResult } from '../utils/utils';

interface DoExamResultsInterface { 
  // currentPosition: Interfaces.PositionAPI | null;
  // setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  // setApplicationStep: (step: ApplicationStepsTypes) => void;
  // windowStatus: {
  //   profile: boolean,
  //   questionsList: boolean,
  // };
  // setProfileWindowStatus: (status: boolean) => void;
  // setQuestionsListWindowStatus: (status: boolean) => void;
  testAnswers: number;
  answeredQuestions: newAnswer[] | [] | undefined;
  // setSelectedQuestion: (question: exam_question) => void;
  doExam: examDataInterface;
  auth: Auth;
  SubmitExamAnswers: (user_id: string) => void;
  page: string;
}

const _DoExamResults = (props: DoExamResultsInterface) => {
  const [redirect, setRedirect] = useState(false);
  const findExamResult = () => {
    let marks: number = 0;
    if (props.answeredQuestions !== undefined) {
      if (props.doExam.exam_answers) {
        return findResult(props.doExam.exam_answers.exam_questions, props.answeredQuestions);
      }
    }
    return marks; 
  }

  if (redirect === true) {
    return <Redirect to="/dashboard" />
  }
  return (
    <div className="">
      {props.answeredQuestions !== undefined &&
      <div className="mt-5 bg-white rounded shadow px-3 py-3">
      <div className="text-4xl font-bold text-blue-600">Exam results</div>
      <div className="text-sm text-gray-500"></div>
        <div className="my-2">
          <div className="font-bold text-lg text-gray-700 my-2 mt-4">Answers history</div>
          <div className="flex flex-col gap-3">
            {psychometric_questions_list.map((item, i) => (
              <div key={i + 1} className="bg-gray-200 px-2 py-2 rounded">
                <div className="grid md:grid-cols-12 w-full">
                  <div className="col-span-10 flex flex-row items-center gap-3">
                    <div key={i + 1} className="w-6 h-6 p-2 rounded-full bg-gray-600 flex justify-center items-center font-bold text-lg text-white cursor-pointer">{item.question_id}</div>
                    <div className="text-sm">
                      <div>{item.question_description}</div>
                      <div className="text-blue-700 font-bold text-sm">Answer:&nbsp;
                        {props.answeredQuestions !== undefined && props.answeredQuestions.find(itm => itm.question_id === item.question_id)?.user_answers?.answer}
                      </div>
                    </div>
                  </div>
                  {props.answeredQuestions !== undefined && props.answeredQuestions.find(itm => itm.question_id === item.question_id)?.user_answers?.status === true ? 
                  <div className="col-span-2 bg-blue-100 rounded px-2 py-1 text-center float-right cursor-pointer font-bold text-md text-blue-600 border border-blue-400 flex items-center justify-center h-max md:ml-3">
                    Right
                  </div> : 
                  <div className="col-span-2 bg-yellow-100 rounded px-2 py-1 text-center float-right cursor-pointer font-bold text-md text-yellow-600 border border-yellow-400 flex items-center justify-center h-max md:ml-3">
                    Wrong
                  </div>}
                </div>
              </div>
            ))}

            <div className={`border-2 ${findExamResult() >= 50 ? "border-blue-400 bg-blue-100 text-blue-700" : "border-red-400 bg-red-100 text-gray-700"} px-3 py-3 rounded flex flex-row justify-between items-center`}>
              <div className="">
                <div className="text-left text-xl">Total marks</div>
                <div className="text-5xl">{findExamResult()}%</div>
              </div>
              {
              props.page !== "doExam" ? "" : findExamResult() >= 50 ?
              <button onClick={() => { props.SubmitExamAnswers(props.auth.user.n_id); setRedirect(true)}} className="btn btn-blue">Continue</button>
              :
              <Link className="btn border border-gray-400 bg-white" to="/dashboard">Back to home</Link>}
            </div>
          </div>
        </div>
      </div>}
    </div>
  )
}


const mapStateToProps = ({
  todos,
  auth,
  doExam,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface } => {
  return { todos: todos, auth: auth, doExam: doExam };
};

const DoExamResults = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers
})(_DoExamResults);

export default DoExamResults;