import React, { Fragment, useEffect, useState } from "react";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { PopupAlertData } from "../../components/Alert/Popup";
import axios from "axios";
import { API } from "../../utils/api";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import {
  createAplicationSession,
  disableApplicationSession,
  disableSystemSetting,
  getApplicationSessions,
  updateAplicationSession,
} from "../../actions/settings";
import { ApplicationSessionInterface } from "../../shared/interfaces";
import { errorToText } from "../../utils/errors";
import { DATE } from "../../utils/functions";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import CreateApplicationSession from "./CreateApplicationSession";
import Table from "../../components/Table/Table";
import { MdEdit } from "react-icons/md";
import { VscLoading } from "react-icons/vsc";

interface AppPropsInterface {
  application_sessions: ApplicationSessionInterface[];
  PushPopupAlert: (alert: PopupAlertData) => void;
  getApplicationSessions: Function;
  disableApplicationSession: Function;
  createAplicationSession: Function;
  updateAplicationSession: Function;
}

const _ApplicationSession = (props: AppPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [loadingRequest, setLoadingRequest] = useState<string>("");
  const [addNew, setAddNew] = useState<boolean>(false);
  const [selectedPeriod, setSelectedPeriod] =
    useState<ApplicationSessionInterface | null>(null);

  const activateSession = async (
    application_id: string,
    status: boolean,
    name: string
  ) => {
    // if (!status) {
    //   return props.PushPopupAlert({
    //     title: `Not allowed to stop application session`,
    //     theme: PopupAlertTheme.warning,
    //   });
    // }
    setLoadingRequest(application_id);
    try {
      const res = await axios.put(`${API.APPLICATION}/applicationsesion`, {
        application_session_id: application_id,
        is_active: status,
      });
      // props.disableApplicationSession({ application_id, status });
      props.updateAplicationSession(res.data.data);
      props.PushPopupAlert({
        title: `${name} ${status ? "enabled" : "disabled"} successfully`,
        theme: PopupAlertTheme.success,
      });
      console.log("Disable: ", res.data.data);
    } catch (error) {
      console.error("err: ", error);
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadingRequest("");
  };

  const tableData = (): JSX.Element[] => {
    let data: ApplicationSessionInterface[] = props.application_sessions;
    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={9}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((item, index) => (
      <tr key={index} className="border-b border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>{item.application_session_name}</span>
        </td>
        <td className="p-3 text-left">{item.period}</td>
        {/* <td className="p-3 text-left">{user.phone_number}</td> */}
        <td className="p-3 text-left">{DATE(item.application_start_date)}</td>
        <td className="p-3 text-left">{DATE(item.application_end_date)}</td>
        <td>
          {item.is_active ? (
            <span className={`px-2 py-1 font-semibold text-green-700`}>
              Active
            </span>
          ) : (
            <span className={`px-2 py-1 font-semibold text-red-700`}>
              Inactive
            </span>
          )}
        </td>
        <td>
          <div className="flex items-center gap-1">
            <div
              className={`relative ${
                loadingRequest.length > 0
                  ? "cursor-not-allowed"
                  : "cursor-pointer"
              }`}
              onClick={() =>
                !loadingRequest.length &&
                activateSession(
                  item.application_session_id,
                  !item.is_active,
                  item.application_session_name
                )
              }
            >
              <span className="block w-14 h-8 bg-gray-100 rounded-full shadow-inner"></span>
              <span
                className={`absolute block w-6 h-6 mt-1 ml-1 rounded-full shadow inset-y-0 left-0 focus-within:shadow-outline transition-transform duration-500 ease-in-out ${
                  !item.is_active
                    ? "bg-red-500"
                    : "bg-green-500 transform translate-x-full"
                }`}
              ></span>
            </div>
            {loadingRequest === item.application_session_id && (
              <VscLoading className="animate__animated animate__rotateIn animate__infinite" />
            )}
          </div>
        </td>
        <td className="px-5 text-center">
          <div className="flex flex-row items-center gap-2">
            <div
              onClick={() => {
                !loadingRequest.length && setSelectedPeriod(item);
                !loadingRequest.length && setAddNew(true);
              }}
              className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 ${
                loadingRequest.length > 0
                  ? "cursor-not-allowed"
                  : "cursor-pointer"
              }`}
            >
              <MdEdit className="text-3xl text-blue-700" />
            </div>
            {/* <div
              // onClick={() =>
              //   setSelectedQuestionType({
              //     action: QT_QuestionTypeAction.Delete,
              //     selectedQuestionType: item,
              //   })
              // }
              className="bg-gray-100 hover:bg-red-100 rounded-full px-1 py-1 cursor-pointer"
            >
              <IoMdTrash className="text-3xl text-yellow-700" />
            </div> */}
          </div>
        </td>
      </tr>
    ));
  };

  useEffect(() => {
    if (
      props.application_sessions === null ||
      props.application_sessions.length === 0
    ) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        setLoading(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }
  }, [props, setLoading]);

  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">
            Application Session settings
          </div>
          {loading === false && (
            <div
              onClick={() => setAddNew(true)}
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
            >
              Add new
            </div>
          )}
        </div>
        <div className="flex flex-col">
          {props.application_sessions === null || loading === true ? (
            <Loading />
          ) : props.application_sessions.length === 0 ? (
            <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
              <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
              <span>No settings available</span>
            </div>
          ) : (
            <div className="my-1">
              <Table
                columns={[
                  { columnTitle: "#", alignText: "left", padding: "p-3" },
                  { columnTitle: "Session", alignText: "left", padding: "p-3" },
                  { columnTitle: "Perriod", alignText: "left", padding: "p-3" },
                  {
                    columnTitle: "Start Date",
                    alignText: "left",
                    padding: "p-3",
                  },
                  {
                    columnTitle: "End Date",
                    alignText: "left",
                    padding: "p-3",
                  },
                  { columnTitle: "Status", alignText: "left", padding: "p-3" },
                  { columnTitle: "", alignText: "left", padding: "p-3" },
                  { columnTitle: "", alignText: "left", padding: "p-3" },
                ]}
                rows={tableData}
                pagination={false}
                search={false}
                thColor="gray"
              />
            </div>
          )}
        </div>
      </div>

      {addNew === true && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <div className="flex flex-col">
                <span className="font-extrabold text-2xl text-gray-500">
                  {selectedPeriod !== null
                    ? selectedPeriod.application_session_name
                    : "Add new Application Session"}
                </span>
                <span className="text-sm text-gray-500 font-normal">
                  {selectedPeriod !== null
                    ? "Update the selected application session"
                    : "Application Session is a very sensitive data, make sure you give valid data"}
                </span>
              </div>
            </div>
          }
          backDrop={false}
          theme={Themes.default}
          close={() => {
            setAddNew(false);
            setSelectedPeriod(null);
          }}
          widthSizeClass={ModalSize.medium}
          displayClose={true}
          backDropClose={true}
          padding={{ title: true, body: true }}
        >
          <CreateApplicationSession
            closeModal={setAddNew}
            selectedPeriod={selectedPeriod}
            createAplicationSession={props.createAplicationSession}
            updateAplicationSession={props.updateAplicationSession}
            PushPopupAlert={props.PushPopupAlert}
            setSelectedPeriod={setSelectedPeriod}
          />
        </Modal>
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
}: StoreState): {
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
} => {
  return {
    auth: auth,
    application_sessions: system_settings.applicationSessions,
  };
};

const ApplicationSession = connect(mapStateToProps, {
  getApplicationSessions,
  disableApplicationSession,
  createAplicationSession,
  updateAplicationSession,
  PushPopupAlert,
})(_ApplicationSession);

export default ApplicationSession;
