import React from "react";
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  FC_RegisterAccount,
  FC_RegisterCheckCode,
  FC_CleanUserDetails,
} from "../../actions";
import { StoreState } from "../../reducers";
import {
  validateEmail,
  hasUppercase,
  hasLowercase,
  hasNumber,
  hasSpecialcase,
} from "../../utils/validation";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import ConfirmationCode from "../../components/ConfirmationCode/ConfirmationCode";
import { Link } from "react-router-dom";
import { MdCheckCircle, MdPanoramaFishEye } from "react-icons/md";

// props for the component
interface AppProps {
  auth: Auth;
  FC_RegisterAccount: Function;
  FC_RegisterCheckCode: Function;
  FC_CleanUserDetails: Function;
}

interface AppState {
  n_id: string;
  phone_number: string;
  email: string;
  password: string;
  re_password: string;
  loading: boolean;
  error: {
    target: string;
    msg: string;
  };
  register_success: boolean;
  code_loading: boolean;
  password_validation: {
    uppercase: boolean;
    lowercase: boolean;
    number: boolean;
    length: boolean;
    special: boolean;
  };
}

class _RegisterAccount extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      n_id: "",
      phone_number: "",
      email: "",
      password: "",
      re_password: "",
      error: { target: "", msg: "" },
      register_success: false,
      code_loading: false,
      password_validation: {
        uppercase: false,
        lowercase: false,
        number: false,
        length: false,
        special: false,
      },
    };
  }

  registerHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // check for errors
    if (this.state.n_id.length <= 4) {
      return this.setState({
        error: {
          target: "n_id",
          msg: "NID is required",
        },
      });
    }

    // check for errors
    if (this.state.phone_number.length <= 4) {
      return this.setState({
        error: {
          target: "phone_number",
          msg: "Phone number is required",
        },
      });
    }

    if (this.state.email.length <= 4 || !validateEmail(this.state.email)) {
      return this.setState({
        error: {
          target: "email",
          msg: "Please enter a email",
        },
      });
    }

    if (
      !(
        this.state.password_validation.uppercase &&
        this.state.password_validation.lowercase &&
        this.state.password_validation.number &&
        this.state.password_validation.special &&
        this.state.password_validation.length
      )
    ) {
      return this.setState({
        error: {
          target: "password",
          msg: "Password must pass the validation test",
        },
      });
    }

    // check for errors
    if (this.state.re_password !== this.state.password) {
      return this.setState({
        error: {
          target: "re_password",
          msg: "Passwords does not match",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: "",
        msg: "",
      },
    });
    // send data
    this.props.FC_RegisterAccount(
      {
        n_id: this.state.n_id,
        email: this.state.email,
        phone_number: this.state.phone_number,
        password: this.state.password,
      },
      (status: boolean, error: string) => {
        if (status === false) {
          this.setState({
            error: { target: "all", msg: error },
            loading: false,
          });
        } else {
          this.setState({
            loading: false,
          });
        }
      }
    );
  };

  handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    if (e.currentTarget.name === "password") {
      this.setState({
        ...this.state,
        error: {
          target: "",
          msg: "",
        },
        [e.currentTarget.name]: e.target.value,
        password_validation: {
          uppercase: hasUppercase(e.target.value),
          lowercase: hasLowercase(e.target.value),
          special: hasSpecialcase(e.target.value),
          number: hasNumber(e.target.value),
          length: e.target.value.length >= 8 && e.target.value.length < 64,
        },
      } as Pick<AppState, keyof AppState>);
    } else {
      this.setState({
        ...this.state,
        error: {
          target: "",
          msg: "",
        },
        [e.currentTarget.name]: e.target.value,
      } as Pick<AppState, keyof AppState>);
    }
  };

  submitCodeHandler = (code: number) => {
    this.setState({
      code_loading: true,
      error: {
        target: "",
        msg: "",
      },
    });
    this.props.FC_RegisterCheckCode(
      { n_id: this.props.auth.user.n_id, activation_code: code },
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            register_success: true,
            loading: false,
            code_loading: false,
          });
        } else {
          this.setState({
            error: {
              target: "all",
              msg: msg,
            },
            loading: false,
            code_loading: false,
          });
        }
      }
    );
  };

  render() {
    let ValidationDesign = !(
      this.state.password_validation.uppercase &&
      this.state.password_validation.lowercase &&
      this.state.password_validation.number &&
      this.state.password_validation.special &&
      this.state.password_validation.length
    ) ? (
      <div className="px-2 mb-2 bg-white rounded shadow-lg mx-2 p-2 -mt-2">
        <div className="p-1 border-gray-200 border rounded-full flex">
          {this.state.password_validation.lowercase && (
            <div className="bg-blue-500 pt-1 rounded-l-full inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.uppercase && (
            <div className="bg-blue-500 pt-1  inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.number && (
            <div className="bg-blue-500 pt-1  inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.length && (
            <div className="bg-blue-500 pt-1  inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.special && (
            <div className="bg-blue-500 pt-1 rounded-r-full inline-block- w-2/6"></div>
          )}
        </div>

        <h5>Password must:</h5>
        <div className="pt-1 text-sm">
          <div
            className={`${
              this.state.password_validation.lowercase
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.lowercase ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Include a lowercase character</span>
          </div>

          <div
            className={`${
              this.state.password_validation.uppercase
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.uppercase ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Include an uppercase character</span>
          </div>

          <div
            className={`${
              this.state.password_validation.number
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.number ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Include a Number</span>
          </div>

          <div
            className={`${
              this.state.password_validation.length
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.length ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Between 8 and 64 character</span>
          </div>

          <div
            className={`${
              this.state.password_validation.special
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.special ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>A special character</span>
          </div>
        </div>
      </div>
    ) : null;
    // check if successfully is done
    if (this.state.register_success === true) {
      return (
        <div className="w-full h-screen">
          <div className="animate__animated animate__zoomIn max-w-4xl mx-auto bg-opacity-80 bg-green-100 p-6 rounded mt-2 text-center">
            <MdCheckCircle className="text-9xl text-center mx-auto text-green-700" />
            <h1 className="text-3xl text-green-800 font-bold">
              Account created
            </h1>
            <p className="text-green-800 mb-4">
              Your account has been created successfully.
            </p>

            <Link
              className="bg-green-600 text-white py-2 px-5 mt-3 rounded hover:bg-green-500"
              to="/login"
            >
              Login
            </Link>
          </div>
        </div>
      );
    }

    return (
      <div>
        <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-24  h-screen flex flex-wrap ">
          <div className="mx-auto">
            <div className="col-span-2- mx-auto">
              {this.props.auth.user?.n_id?.length >= 3 ? (
                <div className="">
                  {this.state.error.target === "all" && (
                    <div className="mx-auto w-full">
                      <Alert
                        title={this.state.error.msg}
                        theme={AlertInterface.danger}
                      />
                    </div>
                  )}
                  <ConfirmationCode
                    user={this.props.auth.user}
                    submitCode={this.submitCodeHandler}
                    code_loading={this.state.code_loading}
                  />
                </div>
              ) : (
                <div className="animate__animated animate__fadeIn bg-white border-gray-200 border shadow-xl mx-auto px-4 py-5 rounded max-w-xl mt-6 sm:mx-auto">
                  <form onSubmit={this.registerHandler}>
                    <h1 className="px-2 font-extrabold text-4xl text-blue-500  pt-2 pb-4">
                      Register
                    </h1>

                    {this.state.error.target === "all" && (
                      <div className="p-2">
                        <Alert
                          title={this.state.error.msg}
                          theme={AlertInterface.danger}
                        />
                      </div>
                    )}

                    <div className="p-2">
                      <label htmlFor="n_id" className="d-block">
                        National Id
                      </label>
                      <input
                        type="text"
                        name="n_id"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "n_id"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Your National Id"
                        value={this.state.n_id}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "n_id" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <label htmlFor="n_id" className="d-block">
                        Phone Number
                      </label>
                      <input
                        type="text"
                        name="phone_number"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "phone_number"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="0700000000"
                        value={this.state.phone_number}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "phone_number" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <label htmlFor="email" className="d-block">
                        Email
                      </label>
                      <input
                        type="text"
                        name="email"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "email"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="myemail@email.com"
                        value={this.state.email}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "email" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2 mt-2">
                      <label htmlFor="username" className="d-block">
                        Password
                      </label>
                      <input
                        type="password"
                        name="password"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "password"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Password"
                        value={this.state.password}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "password" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                    {ValidationDesign}

                    <div className="p-2 mt-2">
                      <label htmlFor="re_password" className="d-block">
                        Re-Password
                      </label>
                      <input
                        type="password"
                        name="re_password"
                        onChange={this.handleChange}
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "re_password"
                            ? "border-red-500 border-2"
                            : "input-border"
                        }`}
                        placeholder="Re-enter password"
                        value={this.state.re_password}
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "re_password" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>

                    <div className="p-2">
                      <div className="flex mt-2">
                        <div className="flex-1 ">
                          <Link
                            to="/login"
                            className="font-bold text-blue-600 underline"
                          >
                            Login
                          </Link>
                        </div>

                        <div className="flex-1  text-right">
                          <button
                            className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                            disabled={this.state.loading}
                            type="submit"
                          >
                            {this.state.loading === true
                              ? "Creating account..."
                              : "Create account"}
                          </button>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
}: StoreState): { todos: Todo[]; auth: Auth } => {
  return { todos: todos, auth: auth };
};

export const RegisterAccount = connect(mapStateToProps, {
  FC_RegisterAccount,
  FC_RegisterCheckCode,
  FC_CleanUserDetails,
})(_RegisterAccount);
