import { Action, ActionTypes, courseStoreInterface } from "../actions";

// default state
const defaultState: courseStoreInterface = {
  courses: null,
  selected_course: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const courseReducer = (state: courseStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOAD_COURSES:
      if (state.courses !== null) {
        state.courses.length = 0;
      }
      state.courses = action.payload;
      return state;
    case ActionTypes.SELECTED_COURSE:
      return {...state, selected_course: action.payload};
    default:
      return state;
  }
};