import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { QuestionInterface, uploadQuestionsResponse } from "../interfaces/question";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface setSelectedQuestionActions {
  type: ActionTypes.SET_SELECTED_QUESTION,
  payload: QuestionInterface
}

export interface loadQuestionBankActions {
  type: ActionTypes.LOAD_QUESTION_BANK,
  payload: QuestionInterface[]
}

export interface setUploadedQuestionsActions {
  type: ActionTypes.SET_UPLOADED_QUESTIONS,
  payload: uploadQuestionsResponse | null
}

export interface QuestionBankStoreInterface {
  questions: QuestionInterface[] | null;
  selectedQuestion: QuestionInterface | null;
  uploadedQuestions: uploadQuestionsResponse | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadQuestionBank = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<QuestionInterface[]>(`${API.QBank}/question`);
      dispatch<loadQuestionBankActions>({
        type: ActionTypes.LOAD_QUESTION_BANK,
        payload: res.data,
      });
      console.log("Question bank: ", res.data);
    } catch (error: any) {
      console.log("Err: ", {...error});
    }
    callback(false);
  };
};

export const setSelectedQuestion = (question: QuestionInterface): setSelectedQuestionActions => {
  return {
    type: ActionTypes.SET_SELECTED_QUESTION,
    payload: question,
  };
}

export const setUploadedQuestions = (questions: uploadQuestionsResponse | null): setUploadedQuestionsActions => {
  return {
    type: ActionTypes.SET_UPLOADED_QUESTIONS,
    payload: questions,
  };
}