import React from "react";
import { MdFeedback } from "react-icons/md";
import { connect } from "react-redux";
import { Auth } from "../../actions";
import { StoreState } from "../../reducers";
import Alert, { AlertInterface } from "../Alert/Alert";

// props for the component
interface AppProps {
  auth: Auth;
  close: Function;
}

enum ErrorTypes {
  names,
  email,
  msg,
  none,
  error__msg,
  success__msg,
}

interface AppState {
  loading: boolean;
  names: string;
  email: string;
  message: string;
  error: {
    target: ErrorTypes;
    msg: string;
  };
}

class _GeneralFeedback extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      names: "",
      email: "",
      message: "",
      error: {
        target: ErrorTypes.none,
        msg: "",
      },
    };
  }

  handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ): void => {
    this.setState({
      ...this.state,
      error: {
        target: ErrorTypes.none,
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // validation
    if (this.state.names.length <= 2) {
      return this.setState({
        error: {
          target: ErrorTypes.names,
          msg: "Please provide an name",
        },
      });
    }

    // validation
    if (this.state.message.length <= 2) {
      return this.setState({
        error: {
          target: ErrorTypes.msg,
          msg: "Please provide a feedback",
        },
      });
    }
    this.setState({
      loading: true,
    });
    fetch(
      "https://sheet.best/api/sheets/f4313b7d-5b70-4c31-88be-7f9e2c05e9c5",
      {
        method: "POST", // or 'PUT'
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          names: this.state.names,
          email: this.state.email,
          feedback: this.state.message,
          time: new Date().toDateString(),
          url: "www-link",
        }),
      }
    )
      .then((response) => response.json())
      .then((data) => {
        this.setState({
          loading: false,
          message: "",
          error: {
            target: ErrorTypes.success__msg,
            msg: "Thank you for sending us your feedback",
          },
        });
        // console.log("Success:", data);
      })
      .catch((error) => {
        // console.error("Error:", error);
        this.setState({
          loading: false,
          error: {
            target: ErrorTypes.error__msg,
            msg: "Something went wrong, please try again later",
          },
        });
      });
  };

  render() {
    return (
      <div className="animate__animated animate__slideInUp animate__faster fixed bottom-2 bg-hero shadow-xl border right-2 lg:w-1/3 bg-white rounded-md" style={{zIndex: 9999999999999}}>
        <div className="p-2 rounded-md text-white text-center">
          <MdFeedback className="inline-block text-4xl" />
          <h1 className="text-sm">Feedback</h1>
        </div>
        <div className="p-2 m-1 bg-white rounded-md">
          <form onSubmit={this.submitHandler}>
            {this.state.error.target === ErrorTypes.error__msg && (
              <Alert
                theme={AlertInterface.danger}
                title={this.state.error.msg}
              />
            )}

            {this.state.error.target === ErrorTypes.success__msg && (
              <Alert
                theme={AlertInterface.success}
                title={this.state.error.msg}
              />
            )}
            <div className="p-2">
              <label htmlFor="email" className="d-block">
                Names<span className="text-red-600">*</span>
              </label>
              <input
                type="text"
                name="names"
                onChange={this.handleChange}
                className={`input-white w-20 inline-block  ${
                  this.state.error.target === ErrorTypes.names
                    ? "border-red-500 border-2"
                    : "input-border"
                }`}
                placeholder="Your names"
                value={this.state.names}
                disabled={this.state.loading}
              />
              {this.state.error?.target === ErrorTypes.names && (
                <span className="text-red-600">{this.state.error?.msg}</span>
              )}
            </div>

            <div className="p-2 ">
              <label htmlFor="email" className="d-block">
                Email
              </label>
              <input
                type="email"
                name="email"
                onChange={this.handleChange}
                className={`input-white w-20 inline-block  ${
                  this.state.error.target === ErrorTypes.names
                    ? "border-red-500 border-2"
                    : "input-border"
                }`}
                placeholder="Your names"
                value={this.state.email}
                disabled={this.state.loading}
              />
              {this.state.error?.target === ErrorTypes.names && (
                <span className="text-red-600">{this.state.error?.msg}</span>
              )}
            </div>

            <div className="p-2 ">
              <label htmlFor="email" className="d-block">
                Message<span className="text-red-600">*</span>
              </label>
              <textarea
                name="message"
                onChange={this.handleChange}
                className={`input-white w-20 inline-block  ${
                  this.state.error.target === ErrorTypes.msg
                    ? "border-red-500 border-2"
                    : "input-border"
                }`}
                value={this.state.message}
                placeholder="Your feedback"
                disabled={this.state.loading}
                cols={20}
                rows={10}
              ></textarea>{" "}
              {this.state.error?.target === ErrorTypes.msg && (
                <span className="text-red-600">{this.state.error?.msg}</span>
              )}
            </div>
            <div className="px-2 text-right flex">
              <button
                disabled={this.state.loading}
                className="py-2 px-3 rounded bg-blue-500 hover:bg-blue-600 text-white"
              >
                {this.state.loading === true ? (
                  <b className="animate__animated animate__fadeIn animate__infinite">
                    Sending...
                  </b>
                ) : (
                  <span>Send Feedback</span>
                )}
              </button>
              <button
                disabled={this.state.loading}
                type="button"
                onClick={() => this.props.close()}
                className="py-2 ml-2 inline-block px-3 rounded bg-gray-200 hover:bg-red-600 text-gray-600 hover:text-white"
              >
                Close
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const GeneralFeedback = connect(mapStateToProps, {})(_GeneralFeedback);
