// Proctoring

export interface eventDefinerInterface {
  event_type: string;
  description: string;
  done_by: string;
  user_id: string;
  room_id: string;
}

export interface Proctoring {
  room: RoomServer;
  events: EventInterface[];
  candidates: CandidateProctoringInterface[];
  me: CandidateProctoringInterface;
}

export interface ActiveCandidateLiveServer {
  room_id: string;
  user_id: string;
  peer_id: string;
}

//* ********************** ACTION TYPE INTERCACES ********************** */
export interface AddSctiveUserToRoom {
  type: ActionTypes.ADD_ACTIVE_USER_TO_ROOM;
  payload: ActiveCandidateLiveServer[];
}

export interface AddOneActiveUserToRoom {
  type: ActionTypes.ADD_ONE_ACTIVE_USER_TO_ROOM;
  payload: string;
}

export interface CandidateOffline {
  type: ActionTypes.CANDIDATE_OFFLINE;
  payload: string;
}

export interface AddRoomDetailsInterface {
  candidates: CandidateProctoringInterface[];
  room: RoomServer;
}

export interface AddRoomDetails {
  type: ActionTypes.ADD_ROOM_DETAILS;
  payload: AddRoomDetailsInterface;
}

export interface ExamStartStop {
  type: ActionTypes.EXAM_START_STOP;
  payload: boolean;
}

export interface CandidateContinueExam {
  type: ActionTypes.CANDIDATE_CONTINUE_EXAM;
  payload: string;
}

export interface CandidateStopExam {
  type: ActionTypes.CANDIDATE_STOP_EXAM;
  payload: string;
}

export interface CandidateExamEvent {
  type: ActionTypes.CANDIDATE_EXAM_EVENT;
  payload: EventInterface;
}

export interface AddMyDataToProctoring {
  type: ActionTypes.ADD_MY_DATA_TO_PROCTORING;
  payload: CandidateProctoringInterface;
}

export interface MeStartStopExam {
  type: ActionTypes.ME_START_STOP_EXAM;
  payload: boolean;
}

export interface BlockExam {
  type: ActionTypes.BLOCK_EXAM;
  payload: boolean;
}
/**
 *
 * @param type
 * @param description
 * @description: prepare the event so that it may be identical with the interface
 */
export const eventDefiner = (event: eventDefinerInterface) =>
  ({
    event_id: "uuid",
    user_id: event.user_id,
    event_type: event.event_type,
    description: event.description,
    room_id: event.room_id,
    done_by: event.done_by,
    time: new Date().toDateString(),
  } as EventInterface);

/**
 * @description Get the room of the candidates
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_GetProctoringRoom = (room_id: string, callback: Function) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      const res = await axios.get(`${API.AUTH}/users`);

      const candidates = res.data.map(
        (itm: any) =>
          ({
            active: false,
            email: itm.email,
            fname: itm?.user_info?.fname,
            lname: itm?.user_info?.lname,
            stopped: false,
            user_id: itm.user_id,
            muted: false,
            peer: "",
            peer_id: "",
            ref: null,
          } as CandidateProctoringInterface)
      );

      dispatch<AddRoomDetails>({
        type: ActionTypes.ADD_ROOM_DETAILS,
        payload: {
          room: {
            block_exam: false,
            course_id: "asrefidfodpfd",
            room_id: "janvier-room",
            course_name: "Mathematics",
            start_time: new Date().toDateString(),
            end_time: new Date().toDateString(),
            exam_done: false,
            start_exam: false,
            video_support: false,
            name: "Name of room",
          } as RoomServer,
          candidates: candidates,
        },
      });

      // console.log({ user_details: res.data });
      callback(true, [], "");
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      callback(false, [], errorToText(error));
    }
  };
};

/**
 * @description receive the list of active candidates form the live server and activate them
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_SetActiveCandidates = (
  candidates: ActiveCandidateLiveServer[]
) => {
  return (dispatch: Dispatch) => {
    dispatch<AddSctiveUserToRoom>({
      type: ActionTypes.ADD_ACTIVE_USER_TO_ROOM,
      payload: candidates,
    });
  };
};

