import React, { lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Auth, FC_Logout, System } from "./actions";
import { StoreState } from "./reducers";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import {
  setNewConnection,
  FC_CheckLoggedIn,
  NavToogle,
  RemovePopupAlert,
  PushPopupAlert,
} from "./actions";
import { API } from "./utils/api";
import { io } from "socket.io-client";

//* Components
import Navigation from "./components/Navigation/Navigation";
import ProtectedRoute from "./components/PrivateRoute/PrivateRoute";

//* Routes
// import { Proctoring } from "./containers/Proctoring/Proctoring";
// import { ProctoringContainer } from "./containers/Proctoring/ProctoringContainer";
// import { ProctoringCandidateOnline } from "./containers/ProctoringCandidateOnline/ProctoringCandidateOnline";
// import { ProctoringOnlineSupervisor } from "./containers/ProctoringOnlineSupervisor/ProctoringOnlineSupervisor";

import { TempHomePage } from "./containers/TempHomePage/TempHomePage";
import { Login } from "./containers/Login/Login";
import AccessManagement from "./containers/AccessManagement";
import UserManagement from "./containers/UserManagement";
import UserAccess from "./containers/UserManagement/UserAccess";
import UserDetails from "./containers/UserManagement/UserDetails";
import District from "./containers/district";
import HeadTeacher from "./containers/HeadTeacher/RequestTeacher";
import { Dashboard } from "./containers/QuestionBank/Dashboard/Dashboard";
import { AddQuestion } from "./containers/QuestionBank/AddQuestion/AddQuestion";
import QuestionsList from "./containers/QuestionBank/QuestionsList/QuestionsList";
import UploadQuestion from "./containers/QuestionBank/AddQuestion/UploadQuestion/UploadQuestion";
import ChooseMethod from "./containers/QuestionBank/AddQuestion/ChooseMethod/ChooseMethod";
import DoExam from "./containers/DoExam/DoExam";
import Application from "./containers/Application/Application";
// import HomePage from "./containers/HomePage/HomePage";
// import HomePageWhite from "./containers/HomePage/HomePageWhite";
import HomePageHybrid from "./containers/HomePage/HomePageHybrid";
// import { UserDashboard } from "./containers/UserDashboard/UserDashboard";
import { RegisterAccount } from "./containers/RegisterAccount/RegisterAccount";
// import { ProfileDetails } from "./containers/ProfileDetails/ProfileDetails";
import LazyLoading from "./components/LazyLoading/LazyLoading";
import ApplicationsDashboard from "./containers/ApplicationsDashboard/ApplicationsDashboard";
import DocumentCategory from "./containers/DocumentCategory";
import DocumentChecking from "./containers/DocumentChecking/DocumentChecking";
import ApplicationAssessment from "./containers/ApplicationAssessment";
import { ForgetPassword } from "./containers/ForgetPassword/ForgetPassword";
import Applications from "./containers/Admin/Applications/Applications";
import Popup, { PopupAlertData } from "./components/Alert/Popup";
import { GeneralFeedback } from "./components/GeneralFeedback/GeneralFeedback";
import { MdFeedback } from "react-icons/md";
import QuestionSettings from "./containers/QuestionBank/QuestionSettings/QuestionSettings";
import AppLoading from "./components/AppLoading/AppLoading";
import SystemSettings from "./containers/SystemSettings";
import Locations from "./containers/Locations";
import Schools from "./containers/district/Schools";

// const {ProfileDetails} = lazy(() =>
//   import("./containers/ProfileDetails/ProfileDetails")
// );

// import { ProctoringCandidateOnline } from "./containers/ProctoringCandidateOnline/ProctoringCandidateOnline";
const Container_ProctoringCandidateOnline = lazy(() =>
  import(
    "./containers/ProctoringCandidateOnline/Container_ProctoringCandidateOnline"
  ).then(({ Container_ProctoringCandidateOnline }) => ({
    default: Container_ProctoringCandidateOnline,
  }))
);

const ProctoringOnlineSupervisor = lazy(() =>
  import(
    "./containers/ProctoringOnlineSupervisor/ProctoringOnlineSupervisor"
  ).then(({ ProctoringOnlineSupervisor }) => ({
    default: ProctoringOnlineSupervisor,
  }))
);

const ProfileDetails = lazy(() =>
  import("./containers/ProfileDetails/ProfileDetails").then(
    ({ ProfileDetails }) => ({ default: ProfileDetails })
  )
);

const UserDashboard = lazy(() =>
  import("./containers/UserDashboard/UserDashboard").then(
    ({ UserDashboard }) => ({ default: UserDashboard })
  )
);
//* Interfaces

// props for the component
interface AppProps {
  auth: Auth;
  system: System;
  isAuthenticated: boolean;
  setNewConnection: any;
  FC_CheckLoggedIn: Function;
  FC_Logout: Function;
  NavToogle: Function;
  RemovePopupAlert: () => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppState {
  loading: boolean;
  socket: any;
  feedback: boolean;
}

const connection = io(API.SOCKET);

class _App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      socket: null,
      feedback: false,
    };
  }

  componentDidMount() {
    //* set the new socket connection
    this.props.setNewConnection(connection);

    //* check if the user is logged in
    this.props.FC_CheckLoggedIn();
  }

  toogleFeedback = () =>
    this.setState({
      feedback: !this.state.feedback,
    });

  render() {
    const authenticationPath = "/login";
    // app loading
    if (this.props.auth.loading === true) {
      return <AppLoading />;
    }
    return (
      <Router>
        <Navigation
          isAuthenticated={this.props.isAuthenticated}
          email={this.props.auth.user.email}
          fname={this.props.auth.user.fname}
          lname={this.props.auth.user.lname}
          role={this.props.auth.user_access.role}
          Logout={this.props.FC_Logout}
          NavToogle={this.props.NavToogle}
          navActive={this.props.system.navigation}
          auth={this.props.auth}
        />

        {/* Popup alert */}
        <div
          className={`mb-3 fixed top-1/5 right-1`}
          role="alert"
          style={{ zIndex: 999999999 }}
        >
          {this.props.system.PopupAlerts !== null &&
            this.props.system.PopupAlerts.length > 0 &&
            this.props.system.PopupAlerts.map((item, i) => (
              <Popup
                title={item.title}
                description={item.description}
                theme={item.theme}
              />
            ))}
        </div>
        {this.state.feedback === true ? (
          <GeneralFeedback close={this.toogleFeedback} />
        ) : (
          <section className="fixed bottom-2 right-2 animate__animated animate__fadeInUp animate__faster">
            <button
              onClick={this.toogleFeedback}
              className="text-white px-4 w-auto h-10 bg-green-600 rounded-full hover:bg-green-700 active:shadow-lg mouse shadow transition ease-in duration-200 focus:outline-none"
            >
              <MdFeedback className="inline-block mr-2" />
              <span>Feedback</span>
            </button>
          </section>
        )}

        <div
          className={`pt-16 ${
            this.props.isAuthenticated === true
              ? this.props.system.page_has_sub_nav === true
                ? "lg:ml-64 lg:pr-2"
                : "pt-16 lg:ml-16 pl-2 lg:pr-2"
              : "w-full"
          }`}
        >
          <div
            className={`${
              this.props.isAuthenticated === true ? "max-w-screen-xl" : ""
            }  mx-auto`}
          >
            <Switch>
              <Route exact path="/" component={HomePageHybrid} />
              <Route exact path="/" component={TempHomePage} />
              <Route exact path="/register" component={RegisterAccount} />
              <Route exact path="/login" component={Login} />
              <Route exact path="/forget-password" component={ForgetPassword} />
              <Suspense fallback={<LazyLoading />}>
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/dashboard"
                  component={UserDashboard}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/admin-center"
                  component={ProctoringOnlineSupervisor}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/candidate-center"
                  component={Container_ProctoringCandidateOnline}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/profile"
                  component={ProfileDetails}
                />
                {/* Emmy routes */}
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/question-bank"
                  component={QuestionsList}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/question-bank-dashboard"
                  component={Dashboard}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/upload-questions"
                  component={UploadQuestion}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/add-question"
                  component={AddQuestion}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/method-add-question"
                  component={ChooseMethod}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/do-exam"
                  component={DoExam}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/apply"
                  component={Application}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/applications"
                  component={ApplicationsDashboard}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/applications-list"
                  component={Applications}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/question-bank-settings"
                  component={QuestionSettings}
                />
                {/* Emmy routes */}
                {/* Patrick routes */}
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/roles"
                  component={AccessManagement}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/users"
                  component={UserManagement}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/users/access/:user_id"
                  component={UserAccess}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/users/:user_id"
                  component={UserDetails}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/district"
                  component={District}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/head-teacher"
                  component={HeadTeacher}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/document-assessment"
                  component={DocumentChecking}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/document-category"
                  component={DocumentCategory}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/application-assessment"
                  component={ApplicationAssessment}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/locations"
                  component={Locations}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/settings"
                  component={SystemSettings}
                />
                <ProtectedRoute
                  isAuthenticated={this.props.auth.isAuthenticated}
                  authenticationPath={authenticationPath}
                  exact
                  path="/schools"
                  component={Schools}
                />
                {/* Patrick routes */}
              </Suspense>
            </Switch>
          </div>
        </div>
      </Router>
    );
  }
}

const mapStateToProps = ({
  auth,
  system,
}: StoreState): {
  auth: Auth;
  isAuthenticated: boolean;
  system: System;
} => {
  return {
    auth: auth,
    isAuthenticated: auth.isAuthenticated,
    system: system,
  };
};

export const App = connect(mapStateToProps, {
  setNewConnection,
  FC_CheckLoggedIn,
  FC_Logout,
  NavToogle,
  RemovePopupAlert,
  PushPopupAlert,
})(_App);
