import React, { Fragment, useEffect, useState } from "react";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { PopupAlertData } from "../../components/Alert/Popup";
import axios from "axios";
import { API } from "../../utils/api";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import {
  disableSystemSetting,
  getSystemSettings,
} from "../../actions/settings";
import { SystemSettingsInterface } from "../../shared/interfaces";
import { errorToText } from "../../utils/errors";
import { VscLoading } from "react-icons/vsc";
import { capitalizeFirstLetter } from "../../shared/dataTest";

interface AppPropsInterface {
  system_settings: SystemSettingsInterface[];
  PushPopupAlert: (alert: PopupAlertData) => void;
  getSystemSettings: Function;
  disableSystemSetting: Function;
}

const _SystemSettings = (props: AppPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [loadingRequest, setLoadingRequest] = useState<string>("");

  const disableSettings = async (
    system_setting_id: string,
    status: "enabled" | "disabled",
    name: string
  ) => {
    setLoadingRequest(system_setting_id);
    try {
      const res = await axios.patch(`${API.AUTH}/settings`, {
        setting_id: system_setting_id,
        status: status,
      });
      props.disableSystemSetting({ system_setting_id, status });
      props.PushPopupAlert({
        title: `${name} ${status} successfully`,
        theme: PopupAlertTheme.success,
      });
      console.log("Deleted: ", res);
    } catch (error) {
      console.error("err: ", error);
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadingRequest("");
  };

  useEffect(() => {
    if (props.system_settings === null || props.system_settings.length === 0) {
      props.getSystemSettings((status: boolean, msg: string) => {
        setLoading(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }
  }, [props, setLoading]);

  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">
            System Settings
          </div>
          {/* {loading === false && (
            <div
              onClick={() => setAddNew(true)}
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
            >
              Add new
            </div>
          )} */}
        </div>
        <div className="flex flex-col">
          {props.system_settings === null || loading === true ? (
            <Loading />
          ) : props.system_settings.length === 0 ? (
            <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
              <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
              <span>No settings available</span>
            </div>
          ) : (
            props.system_settings.map((item, i) => (
              <div
                key={i + item.system_setting_id}
                className={`border flex flex-row px-3 py-2 rounded items-start my-1 justify-between`}
              >
                <div className="flex flex-col">
                  <span className="text-gray-600 font-bold">{item.name}</span>
                  <span className={`text-xs pr-5 text-gray-500`}>
                    {item.key}
                  </span>
                  <span
                    className={`text-xs pr-5 ${
                      item.status === "disabled"
                        ? "text-red-700"
                        : "text-green-700"
                    }`}
                  >
                    {capitalizeFirstLetter(item.status)}
                  </span>
                </div>
                <div className="flex flex-row items-center gap-2">
                  <div
                    className={`relative ${
                      loadingRequest.length > 0
                        ? "cursor-not-allowed"
                        : "cursor-pointer"
                    } flex items-center gap-1`}
                    onClick={() =>
                      !loadingRequest.length &&
                      disableSettings(
                        item.system_setting_id,
                        item.status === "disabled" ? "enabled" : "disabled",
                        item.name
                      )
                    }
                  >
                    <span className="block w-14 h-8 bg-gray-100 rounded-full shadow-inner"></span>
                    <span
                      className={`absolute block w-6 h-6 mt-1 ml-1 rounded-full shadow inset-y-0 left-0 focus-within:shadow-outline transition-transform duration-500 ease-in-out ${
                        item.status === "disabled"
                          ? "bg-red-500"
                          : "bg-green-500 transform translate-x-full"
                      }`}
                    ></span>
                    {loadingRequest === item.system_setting_id ? (
                      <VscLoading className="animate__animated animate__rotateIn animate__infinite" />
                    ) : (
                      <span className="text-lg font-mono">
                        {item.status === "disabled" ? "Off " : "On"}
                      </span>
                    )}
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
}: StoreState): {
  auth: Auth;
  system_settings: SystemSettingsInterface[];
} => {
  return {
    auth: auth,
    system_settings: system_settings.systemSettings,
  };
};

const SystemSettings = connect(mapStateToProps, {
  getSystemSettings,
  disableSystemSetting,
  PushPopupAlert,
})(_SystemSettings);

export default SystemSettings;
