import { combineReducers } from "redux";
import { todosReducer } from "./todos";
import { authReducer } from "./auth";
import { accessReducer, AccessType } from "./access";
import { userReducer } from "./users";

import {
  ApplicationAssessmentInterface,
  PositionInterface,
  UserInterface,
} from "../shared/interfaces";
import { schoolReducer, SchoolReducerType } from "./school";
import { positionReducer } from "./positionRequests";
import { proctoringReducer } from "./proctoring";
import { systemReducer } from "./system";

import {
  Todo,
  Auth,
  Socket,
  Proctoring,
  examDataInterface,
  System,
  applicationPeriodsStoreInterface,
  questionTypesStoreInterface,
  courseStoreInterface,
} from "../actions";
import { socketReducer } from "./socket";

import { doExamReducer } from "./exam";
import { applicationStoreInterface } from "../actions/application";
import { applicationReducer } from "./application";
import { documentReducer, DocumentReducerInterface } from "./document";
import { ApplicationAssessmentReducer } from "./applicationAssessment";
import { applicationsListStoreInterface } from "../actions/ApplicationsList";
import { applicationsListReducer } from "./applications_list";
import { positionsStoreInterface } from "../actions/positions";
import { positionsReducer } from "./positions";
import { applicationPeriodsReducer } from "./application_periods";
import { schoolLevelsStoreInterface } from "../actions/school_levels";
import { schoolLevelsReducer } from "./school_levels";
import { LocationReducerInterface, locationsReducer } from "./location";
import { questionTypesReducer } from "./questionTypes";
import { courseReducer } from "./course";
import { systemSettingsReducer, SystemSettingsType } from "./settings";
import { QuestionBankStoreInterface } from "../actions/question";
import { questionBankReducer } from "./question";

// define the entire state into the entire sire
export interface StoreState {
  todos: Todo[];
  auth: Auth;
  system_settings: SystemSettingsType;
  access: AccessType;
  document: DocumentReducerInterface;
  locations: LocationReducerInterface;
  applicationToAssess: ApplicationAssessmentInterface[];
  users: UserInterface[];
  schools: SchoolReducerType;
  positionRequests: PositionInterface[];
  socket: Socket;
  doExam: examDataInterface;
  application: applicationStoreInterface;
  proctoring: Proctoring;
  system: System;
  applications_list: applicationsListStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionTypes: questionTypesStoreInterface;
  course: courseStoreInterface;
  questionBank: QuestionBankStoreInterface;
}

export const reducers = combineReducers<StoreState>({
  todos: todosReducer,
  auth: authReducer,
  system_settings: systemSettingsReducer,
  access: accessReducer,
  document: documentReducer,
  locations: locationsReducer,
  schools: schoolReducer,
  applicationToAssess: ApplicationAssessmentReducer,
  users: userReducer,
  positionRequests: positionReducer,
  socket: socketReducer,
  doExam: doExamReducer,
  application: applicationReducer,
  proctoring: proctoringReducer,
  system: systemReducer,
  applications_list: applicationsListReducer,
  positions: positionsReducer,
  periods: applicationPeriodsReducer,
  schoolLevels: schoolLevelsReducer,
  questionTypes: questionTypesReducer,
  course: courseReducer,
  questionBank: questionBankReducer,
});
