import axios from "axios";
import React, { useEffect, useState } from "react";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { ApplicationSessionInterface } from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import { DATE } from "../../utils/functions";

interface AppProps {
  closeModal: (state: boolean) => void;
  selectedPeriod: ApplicationSessionInterface | null;
  setSelectedPeriod: React.Dispatch<
    React.SetStateAction<ApplicationSessionInterface | null>
  >;
  PushPopupAlert: (alert: PopupAlertData) => void;
  createAplicationSession: Function;
  updateAplicationSession: Function;
}

const CreateApplicationSession: React.FC<AppProps> = (props) => {
  const [sessionName, setSessionName] = useState<string>("");
  const [sessionPeriod, setSessionPeriod] = useState<string>("");
  const [sessionStart, setSessionStart] = useState<string>("");
  const [sessionEnd, setSessionEnd] = useState<string>("");
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [loading, setLoading] = useState<boolean>(false);

  const isDifferenceValid = function (date1: string, date2: string): boolean {
    const valid_date_1: Date = new Date(date1);
    const valid_date_2: Date = new Date(date2);
    if (valid_date_1 >= valid_date_2) return false;
    else return true;
  };

  useEffect(() => {
    if (props.selectedPeriod) {
      !sessionName.length &&
        setSessionName(props.selectedPeriod.application_session_name);
      !sessionPeriod.length && setSessionPeriod(props.selectedPeriod.period);
      !sessionStart.length &&
        setSessionStart(
          DATE(props.selectedPeriod.application_start_date, "YYYY/MM/DD")
        );
      !sessionEnd.length &&
        setSessionEnd(
          DATE(props.selectedPeriod.application_end_date, "YYYY/MM/DD")
        );
      // console.log({
      //   og: props.selectedPeriod.application_start_date,
      //   new: DATE(props.selectedPeriod.application_start_date),
      //   new_2: DATE(props.selectedPeriod.application_start_date).split("/").reverse().join("/"),
      // });
    }
  }, [
    props.selectedPeriod,
    sessionEnd.length,
    sessionName.length,
    sessionPeriod.length,
    sessionStart.length,
  ]);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // validation
    if (sessionName === "") {
      return setFormError({
        element: "name",
        msg: "Please fill the session name",
      });
    } else if (sessionPeriod === "") {
      return setFormError({
        element: "period",
        msg: "Please fill session period",
      });
    } else if (sessionStart === "") {
      return setFormError({
        element: "start_date",
        msg: "Please fill the session start date",
      });
    } else if (sessionEnd === "") {
      return setFormError({
        element: "end_date",
        msg: "Please fill the session end date",
      });
    } else if (!isDifferenceValid(sessionStart, sessionEnd)) {
      return setFormError({
        element: "end_date",
        msg: "Please fill the valid session end date",
      });
    } else {
      setLoading(true);
      try {
        // Update current
        if (props.selectedPeriod !== null) {
          console.log({
            application_session_id: props.selectedPeriod.application_session_id,
            application_session_name: sessionName,
            period: sessionPeriod,
            application_start_date: sessionStart,
            application_end_date: sessionEnd,
          });
          const res = await axios.patch(
            `${API.APPLICATION}/applicationsesion`,
            {
              application_session_id:
                props.selectedPeriod.application_session_id,
              application_session_name: sessionName,
              period: sessionPeriod,
              application_start_date: sessionStart,
              application_end_date: sessionEnd,
            }
          );

          props.updateAplicationSession(res.data.data);

          props.PushPopupAlert({
            title: `Application Session updated successfully`,
            theme: PopupAlertTheme.info,
          });
          props.setSelectedPeriod(null);
          props.closeModal(false);
          console.log("Result: ", res.data.data);
        } else {
          // Add new
          const res = await axios.post(`${API.APPLICATION}/applicationsesion`, {
            application_session_name: sessionName,
            period: sessionPeriod,
            application_start_date: sessionStart,
            application_end_date: sessionEnd,
          });
          props.createAplicationSession(res.data.session);
          props.PushPopupAlert({
            title: `Application session created successfully`,
            theme: PopupAlertTheme.success,
          });
          props.closeModal(false);
          console.log("Result: ", res);
        }
      } catch (error) {
        props.PushPopupAlert({
          title: `${errorToText(error)}! Try again`,
          theme: PopupAlertTheme.danger,
        });
        console.error("Err: ", { error });
      }
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="-mt-2">
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Session name
        </span>
        <input
          disabled={loading}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            setSessionName(e.target.value);
            setFormError(null);
          }}
          type="text"
          value={sessionName}
          className={`input-white border-2 ${
            formError !== null && formError.element === "name"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "name" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Session period
        </span>
        <input
          disabled={loading}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            setSessionPeriod(e.target.value);
            setFormError(null);
          }}
          type="text"
          value={sessionPeriod}
          className={`input-white border-2 ${
            formError !== null && formError.element === "period"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "period" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Session start date
        </span>
        <input
          disabled={loading}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            setSessionStart(e.target.value);
            setFormError(null);
          }}
          type="date"
          value={sessionStart}
          className={`input-white border-2 ${
            formError !== null && formError.element === "start_date"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "start_date" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>

      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Session end date
        </span>
        <input
          disabled={loading}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            setSessionEnd(e.target.value);
            setFormError(null);
          }}
          type="date"
          value={sessionEnd}
          className={`input-white border-2 ${
            formError !== null && formError.element === "end_date"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "end_date" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>

      <div className="flex flex-row justify-between items-center">
        <button
          disabled={loading}
          type="submit"
          className={`btn bg-blue-600 text-white ${
            loading === true
              ? "animate__animated animate__flash animate__infinite"
              : ""
          }`}
        >
          {loading === true
            ? "Please wait..."
            : props.selectedPeriod !== null
            ? "Update"
            : "Create"}
        </button>
      </div>
    </form>
  );
};

export default CreateApplicationSession;
