import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { BiRightArrowAlt } from "react-icons/bi";
import {
  MdDelete,
  MdEdit,
  MdHighlightOff,
  MdRemoveRedEye,
  MdSettings,
} from "react-icons/md";
import { SiAddthis } from "react-icons/si";
import { TiInputChecked } from "react-icons/ti";
import { connect } from "react-redux";
import { customizeUserAccess } from "../../actions/users";
import Table from "../../components/Table/Table";
import {
  AccessTaskInterface,
  CustomeAccessInterface,
  UserInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";

interface AppProps {
  userInfo: UserInterface;
  manageUserId: string;
  customizeUserAccess: Function;
  setFeedBack: React.Dispatch<
    React.SetStateAction<{
      type: "success" | "danger" | "warning" | "";
      message: string;
    }>
  >;
  feedBack: {
    type: "success" | "danger" | "warning" | "";
    message: string;
  };
  setUserAccess: React.Dispatch<
    React.SetStateAction<AccessTaskInterface[] | null>
  >;
  userAccess: AccessTaskInterface[] | null;
  setCurrentAccess: React.Dispatch<
    React.SetStateAction<AccessTaskInterface | null>
  >;
  currentAccess: AccessTaskInterface | null;
}

const UserAccess: React.FC<AppProps> = ({
  userInfo,
  customizeUserAccess,
  setFeedBack,
  feedBack,
  setUserAccess,
  userAccess,
  currentAccess,
  setCurrentAccess,
}) => {
  const [loading, setLoadind] = useState<boolean>(true);
  const [loadindRequest, setLoadindRequest] = useState<boolean>(false);
  // const [currentAccess, setCurrentAccess] =
  //   useState<AccessTaskInterface | null>();
  // const [userAccess, setUserAccess] = useState<AccessTaskInterface[] | null>();
  const [createPermission, setCreatePermission] = useState<boolean>(false);
  const [viewPermission, setViewPermission] = useState<boolean>(false);
  const [updatePermission, setUpdatePermission] = useState<boolean>(false);
  const [deletePermission, setDeletePermission] = useState<boolean>(false);

  const overrideUserAccess = (
    default_access: AccessTaskInterface[],
    custome_access: AccessTaskInterface[]
  ) => {
    console.log({ default_access, custome_access });
    if (default_access && custome_access && custome_access.length > 0) {
      let overideAccess: AccessTaskInterface[] = [];
      for (const [key, access] of Object.entries(default_access)) {
        const findCustom = custome_access.find(
          (custom) => custom.id === access.id
        );
        if (findCustom) {
          overideAccess.push(findCustom);
        } else {
          overideAccess.push(access);
        }
        console.log(key);
      }
      setUserAccess(overideAccess);
    }
  };

  useEffect(() => {
    const overrideUserAccess = (
      default_access: AccessTaskInterface[],
      custome_access: AccessTaskInterface[]
    ) => {
      if (default_access && custome_access && custome_access.length > 0) {
        let overideAccess: AccessTaskInterface[] = [];
        for (const [key, access] of Object.entries(default_access)) {
          const findCustom = custome_access.find(
            (custom) => custom.id === access.id
          );
          if (findCustom) {
            overideAccess.push(findCustom);
          } else {
            overideAccess.push(access);
          }
          console.log(key);
        }
        setUserAccess(overideAccess);
      }
    };

    if (userInfo && userInfo.custome_access) {
      userInfo.default_access &&
        overrideUserAccess(
          userInfo.default_access.access,
          userInfo.custome_access.access
        );
    }
    console.log("nturebase");
  }, [userInfo, setUserAccess]);

  useEffect(() => {
    if (userInfo && !userInfo.custome_access) {
      userInfo.default_access && setUserAccess(userInfo.default_access.access);
    }
    setLoadind(false);
  }, [userInfo, setUserAccess]);

  const tableData = (): JSX.Element[] => {
    if (!userAccess)
      return [
        <tr key={46}>
          <td>No data</td>
        </tr>,
      ];
    return userAccess.map((access, i) => (
      <tr
        key={i + access.id}
        className={`text-left border-b border-gray-200 cursor-pointer ${
          currentAccess &&
          currentAccess.id === access.id &&
          "bg-gray-300 shadow-md my-3 hover:bg-gray-100"
        }`}
        onClick={() =>
          currentAccess
            ? (setCurrentAccess(null),
              setCreatePermission(false),
              setViewPermission(false),
              setUpdatePermission(false),
              setDeletePermission(false))
            : (setCurrentAccess(access),
              setCreatePermission(access.permitted.create),
              setViewPermission(access.permitted.view),
              setUpdatePermission(access.permitted.update),
              setDeletePermission(access.permitted.delete))
        }
      >
        <td className="px-2 py-1">
          <label className="inline-flex items-center">
            {/* <input
              type="radio"
              name="taskAccess"
              value={access.id}
              className="form-radio h-4 w-4 text-gray-600"
              onChange={() => {
                currentAccess
                  ? this.setState({ currentAccess: null })
                  : this.setState({ currentAccess: access });
              }}
              checked={currentAccess ? true : false}
            /> */}
            <span className="text-gray-900 capitalize text-xl font-normal cursor-pointer">
              {access.task}
            </span>
          </label>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.create ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.create ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.create ? " Yes" : "No"}
          </div>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.view ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.view ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.view ? " Yes" : "No"}
          </div>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.update ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.update ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.update ? " Yes" : "No"}
          </div>
        </td>
        <td>
          <div
            className={`inline-flex items-center text-xl ${
              access.permitted.delete ? "text-green-700" : "text-red-700"
            }`}
          >
            {access.permitted.delete ? <TiInputChecked /> : <MdHighlightOff />}
            {access.permitted.delete ? " Yes" : "No"}
          </div>
        </td>
      </tr>
    ));
  };

  const handleCustomization = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoadindRequest(true);
    setFeedBack({
      type: "",
      message: "",
    });
    let task: [] | CustomeAccessInterface | null = null;
    task = userInfo.custome_access;
    let index = -1;

    try {
      if (task && currentAccess) {
        index = task.access.findIndex(
          (access) => access.id === currentAccess.id
        );
        if (index >= 0) {
          task.access[index].permitted.create = createPermission;
          task.access[index].permitted.view = viewPermission;
          task.access[index].permitted.update = updatePermission;
          task.access[index].permitted.delete = deletePermission;
        } else {
          task.access.push({
            id: currentAccess.id,
            task: currentAccess.task,
            key: currentAccess.key,
            permitted: {
              create: createPermission,
              view: viewPermission,
              update: updatePermission,
              delete: deletePermission,
            },
          });
        }
      } else if (currentAccess) {
        task = {
          access: [
            {
              id: currentAccess.id,
              task: currentAccess.task,
              key: currentAccess.key,
              permitted: {
                create: createPermission,
                view: viewPermission,
                update: updatePermission,
                delete: deletePermission,
              },
            },
          ],
          user_id: userInfo.user_id,
          custome_user_access_id: "",
        };
      }

      if (task) {
        let res;
        if (userInfo.custome_access !== null) {
          res = await axios.patch<{ data: CustomeAccessInterface }>(
            `${API.AUTH}/custaccess`,
            {
              access: task.access,
              user_id: userInfo.user_id,
            }
          );
        } else {
          res = await axios.post<{ data: CustomeAccessInterface }>(
            `${API.AUTH}/custaccess`,
            {
              access: task.access,
              user_id: userInfo.user_id,
            }
          );
        }

        if (res) {
          task.custome_user_access_id = res.data.data.custome_user_access_id;
          console.log({ task });
          console.log(res.data.data);

          customizeUserAccess(task);
          task &&
            userInfo.default_access &&
            overrideUserAccess(userInfo.default_access.access, task.access);
          setFeedBack({
            type: "success",
            message: `Reseting User Access complete successfully`,
          });

          task = null;
          setCurrentAccess(null);
          setCreatePermission(false);
          setViewPermission(false);
          setUpdatePermission(false);
          setDeletePermission(false);
        }
      }
    } catch (error) {
      console.error({ customize_access: error });
      setFeedBack({
        type: "danger",
        message: errorToText(error),
      });
    }
    setLoadindRequest(false);
  };

  if (loading === true || userInfo === null) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading User Access...
      </div>
    );
  }

  if (loading) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading...
      </div>
    );
  }

  return (
    <Fragment>
      <div className="grid grid-cols-5 gap-4 my-4">
        <div className="col-span-2 bg-white w-full shadow-lg text-center p-2">
          <span className="text-gray-600 mt-2 font-semibold text-2xl">
            Settings
          </span>
          {!loading && (
            <Table
              columns={[
                {
                  columnTitle: "Access Name",
                  alignText: "left",
                  padding: "p-3",
                },
                { columnTitle: "Create", alignText: "left" },
                { columnTitle: "View", alignText: "left" },
                { columnTitle: "Update", alignText: "left" },
                { columnTitle: "Delete", alignText: "left" },
              ]}
              rows={tableData}
              pagination={false}
              search={true}
              searchBG="bg-gray-50"
              thColor="gray"
            />
          )}
        </div>
        <div className="col-span-3">
          {!currentAccess ? (
            <div className="w-full flex flex-col items-center text-gray-600 gap-1 py-3">
              <MdSettings className="w-20 h-20" />
              <span className="text-12">Choose Access to Customize</span>
            </div>
          ) : (
            <div>
              <h1 className="text-3xl font-medium text-center my-3 text-gray-900">
                Customize User Access
              </h1>
              <div className="flex justify-center">
                <h2 className="text-2xl font-semibold text-center text-gray-600 flex flex-row items-center">
                  <BiRightArrowAlt className="w-7 h-7" />
                  {currentAccess.task}
                </h2>
              </div>

              <div className="flex flex-col">
                <form
                  className="flex flex-col items-center"
                  onSubmit={(e) => handleCustomization(e)}
                >
                  <div className="inline-flex gap-2 items-center">
                    <SiAddthis className="w-7 h-7 text-gray-600" />
                    <div className="flex flex-col">
                      <h3 className="font-semibold text-xl">Create</h3>
                      <span className="text-gray-600 text-sm md:pr-1">
                        This permission gives the user the ability to create
                      </span>
                    </div>
                    <input
                      type="checkbox"
                      className="form-checkbox h-7 w-7 text-teal-600"
                      onChange={() => setCreatePermission(!createPermission)}
                      checked={createPermission}
                      disabled={loadindRequest}
                    />
                  </div>
                  <div className="inline-flex gap-2 items-center">
                    <MdRemoveRedEye className="w-7 h-7 text-gray-600" />
                    <div className="flex flex-col">
                      <h3 className="font-semibold text-xl">View</h3>
                      <span className="text-gray-600 text-sm md:pr-4">
                        This permission gives the user the ability to view
                      </span>
                    </div>
                    <input
                      type="checkbox"
                      className="form-checkbox h-7 w-7 text-teal-600"
                      onChange={() => setViewPermission(!viewPermission)}
                      checked={viewPermission}
                      disabled={loadindRequest}
                    />
                  </div>
                  <div className="inline-flex gap-2 items-center">
                    <MdEdit className="w-7 h-7 text-gray-600" />
                    <div className="flex flex-col">
                      <h3 className="font-semibold text-xl">Update</h3>
                      <span className="text-gray-600 text-sm">
                        This permission gives the user the ability to update
                      </span>
                    </div>
                    <input
                      type="checkbox"
                      className="form-checkbox h-7 w-7 text-teal-600"
                      onChange={() => setUpdatePermission(!updatePermission)}
                      checked={updatePermission}
                      disabled={loadindRequest}
                    />
                  </div>
                  <div className="inline-flex gap-2 items-center">
                    <MdDelete className="w-7 h-7 text-gray-600" />
                    <div className="flex flex-col">
                      <h3 className="font-semibold text-xl">Delete</h3>
                      <span className="text-gray-600 text-sm md:pr-1">
                        This permission gives the user the ability to delete
                      </span>
                    </div>
                    <input
                      type="checkbox"
                      className="form-checkbox h-7 w-7 text-teal-600"
                      onChange={() => setDeletePermission(!deletePermission)}
                      checked={deletePermission}
                      disabled={loadindRequest}
                    />
                  </div>

                  <div className="flex items-center justify-center w-full py-3 gap-2">
                    {/* <button className="btn btn-secondary bg-red-600">Back</button> */}
                    <button
                      className="btn bg-gray-500 hover:bg-gray-700 focus:outline-none text-white"
                      disabled={loadindRequest}
                      onClick={() => {
                        setCurrentAccess(null);
                        setCreatePermission(false);
                        setViewPermission(false);
                        setUpdatePermission(false);
                        setDeletePermission(false);
                      }}
                    >
                      Cancel
                    </button>
                    <button className="btn btn-blue" disabled={loadindRequest}>
                      {!loadindRequest ? "Save Change" : "Processing.."}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default connect(null, {
  customizeUserAccess,
})(UserAccess);
