import { Action, ActionTypes, examDataInterface } from "../actions";
import { psychometric_questions_list } from "../containers/DoExam/QuestionsList/QuestionsList";
import { ExamsList, examStatus } from "../interfaces/exam";

// default state
const defaultState: examDataInterface = {
  examsList: ExamsList,
  exam_answers: {
    exam_id: "",
    exam_date_time: new Date(),
    duration: 2,
    course: {
      course_id: "",
      course_name: "",
      course_code: "",
    },
    exam_location: {
      province: "",
      district: "",
      center: "",
      room: ""
    },
    proctoring_type: "",
    academic_year: "",
    exam_questions: psychometric_questions_list,
    exam_questions_answers: {
      questionAnswers: []
    },
    status: examStatus.Pending,
  }
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const doExamReducer = (state: examDataInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.GET_USER_ANSWERS:
      return {
        ...state
      }
    case ActionTypes.LOAD_EXAM:
      state.user_id = action.payload.user_id
      state.exam_answers = action.payload.exam;
      return {
        ...state, 
      }
    case ActionTypes.ADD_ANSWER:
      if (state.exam_answers) {
        if (state.exam_answers.exam_questions_answers) {
          state.exam_answers.exam_questions_answers.questionAnswers = state.exam_answers.exam_questions_answers.questionAnswers.filter((itm: { question_id: string | null; }) => itm.question_id !== action.payload.question_id);
          state.exam_answers.exam_questions_answers.questionAnswers.push(action.payload);
          localStorage.setItem(ActionTypes.ADD_ANSWER, JSON.stringify(state));
        }
        else {
          alert("You did")
        }
      }
      return {
        ...state, 
      }
    case ActionTypes.deleteAnswers:
      state.exam_answers!.exam_questions_answers!.questionAnswers.length = 0;
      return {
        ...state, 
      }
    case ActionTypes.submitAnswersAction:
      state.user_id = action.payload;
      if (state.exam_answers) {
        state.exam_answers.status = examStatus.Done
      }
      return {
        ...state, 
      }
    case ActionTypes.CHANGE_EXAM_STATUS:
      state.user_id = action.payload.user_id;
      if (state.exam_answers) {
        state.exam_answers.status = action.payload.status
      }
      return {
        ...state, 
      }
    default:
      return state;
  }
};
