import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { questionType } from "../interfaces/question";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface setSelectedQuestionTypeActions {
  type: ActionTypes.SET_SELECTED_QUESTION_TYPE,
  payload: questionType
}

export interface questionTypesActions {
  type: ActionTypes.GET_QUESTION_TYPES,
  payload: questionType[]
}

export interface questionTypesStoreInterface {
  questionTypes: questionType[] | null;
  selectedQuestionType: questionType | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadQuestionTypes = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<questionType[]>(`${API.QBank}/question-type`);
      dispatch<questionTypesActions>({
        type: ActionTypes.GET_QUESTION_TYPES,
        payload: res.data,
      });
      console.log("Q types: ", res.data);
    } catch (error: any) {
      console.log("Err: ", {...error});
    }
    callback(false);
  };
};

export const setSelectedQuestionType = (questionType: questionType): setSelectedQuestionTypeActions => {
  return {
    type: ActionTypes.SET_SELECTED_QUESTION_TYPE,
    payload: questionType,
  };
}