import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { AdminApplicationsList, GetApplicationsAPI } from "../interfaces/application";
import { API } from "../utils/api";
import { education_category, PositionAPI } from "../../src/interfaces/index";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface applicationsListActions {
  type: ActionTypes.LOAD_APPLICATIONS_LIST,
  payload: AdminApplicationsList[]
}

export interface applicationsListStoreInterface {
  applicationsList: AdminApplicationsList[] | null;
  selected_application: GetApplicationsAPI | null;
  selected_position: PositionAPI | null;
  positions_list: PositionAPI[] | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const AdminLoadApplicationList = (position_id: string, session_id: string, education_category: education_category | null, callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<AdminApplicationsList[]>(`${API.APPLICATION}/application/${position_id}/${session_id}`);
      dispatch<applicationsListActions>({
        type: ActionTypes.LOAD_APPLICATIONS_LIST,
        payload: education_category === null ? res.data : res.data.filter(itm => itm.position.education_institute_id.education_institute_id === education_category.education_institute_id),
      });
      console.log("A: ", res.data);
    } catch (error) {
      console.log("Err: ", {...error});
    }
    callback(false);
  };
};