import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { PositionInterface } from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface FetchPositionsAction {
  type: ActionTypes.FETCH_POSITIONS;
  payload: PositionInterface[];
}

export interface CreatePositionAction {
  type: ActionTypes.CREATE_POSITION;
  payload: PositionInterface;
}

export interface UpdatePositionAction {
  type: ActionTypes.UPDATE_POSITION;
  payload: PositionInterface;
}

export interface DeletePositionAction {
  type: ActionTypes.DELETE_POSITION;
  payload: PositionInterface[];
}

export type PositionActionType =
  | FetchPositionsAction
  | CreatePositionAction
  | UpdatePositionAction
  | DeletePositionAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const fetchPositions = () => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<PositionInterface[]>(
        `${API.APPLICATION}/position`
      );
      dispatch<FetchPositionsAction>({
        type: ActionTypes.FETCH_POSITIONS,
        payload: res.data,
      });
    } catch (error) {
      console.log({ position_error: error });
    }
  };
};

export const createPosition = (data: PositionInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<CreatePositionAction>({
      type: ActionTypes.CREATE_POSITION,
      payload: data,
    });
  };
};

export const updatePosition = (data: PositionInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<UpdatePositionAction>({
      type: ActionTypes.UPDATE_POSITION,
      payload: data,
    });
  };
};

export const deletePosition = (data: PositionInterface[]) => {
  return async (dispatch: Dispatch) => {
    dispatch<DeletePositionAction>({
      type: ActionTypes.DELETE_POSITION,
      payload: data,
    });
  };
};
