import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import {
  MdAdd,
  MdCancel,
  MdClear,
  MdCreate,
  MdKeyboardArrowRight,
  MdLocationCity,
  MdSave,
} from "react-icons/md";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import {
  FC_CreateDistrict,
  FC_CreateProvince,
  FC_DeleteDistrict,
  FC_DeleteProvince,
  FC_UpdateDistrict,
  FC_UpdateProvince,
  getAllRoom,
} from "../../actions/location";
import { PopupAlertData } from "../../components/Alert/Popup";
import {
  MainItem,
  SideNavigation,
} from "../../components/Navigation/SideNavigation";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import {
  CentersInterface,
  DistrictInterface,
  RoomLocationInterface,
  RoomsInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import ViewCenters from "./ViewCenters";

interface AppProps {
  locations: RoomLocationInterface[];
  getAllRoom: Function;
  FC_CreateProvince: Function;
  FC_UpdateProvince: Function;
  FC_DeleteProvince: Function;
  FC_CreateDistrict: Function;
  FC_UpdateDistrict: Function;
  FC_DeleteDistrict: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const Index: React.FC<AppProps> = ({
  locations,
  getAllRoom,
  FC_CreateProvince,
  FC_UpdateProvince,
  FC_DeleteProvince,
  FC_CreateDistrict,
  FC_UpdateDistrict,
  FC_DeleteDistrict,
  PushPopupAlert,
}) => {
  const [actionTypes, setActionTypes] = useState<
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_CENTER"
    | "UPDATE_CENTER"
    | "CREATE_ROOM"
    | "UPDATE_ROOM"
    | ""
  >("");
  const [thisProvince, setThisProvince] =
    useState<RoomLocationInterface | null>(null);
  const [thisDistrict, setThisDistrict] = useState<DistrictInterface | null>(
    null
  );
  const [thisCenter, setThisCenter] = useState<CentersInterface | null>(null);
  const [thisRoom, setThisRoom] = useState<RoomsInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    if (!locations.length) {
      getAllRoom((status: boolean, msg: string) => {
        if (status === true && msg.length > 0) console.log(msg);
      });
    } else if (locations.length && thisProvince === null) {
      setThisProvince(locations[0]);
    } else if (thisProvince && thisDistrict === null) {
      setThisDistrict(locations[0].districts[0]);
    } else {
      setLoading(false);
    }
  }, [getAllRoom, locations.length, locations, thisProvince, thisDistrict]);

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading locations...
      </div>
    );
  }

  return (
    <Fragment>
      <SideNavigation>
        <span className="text-base mt-2">Provinces</span>
        {locations.map((province, index) => (
          <section
            key={index + province.province_id}
            className="mt-1"
            onClick={() => {
              setThisProvince(province);
              setThisDistrict(province.districts[0]);
            }}
          >
            <MainItem
              title={province.province_name}
              active={
                thisProvince &&
                thisProvince.province_id === province.province_id
                  ? true
                  : false
              }
            />
          </section>
        ))}
      </SideNavigation>

      <div className="w-full my-5">
        {/* header title */}
        <div className="flex flex-row items-start gap-2">
          <MdLocationCity className="text-blue-500 w-24 h-24" />
          <div className="flex justify-between w-full mt-2">
            <div className="flex flex-col">
              <h1 className="text-4xl font-semibold">Locations Management</h1>

              <div className="flex flex-row items-center md:gap-3 gap-2">
                <div className="flex flex-row items-center mt-2 gap-1">
                  {/* <span className="text-gray-600 text-lg">Desc:</span> */}
                  {thisProvince && (
                    <span className="text-gray-600 text-lg font-bold">
                      {thisProvince.province_name}
                    </span>
                  )}
                  {thisDistrict && (
                    <div className="flex flex-row items-center gap-1">
                      <MdKeyboardArrowRight className="w-5 h-5" />
                      <span className="text-blue-600 text-lg font-bold">
                        {thisDistrict.district_name}
                      </span>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* end header title */}

        <div className="w-full flex h-full flex-col md:flex-row md:items-start gap-2">
          {/* ============================  DISTRICT ============================= */}
          <div className="min-h-full w-1/4 bg-white shadow rounded border px-4 py-2">
            <span className="text-base mt-2">Districts</span>
            {thisProvince &&
            locations.filter(
              (loc) => loc.province_id === thisProvince.province_id
            ).length > 0 ? (
              locations
                .filter(
                  (loc) => loc.province_id === thisProvince.province_id
                )[0]
                .districts.map((district, index) => (
                  <section
                    key={index + district.district_id}
                    className="mt-2"
                    onClick={() => setThisDistrict(district)}
                  >
                    <MainItem
                      title={district.district_name}
                      active={
                        thisDistrict &&
                        thisDistrict.district_id === district.district_id
                          ? true
                          : false
                      }
                    />
                  </section>
                ))
            ) : (
              <div>No</div>
            )}
          </div>
          {/* =========================== END DISTRICT =========================== */}

          {/* ============================= CENTERS ============================= */}
          {thisDistrict && thisProvince && (
            <ViewCenters
              thisProvinceId={thisProvince.province_id}
              thisDistrict={thisDistrict}
              thisCenter={thisCenter}
              thisRoom={thisRoom}
              loadingRequest={loadingRequest}
              actionTypes={actionTypes}
              setThisDistrict={setThisDistrict}
              setLoadingRequest={setLoadingRequest}
              setThisCenter={setThisCenter}
              setThisRoom={setThisRoom}
              setActionTypes={setActionTypes}
            />
          )}
          {/* =========================== END CENTERS =========================== */}
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  locations,
}: StoreState): {
  locations: RoomLocationInterface[];
} => {
  return { locations: locations.roomLocation };
};

export default connect(mapStateToProps, {
  getAllRoom,
  FC_CreateProvince,
  FC_UpdateProvince,
  FC_DeleteProvince,
  FC_CreateDistrict,
  FC_UpdateDistrict,
  FC_DeleteDistrict,
  PushPopupAlert,
})(Index);
