import React, { useEffect, useState } from "react";
import { MdNavigateBefore, MdNavigateNext, MdSearch } from "react-icons/md";

const FC_makeID = (length: number = 5): string => {
  let result: string = "";
  let characters: string =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  let charactersLength = characters.length;
  for (let i: number = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
};

export interface TableColumnsInterface {
  columnTitle: JSX.Element | string;
  alignText: string;
  padding?: string;
}

interface TableProps {
  columns: TableColumnsInterface[];
  rows: Function;
  hideColumns?: boolean;
  pagination?: boolean;
  search?: boolean;
  tableSize?: string;
  tableClass?: string;
  tableId?: string;
  rounded?: boolean;
  searchBG?: string;
  thColor?: string;
}

const Table: React.FC<TableProps> = ({
  rows,
  columns,
  hideColumns = false,
  pagination = false,
  search = false,
  tableSize = "w-full",
  tableClass,
  tableId = "myTable",
  rounded = false,
  searchBG = "bg-gray-200",
  thColor = "blue",
}) => {
  const startPage = 1;
  tableId = tableId === "myTable" ? FC_makeID() : tableId;
  let table: HTMLElement, tr: HTMLCollectionOf<HTMLTableRowElement>;
  const [totalItems, setTotalItems] = useState<number>(0);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [pages, setPages] = useState<number[]>();
  const [pageSize, setPageSize] = useState<number>(10);
  const [startIndex, setStartIndex] = useState<number>(0);
  const [endIndex, setEndIndex] = useState<number>(0);

  useEffect(() => {
    pagination && tablePagination();
  });

  const filterTable = (
    data: string,
    filterType?: "default" | "field"
  ): void => {
    var filter, tr, td, rows: number, cols: number, txtValue;
    if (filterType === "field" && data === "all") data = "";
    filter = data.toLowerCase();
    table = document.getElementById(tableId)!;
    tr = table.getElementsByTagName("tr");
    for (rows = 0; rows < tr.length; rows++) {
      for (cols = 0; cols < columns.length; cols++) {
        td = tr[rows].getElementsByTagName("td")[cols];
        if (td) {
          txtValue = td.textContent || td.innerText;
          if (txtValue.toLowerCase().indexOf(filter) > -1) {
            tr[rows].style.display = "";
            break;
          } else {
            tr[rows].style.display = "none";
          }
        }
      }
    }
  };

  const tablePagination = (): void => {
    table = document.getElementById(tableId)!;
    tr = table.getElementsByTagName("tr");
    setTotalItems(tr.length);
    setTotalPages(Math.ceil(totalItems / pageSize));

    // calculate start and end item indexes on the current page
    setStartIndex((currentPage - 1) * pageSize);
    setEndIndex(Math.min(startIndex + pageSize - 1, totalItems - 1));

    /**
     * TODO!
     * There is a problem with this condition
     */
    let _pages = Array.from(Array(totalPages + 1 - startPage).keys()).map(
      (i) => startPage + i
    );
    if (!pages || _pages.length !== pages.length) {
      setPages(_pages);
    }

    for (let i: number = 0; i < tr.length; i++) {
      if (i >= startIndex && i <= endIndex) {
        tr[i].style.display = "";
      } else {
        tr[i].style.display = "none";
      }
    }
  };

  return (
    <div className={`${tableSize}`}>
      {/* search box  */}
      {search && (
        <div className={`${searchBG} px-3 py-2 my-2 max-w-screen-2xl mx-auto`}>
          {search && (
            <div className="bg-white rounded flex items-center w-full p-3 shadow-sm border border-gray-200">
              <MdSearch className="h-5 w-5 fill-current text-gray-400" />
              <input
                type="search"
                placeholder="search"
                onChange={(e) => filterTable(e.target.value)}
                className="w-full pl-4 text-sm outline-none focus:outline-none bg-transparent"
              />

              {pagination && (
                <div className="select">
                  <select
                    className="text-base outline-none focus:outline-none bg-transparent"
                    onChange={(e) => setPageSize(Number(e.target.value))}
                  >
                    <option value="10">10</option>
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                  </select>
                </div>
              )}
            </div>
          )}
        </div>
      )}
      <table
        className={
          tableClass
            ? tableClass
            : `w-full shadow ${
                rounded && "sm:rounded-lg"
              } table-auto order-table max-w-full`
        }
        id={tableId}
      >
        {!hideColumns && (
          <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
            <tr
              className={`text-left tracking-wide bg-${thColor}-100 border-b-2 border-${thColor}-500 text-${thColor}-600 overflow-hidden`}
            >
              {columns.map((column, index) => (
                <th
                  key={index}
                  className={`${column.padding} text-${column.alignText}`}
                >
                  {column.columnTitle}
                </th>
              ))}
            </tr>
          </thead>
        )}
        <tbody id="table-data" className="bg-white text-black text-base">
          {rows()}
        </tbody>
      </table>
      {pagination && (
        <div className="sm:flex-1 sm:flex sm:items-center sm:justify-between mt-4 work-sans">
          <div>
            <span className="text-xs xs:text-sm text-gray-900">
              Showing 1 to {pageSize} of {totalItems - 1} Entries
            </span>
          </div>
          <div>
            <nav className="inline-flex shadow-sm">
              <div>
                <button
                  type="button"
                  className={`inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 text-sm font-medium text-gray-500 ${
                    currentPage !== 1 && "bg-white"
                  }`}
                  aria-label="Previous"
                  disabled={currentPage === 1}
                  onClick={() => setCurrentPage(currentPage - 1)}
                >
                  <MdNavigateBefore className="h-5 w-5" />
                </button>
              </div>
              <div>
                {pages &&
                  pages.map((page, pageIndex) => (
                    <button
                      key={pageIndex}
                      className={`-ml-px inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium text-gray-700 ${
                        page !== currentPage && "bg-white"
                      }`}
                      disabled={page === currentPage}
                      onClick={() => setCurrentPage(page)}
                    >
                      {page}
                    </button>
                  ))}
              </div>
              <div>
                <button
                  type="button"
                  className={`inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 text-sm leading-5 font-medium text-gray-500 ${
                    currentPage !== totalPages && "bg-white"
                  }`}
                  aria-label="Next"
                  disabled={currentPage === totalPages}
                  onClick={() => setCurrentPage(currentPage + 1)}
                >
                  <MdNavigateNext className="h-5 w-5" />
                </button>
              </div>
            </nav>
          </div>
        </div>
      )}
    </div>
  );
};

export default Table;
