import React, { useState } from "react";
import { RiFolderSettingsFill } from "react-icons/ri";
import ApplicationSession from "./ApplicationSessions";
import SystemSettings from "./SystemSettings";
enum SettingsType {
  systemSettings = "systemSettings",
  applicationSession = "applicationSession",
}

const Index = () => {
  const settings: { settingType: SettingsType; caption: string }[] = [
    {
      settingType: SettingsType.systemSettings,
      caption: "System Settings",
    },
    {
      settingType: SettingsType.applicationSession,
      caption: "Appication Session",
    },
  ];
  const [selectedSetting, setSelectedSetting] = useState<SettingsType | null>(
    SettingsType.systemSettings
  );
  return (
    <div className="">
      <div className="mt-3 rounded flex flex-row justify-between items-center">
        <div className="flex flex-row items-center gap-3">
          <RiFolderSettingsFill className="text-6xl text-blue-600" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-2xl font-bold">
              System settings
            </span>
            {/* <span className="text-gray-600 text-sm">
              Customization of question bank predefined settings including
              courses, chapters, question types, etc
            </span> */}
          </div>
        </div>
        {/* <div className="flex flex-row">
          <Link
            to="/method-add-question"
            className={`btn bg-blue-100 hover:bg-white text-blue-700 border-2 border-white`}
          >
            Add questions
          </Link>
          <div className="btn bg-black bg-opacity-30 text-white border-2 border-white">
            Change course
          </div>
        </div> */}
      </div>
      <div className="mt-3 flex-col">
        <div className="flex flex-col">
          <div className="bg-white flex rounded">
            <div className="bg-white w-full border border-white rounded">
              <div className="flex flex-row items-center">
                {settings.map((item, i) => (
                  <div
                    key={i}
                    onClick={() => setSelectedSetting(item.settingType)}
                    className={`animate__animated ${
                      selectedSetting === item.settingType
                        ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn"
                        : "bg-white border-white text-gray-600"
                    } hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mx-1`}
                  >
                    {item.caption}
                  </div>
                ))}
              </div>
              <div className="=rounded mx-1 mb-1">
                {selectedSetting === SettingsType.systemSettings && (
                  <SystemSettings />
                )}
                {selectedSetting === SettingsType.applicationSession && (
                  <ApplicationSession />
                )}
                {/* {selectedSetting === SettingsType.schoolLevels && (
                  <SchoolLevels />
                )}
                {selectedSetting === SettingsType.courses && (
                  <Courses />
                )} */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Index;
