import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { SystemUserRoleInterface } from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface FetchRolesAction {
  type: ActionTypes.FETCH_ACCESS_ROLES;
  payload: SystemUserRoleInterface[];
}

export interface CreateRoleAction {
  type: ActionTypes.CREATE_ACCESS_ROLE;
  payload: SystemUserRoleInterface;
}

export interface UpdateRoleAction {
  type: ActionTypes.UPDATE_ACCESS_ROLE;
  payload: SystemUserRoleInterface;
}

export interface DeleteRoleAction {
  type: ActionTypes.DELETE_ACCESS_ROLE;
  payload: SystemUserRoleInterface[];
}

export type AccessRoleActionType =
  | FetchRolesAction
  | CreateRoleAction
  | UpdateRoleAction
  | DeleteRoleAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const fetchAccessRoles = () => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<SystemUserRoleInterface[]>(
        `${API.AUTH}/role`
      );
      await dispatch<FetchRolesAction>({
        type: ActionTypes.FETCH_ACCESS_ROLES,
        payload: res.data,
        // payload: response.data,
      });
    } catch (error) {
      console.log({ code_error: error });
    }
  };
};

export const createAccessRoles = (data: SystemUserRoleInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<CreateRoleAction>({
      type: ActionTypes.CREATE_ACCESS_ROLE,
      payload: data,
    });
  };
};

export const updateAccessRoles = (data: SystemUserRoleInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<UpdateRoleAction>({
      type: ActionTypes.UPDATE_ACCESS_ROLE,
      payload: data,
    });
  };
};

export const deleteAccessRoles = (data: SystemUserRoleInterface[]) => {
  return async (dispatch: Dispatch) => {
    dispatch<DeleteRoleAction>({
      type: ActionTypes.DELETE_ACCESS_ROLE,
      payload: data,
    });
  };
};
