import React, { Component, Fragment } from 'react'
import { MdHistory, MdRadioButtonChecked, MdRadioButtonUnchecked, MdSearch } from 'react-icons/md'
import { connect } from "react-redux";
import {
  Auth,
  loadApplicationPeriods,
  loadAllPositions,
  AdminLoadApplicationList,
  setSelectedPosition,
  setSelectedPeriod,
  applicationStoreInterface,
  positionsStoreInterface,
  applicationPeriodsStoreInterface,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  setSelectedEducationCategory,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { MainItem, SideNavigation } from '../../../components/Navigation/SideNavigation';
import Loading from '../../../components/Loading/Loading';
import { AdminApplicationsList, ApplicationPeriodGet, ApplicationStates, ApplicationStatesDescription } from '../../../interfaces/application';
import { applicationsListStoreInterface } from '../../../actions/ApplicationsList';
import { PositionAPI } from '../../../interfaces/index';
import { IoFilter } from 'react-icons/io5';
import Modal, { ModalSize, Themes } from '../../../components/Modal/Modal';
import { IoIosBriefcase } from 'react-icons/io';
import { ImFilesEmpty } from 'react-icons/im';
import { BiExport, BiFilterAlt } from 'react-icons/bi';
import { FiEdit } from 'react-icons/fi';
import ApplicationDetails from './ApplicationDetails';
import LoadingV2, { LoadingType } from '../../../components/Loading/LoadingV2';
import searchData from "../../../utils/search";
import { education_category } from "../../../interfaces/index";
import { Link } from 'react-router-dom';


interface loadAllApplicationsProps {
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
  AdminLoadApplicationList: (position_id: string, session_id: string, education_category: education_category | null, callback: (loading: boolean) => void) => void;
  setSelectedPosition: (position: PositionAPI) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  periods: applicationPeriodsStoreInterface;
  applications_list: applicationsListStoreInterface;
  application: applicationStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory: (education_category: education_category) => void;
}

interface loadAllApplicationsState {
  loading: boolean;
  selectedApplicationStatus: ApplicationStates | null;
  selectPosition: boolean;
  selectPeriod: boolean;
  applicationDetailsModal: {
    selected_application: AdminApplicationsList,
    status: boolean
  } | null;
  searchInput: string | "",
  searchPositionInput: string | "",
}

class _Applications extends Component<loadAllApplicationsProps, loadAllApplicationsState> {
  constructor(props: loadAllApplicationsProps) {
		super(props);

		this.state = {
      loading: false,
      selectedApplicationStatus: null,
      selectPosition: false,
      selectPeriod: false,
      applicationDetailsModal: null,
      searchInput: "",
      searchPositionInput: "",
		};
	}

  setLoadingState = (state: boolean) => {
    this.setState({ loading: state });
  }

  loadAllApplications = (position: PositionAPI | null, period: ApplicationPeriodGet | null, education_category: education_category | null, callback: (loading: boolean) => void) => {
    if (position !== null && period !== null) {
      this.props.AdminLoadApplicationList(position.position_id, period.application_session_id, education_category, callback);
    }
  }

  AdminApplicationStatuses = (application: AdminApplicationsList[]): { title: string, count: number, status: ApplicationStates }[] => {
    let output: { title: string, count: number, status: ApplicationStates }[] = [];
    for (let i in ApplicationStatesDescription) {
      output.push({
        title: ApplicationStatesDescription[i].caption,
        count: application.filter(itm => itm.application_status === ApplicationStatesDescription[i].key).length,
        status: ApplicationStatesDescription[i].key
      })
    }
    return output;
  }

  filteredApplications = (applications: AdminApplicationsList[] | null, Status: ApplicationStates | null, education_category: education_category | null) => {
    let appl:AdminApplicationsList[] | null = applications;
    if (education_category !== null && applications !== null) {
      appl = applications.filter(itm => itm.position.education_institute_id.education_institute_id === education_category.education_institute_id);
    }
    if (Status === null) {
      return appl;
    } else {
      if (appl === null) {
        return appl;
      } else {
        return appl.filter(item => item.application_status === Status);
      }
    }
  }

  componentDidMount = () => {
    if (this.props.schoolLevels.education_categories === null || this.props.schoolLevels.education_categories.length === 0) {
      this.props.loadEducationCategories(this.setLoadingState);
    }
    if (this.props.periods.applicationPeriods === null) {
      this.props.loadApplicationPeriods(this.setLoadingState);
    }
    if (this.props.periods.selected_period === null) {
      this.setState({ selectPeriod: true });
    }
    if (this.props.applications_list.positions_list === null) {
      this.props.loadAllPositions(this.setLoadingState);
    }
    if (this.props.applications_list.selected_position === null) {
      this.setState({ selectPosition: true });
    }
    this.loadAllApplications(this.props.applications_list.selected_position, this.props.periods.selected_period, this.props.schoolLevels.selectedEducationCategory, this.setLoadingState);
  }
  render() {
    return (
      <Fragment>
        <SideNavigation>
          <div className="flex flex-col mb-4">
            <span className="text-sm text-gray-600 mb-2">Education categories:</span>
            {this.props.schoolLevels.education_categories === null ? <Loading /> :
            this.props.schoolLevels.education_categories.length === 0 ? <span>No result found</span> :
            this.props.schoolLevels.education_categories.map((item, i) => (
              <div 
              onClick={() => {this.props.setSelectedEducationCategory(item); this.loadAllApplications(this.props.applications_list.selected_position, this.props.periods.selected_period, this.props.schoolLevels.selectedEducationCategory, this.setLoadingState)}}
              key={i + 1}className={`border rounded px-2 py-2 mb-2 cursor-pointer ${this.props.schoolLevels.selectedEducationCategory !== null && this.props.schoolLevels.selectedEducationCategory.education_institute_id === item.education_institute_id ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500" : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"} font-bold flex flex-row items-center gap-2`}>
                {this.props.schoolLevels.selectedEducationCategory !== null && this.props.schoolLevels.selectedEducationCategory.education_institute_id === item.education_institute_id ?
                <MdRadioButtonChecked /> :
                <MdRadioButtonUnchecked />}
                <span>{item.education_category_title}</span>
              </div>
            ))}
          </div>
          <div className="">
            <div className="text-gray-600 text-sm mb-2">Selected period:</div>
            <div
            onClick={() => this.setState({ selectPeriod: true })}
             className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline  text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn">
              {this.props.periods.selected_period !== null ? `${this.props.periods.selected_period.application_session_name} - ${this.props.periods.selected_period.period}` : "Filter by period"} 
              <FiEdit className="text-3xl text-blue-600" />
            </div>
          </div>
          {/* <Search data={[1, 2, 3]} /> */}
          <div className="pt-1">
            <div className="text-sm text-gray-700 pb-2 pt-2">Application statuses:</div>
            {this.props.applications_list.applicationsList === null ? <Loading /> : this.AdminApplicationStatuses(this.props.applications_list.applicationsList!).map((itm: { title: string; count: number, status: ApplicationStates }, i: number) => (
              // itm.count > 0 &&
              <Fragment key={itm.title}>
                <div onClick={() => this.setState({ selectedApplicationStatus: itm.status })}>
                  <MainItem title={itm.title} count={itm.count} active={this.state.selectedApplicationStatus === itm.status ? true : false} />
                </div>
              </Fragment>
            ))}
          </div>
        </SideNavigation>
        <div>
          <div className="grid md:grid-cols-12">
              <div className="col-span-6">
                <div className="mt-4 mx-4 flex flex-col gap-1">
                  <span className="text-3xl font-bold text-gray-700">Report of applications</span>
                  <div
                  onClick={() => this.setState({ selectPosition: true})}
                  className="flex flex-row gap-2">
                    <span className="text-gray-500">Selected position: </span>
                    <span className="text-blue-600 font-bold underline cursor-pointer">{this.props.applications_list.selected_position !== null ? this.props.applications_list.selected_position.position_title : "No position selected, Click to select"}</span>
                  </div>
                </div>
              </div>
              <div className="col-span-6">
                <div className="float-right mt-2">
                  <div className="flex flex-row items-center justify-end font-bold mb-2">
                    <BiExport className="text-2xl text-gray-500" />
                    <span className="text-gray-500">Export as</span>
                    <div className="px-4 py-1 ml-2 rounded bg-white border hover:text-blue-600 hover:bg-blue-50 cursor-pointer border-gray-200">Excel</div>
                    <div className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 hover:bg-blue-50 cursor-pointer border-gray-200">PDF</div>
                  </div>
                  <div onClick={() => this.setState({ selectPosition: true})} className="btn bg-blue-50 border border-blue-300 text-blue-600 cursor-pointer hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm ">
                    <BiFilterAlt className="text-xl" />
                    Filter by position
                  </div>
                </div>
              </div>
          </div>
          <div className="bg-white my-4">
            <table className="w-full shadow">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td className="px-2 py-2">Applicant names</td>
                  <td className="px-2 py-2">Gender</td>
                  <td className="px-2 py-2">Phone</td>
                  <td className="px-2 py-2">Position</td>
                  <td className="px-2 py-2">Date</td>
                  <td className="px-2 py-2">Status</td>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td colSpan={7} className="px-2 py-2">
                    <input onChange={(e) => this.setState({ searchInput: e.target.value })} type="text" value={this.state.searchInput} placeholder="Search..." className="w-full bg-gray-100 py-2 px-3 rounded" />
                  </td>
                </tr>
              {this.state.loading === true ?
              <tr>
                <td colSpan={7} className="px-2 py-2">
                  <Loading />
                </td>
              </tr> :
              this.filteredApplications(this.props.applications_list.applicationsList, this.state.selectedApplicationStatus, this.props.schoolLevels.selectedEducationCategory) === null ? 
              <tr>
                <td colSpan={7} className="px-2 py-2">
                  <Loading />
                </td>
              </tr> :
              this.filteredApplications(this.props.applications_list.applicationsList, this.state.selectedApplicationStatus, this.props.schoolLevels.selectedEducationCategory)?.length === 0 ? 
              <tr>
                <td colSpan={7} className="px-2 py-2">
                  <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                    <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                    <span className="text-sm font-bold">No result found! Please try to search again</span>
                    <div className="w-full mt-2 flex flex-row items-center justify-center">
                      <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                        <li onClick={() => this.setState({ selectPeriod: true})} className="underline text-gray-500 cursor-pointer">Filter by period</li>
                        <li className="text-gray-400 italic">or</li>
                        <li onClick={() => this.setState({ selectPosition: true})} className="underline text-blue-600 cursor-pointer">Filter by position</li>
                      </ul>
                    </div>
                  </div>
                </td>
              </tr> : 
              searchData(this.filteredApplications(this.props.applications_list.applicationsList, this.state.selectedApplicationStatus, this.props.schoolLevels.selectedEducationCategory)!, this.state.searchInput).map((item, i) => (
                <tr onClick={() => this.setState({ applicationDetailsModal: { selected_application: item, status: true } })} key={i + 1} className={`${i % 2 !== 0 ? "bg-gray-100" : "bg-white"} text-gray-600 hover:bg-blue-100 hover:text-blue-700 cursor-pointer border-b border-gray-300 hover:border-blue-500 text-sm`}>
                  <td className="px-2 py-2">{i + 1}</td>
                  <td className="px-2 py-2">{item.fname} {item.lname}</td>
                  <td className="px-2 py-2">{item.sex}</td>
                  <td className="px-2 py-2">{item.phone_number}</td>
                  <td className="px-2 py-2">{item.position.position_title}</td>
                  <td className="px-2 py-2">{item.createdAt}</td>
                  <td className="px-2 py-2 font-bold text-xs text-gray-500">{ApplicationStatesDescription.find(itm => itm.key === item.application_status)?.caption}</td>
                </tr>
              ))}
              </tbody>
            </table>
          </div>
        </div>
        {(this.state.selectPosition === true || this.state.selectPeriod === true) &&
          <Modal
            title={<div className="flex flex-row gap-4 items-center">
              {this.state.selectPeriod === true ? 
              <MdHistory className="text-5xl text-blue-600" /> : 
              <IoFilter className="text-5xl text-blue-600" />}
            <div className="flex flex-col">
              <span className="font-bold text-xl text-blue-600">{this.state.selectPeriod === true ? "Choose a period" : "Filter by a position"}</span>
            </div>
            </div>}
            backDrop={true}
            theme={Themes.default}
            close={() => this.props.applications_list.selected_position !== null && this.props.periods.selected_period !== null && this.setState({ selectPosition: false, selectPeriod: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{title: true,}} >
              <div className="px-2 pb-2">
                {this.state.selectPeriod === true ?
                this.props.periods.applicationPeriods === null ? <Loading /> : 
                this.props.periods.applicationPeriods.length === 0 ? 
                <div className="flex flex-col text-gray-400 items-center justify-center">
                  <MdSearch className="text-6xl" />
                  <div className="flex flex-col items-center justify-center">
                    <span>No application periods found!</span>
                    <Link to="/" className="btn bg-yellow-100 text-yellow-700 my-4 hover:bg-yellow-200">Go back</Link>
                  </div>
                </div> :
                this.props.periods.applicationPeriods.map((item, i) => (
                  <div onClick={() => {this.props.setSelectedPeriod(item); this.loadAllApplications(this.props.applications_list.selected_position, this.props.periods.selected_period, this.props.schoolLevels.selectedEducationCategory, this.setLoadingState); this.setState({ selectPeriod: false })}} key={i + 1} className="grid grid-cols-12 bg-blue-50 border border-blue-200 rounded px-2 py-2 cursor-pointer hover:bg-blue-100 hover:border-blue-500 mb-3">
                    <div className="col-span-5">
                      <div className="text-blue-500 text-sm italic">Period</div>
                      <div className="font-bold">{item.application_session_name}</div>
                      <div className="my-4"></div>
                      <div className="text-blue-500 text-sm italic">Status</div>
                      <div className="font-bold">{item.is_active === true ? "Active" : "Expired"}</div>
                    </div>
                    <div className="col-span-5">
                      <div className="text-blue-500 text-sm italic">Start date</div>
                      <div className="font-bold">{item.application_start_date}</div>
                      <div className="my-4"></div>
                      <div className="text-blue-500 text-sm italic">Ending date</div>
                      <div className="font-bold">{item.application_end_date}</div>
                    </div>
                    <div className="col-span-2 flex items-center justify-end">
                      <div className="btn bg-blue-600 text-white mr-2">Choose</div>
                    </div>
                  </div>
                )): this.state.selectPosition === true && 
                this.props.applications_list.positions_list === null ? <LoadingV2 type={LoadingType.dark} /> :
                this.props.applications_list.positions_list?.length === 0 ? 
                <div className="flex flex-col text-gray-400 items-center justify-center">
                  <MdSearch className="text-6xl" />
                  <div className="flex flex-col items-center justify-center">
                    <span>No positions found! Please go back!</span>
                    <Link to="/" className="btn bg-yellow-100 text-yellow-700 my-4 hover:bg-yellow-200">Go back</Link>
                  </div>
                </div> :
                <>
                <div className="flex flex-col">
                  <span className="text-gray-600 text-xs font-bold">Filter by position name</span>
                  <input onChange={(e: React.ChangeEvent<HTMLInputElement>) => this.setState({ searchPositionInput: e.target.value })} type="text" className="mb-4 bg-gray-100 px-3 py-2 rounded" placeholder="Keyword..." />
                </div>
                {searchData(this.props.applications_list.positions_list!, this.state.searchPositionInput).map((item, key) => (
                  <div key={key + 1} onClick={() => { this.props.setSelectedPosition(item); this.loadAllApplications(this.props.applications_list.selected_position, this.props.periods.selected_period, this.props.schoolLevels.selectedEducationCategory, this.setLoadingState); this.setState({ selectPosition: false })}} className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2">
                    <div className="grid lg:grid-cols-12">
                      <div className="col-span-12 lg:col-span-10">
                        <div className="flex flex-row items-center gap-3">
                          <div><IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" /></div>
                          <div className="flex flex-col">
                            <span className="text-xs lg:text-sm font-bold text-gray-700">{item.position_title}</span>
                            <span className="text-xs text-gray-500 italic">School level: <span className="text-gray-800 font-light" style={{fontStyle: 'normal'}}>{item.school_level_id.school_level_name}</span></span>
                            <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">{item.education_institute_id.education_category_title}</span>
                          </div>
                        </div>
                      </div>
                      <div className="col-span-12 lg:col-span-2 hidden lg:block">
                        <div className="h-full flex items-center justify-end">
                          <div className="btn bg-blue-600 text-white float-right">Choose</div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}</>}
                </div>
          </Modal>}
          {this.state.applicationDetailsModal !== null && this.state.applicationDetailsModal.status === true &&
          <Modal
            title={<div className="flex flex-row gap-4 items-center">
              {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
              <div className="flex flex-col">
                <span className="font-bold text-2xl text-gray-500">{this.state.applicationDetailsModal.selected_application.fname} {this.state.applicationDetailsModal.selected_application.lname}</span>
                <span className="text-sm text-blue-600 font-normal">Position: {this.state.applicationDetailsModal.selected_application.position.position_title}</span>
              </div>
            </div>}
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ applicationDetailsModal: null })}
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{title: true,}} >
              <ApplicationDetails 
                selectedApplication={this.state.applicationDetailsModal.selected_application}
              />
          </Modal>
          }
      </Fragment>
    )
  }
}


const mapStateToProps = ({
  auth,
  applications_list,
  application,
  positions,
  periods,
  schoolLevels,
}: StoreState): { auth: Auth; applications_list: applicationsListStoreInterface; application: applicationStoreInterface, positions: positionsStoreInterface, periods: applicationPeriodsStoreInterface, schoolLevels: schoolLevelsStoreInterface; } => {
  return { auth: auth, applications_list, application, positions: positions, periods, schoolLevels: schoolLevels };
};

const Applications = connect(mapStateToProps, {
  loadApplicationPeriods,
  AdminLoadApplicationList,
  loadAllPositions,
  setSelectedPosition,
  setSelectedPeriod,
  loadEducationCategories,
  setSelectedEducationCategory,
})(_Applications);

export default Applications;
