import React, { Component } from "react";
import Footer from "./Footer/Footer";
import Profile from "./Profile/Profile";
import QuestionDetails from "./QuestionDetails/QuestionDetails";
import DoExamTitle from "./DoExamTitle/DoExamTitle";
import * as Interfaces from "../../interfaces/index";
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  ActionTypes,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  ChangeExamStatus,
} from "../../actions";
import { StoreState } from "../../reducers";
import { question_answer } from "../../interfaces/index";
import DoExamResults from "./DoEamResults/DoExamResults";
import { ExamInterface, examStatus } from "../../interfaces/exam";
import { exam_question } from "../../interfaces/exam_question";
import { Redirect } from "react-router";
import CancelErrorMessage from "./Cancel/CancelErrorMessage";
import { ProctoringCandidateOnline } from "../ProctoringCandidateOnline/ProctoringCandidateOnline";
import QuestionsList from "./QuestionsList/QuestionsList";

interface DoExamProps {
  currentPosition: Interfaces.PositionAPI | null;
  // setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  // setApplicationStep: (step: ApplicationStepsTypes) => void;
  // getUserAnswer: Function;
  // addAnswer: (questionAnswer: newAnswer) => addUsersAnswers;
  // SubmitAnswers: Function;
  doExam: examDataInterface;
  auth: Auth;
  loadExam: (user_id: string, exam: ExamInterface) => void;
  addExamAnswer: (questionAnswer: newAnswer) => void;
  deleteExamAnswers: () => void;
  SubmitExamAnswers: (user_id: string) => void;
  ChangeExamStatus: (user_id: string, status: examStatus) => void;
}

interface DoExamState {
  windowStatus: {
    profile: boolean;
    questionsList: boolean;
  };
  selectedQuestion: exam_question | null;
  testAnswers: newAnswer[] | [];
  completedTest: boolean;
  examStatus: boolean;
}

export interface newAnswer {
  question_id: string | null;
  user_answers: question_answer | null;
}

class _DoEam extends Component<DoExamProps, DoExamState> {
  constructor(props: DoExamProps) {
    super(props);

    this.state = {
      windowStatus: {
        profile: false,
        questionsList: false,
      },
      selectedQuestion:
        this.props.doExam.exam_answers !== null
          ? this.props.doExam.exam_answers?.exam_questions[0]
          : null,
      testAnswers: [],
      completedTest: false,
      examStatus: false,
    };
  }
  setProfileWindowStatus = (status: boolean) => {
    this.setState({
      windowStatus: {
        profile: status,
        questionsList: this.state.windowStatus.questionsList,
      },
    });
  };
  setQuestionsListWindowStatus = (status: boolean) => {
    this.setState({
      windowStatus: {
        profile: this.state.windowStatus.profile,
        questionsList: status,
      },
    });
  };

  setSelectedQuestion = (question: exam_question) => {
    this.setState({ selectedQuestion: question });
  };

  answerQuestionFn = (question_id: string, answer: question_answer) => {
    console.log("Input: ", {
      question_id: question_id,
      user_answers: answer,
    });
    let current: newAnswer[] =
      this.state.testAnswers.length > 0
        ? this.state.testAnswers.filter(
            (itm) => itm.question_id !== question_id
          )
        : this.state.testAnswers;
    current.push({
      question_id: question_id,
      user_answers: answer,
    });
    this.setState({
      testAnswers: current,
    });
    for (let i in current) {
      this.props.addExamAnswer(current[i]);
    }

    console.log("Answers: ", this.state.testAnswers);
  };

  setCompletedTest = (state: boolean) => {
    this.setState({ completedTest: state });
  };

  initializeEmptyList = () => {
    // localStorage.removeItem(ActionTypes.ADD_ANSWER)
    let testResults = localStorage.getItem(ActionTypes.ADD_ANSWER);
    if (testResults !== null) {
      let newData = JSON.parse(testResults!).questionAnswers;
      if (newData) {
        if (newData.length > 0) {
          for (let i in newData) {
            this.props.addExamAnswer(newData[i]);
          }
          if (
            newData.length ===
            this.props.doExam.exam_answers?.exam_questions.length
          ) {
            this.setState({ completedTest: true });
            this.props.SubmitExamAnswers(this.props.auth.user.n_id);
          }
        }
      }
    }
  };

  componentDidMount = () => {
    this.initializeEmptyList();
    if (
      this.props.doExam.exam_answers?.exam_questions.length === 0 &&
      this.props.doExam.exam_answers.exam_questions_answers &&
      this.props.doExam.exam_answers.exam_questions_answers?.questionAnswers
        .length > 0
    ) {
      this.setState({
        testAnswers:
          this.props.doExam.exam_answers.exam_questions_answers
            ?.questionAnswers,
      });
    }
    // Function to change exam status
    // this.props.ChangeExamStatus(this.props.auth.user.user_id, examStatus.Editing);
  };

  render() {
    if (this.props.doExam.exam_answers) {
      if (!this.props.doExam.exam_answers.exam_id) {
        return <Redirect to="/dashboard" />;
      } else if (this.props.doExam.exam_answers.status === examStatus.Done) {
        if (
          this.state.completedTest === false ||
          this.props.doExam.exam_answers?.exam_questions_answers
            ?.questionAnswers === null
        ) {
          return <Redirect to="/dashboard" />;
        }
      } else if (
        this.props.doExam.exam_answers.status === examStatus.Canceled
      ) {
        return <CancelErrorMessage />;
      }
      console.log("Exam status", this.props.doExam.exam_answers.status);
    } else {
      return <Redirect to="/dashboard" />;
    }

    return (
      <ProctoringCandidateOnline
        startExam={() => {
          // this.props.ChangeExamStatus(this.props.auth.user.user_id, examStatus.Editing)
        }}
        restartExam={() => {
          // this.initializeEmptyList()
        }}
        stopExam={() => {
          // this.props.ChangeExamStatus(this.props.auth.user.user_id, examStatus.Canceled)
        }}
      >
        {this.state.completedTest === true &&
        this.props.doExam.exam_answers?.exam_questions_answers
          ?.questionAnswers !== null ? (
          <div className="max-w-7xl mx-auto px-2 md:px-4 lg:px-24">
            <DoExamResults
              page="doExam"
              answeredQuestions={
                this.props.doExam.exam_answers?.exam_questions_answers
                  ?.questionAnswers
              }
              testAnswers={
                this.props.doExam.exam_answers?.exam_questions_answers
                  ?.questionAnswers.length === undefined
                  ? 0
                  : this.props.doExam.exam_answers?.exam_questions_answers
                      ?.questionAnswers.length
              }
            />
          </div>
        ) : (
          <div className="fixed top-0 w-full h-screen bg-blue-900 overflow-y-hidden">
            <div className="grid md:grid-cols-12 h-full">
              {this.state.windowStatus.profile === true && (
                <div
                  className={`col-span-6 lg:col-span-2 fixed left-0 md:relative`}
                >
                  <Profile
                    setProfileWindowStatus={this.setProfileWindowStatus}
                  />
                </div>
              )}
              <div
                className={`col-span-${
                  this.state.windowStatus.profile === false ? "12" : "10"
                } bg-gray-800 shadow-lg h-full`}
              >
                <div className="flex flex-row h-screen bg-gray-800">
                  <div className="h-full">
                    {/* Questions list */}
                    <div className={`h-full`}>
                      <QuestionsList
                        answeredQuestions={
                          this.props.doExam.exam_answers?.exam_questions_answers
                            ?.questionAnswers
                        }
                        testAnswers={
                          this.props.doExam.exam_answers?.exam_questions_answers
                            ?.questionAnswers.length === undefined
                            ? 0
                            : this.props.doExam.exam_answers
                                ?.exam_questions_answers?.questionAnswers.length
                        }
                        windowStatus={this.state.windowStatus}
                        setProfileWindowStatus={this.setProfileWindowStatus}
                        setQuestionsListWindowStatus={
                          this.setQuestionsListWindowStatus
                        }
                        selectedQuestion={this.state.selectedQuestion}
                        setSelectedQuestion={this.setSelectedQuestion}
                      />
                    </div>
                  </div>
                  <div className="w-full h-full">
                    {/* Do exam test title */}
                    <DoExamTitle
                      setCompletedTest={this.setCompletedTest}
                      answeredQuestions={
                        this.props.doExam.exam_answers?.exam_questions_answers
                          ?.questionAnswers
                      }
                      testAnswers={
                        this.props.doExam.exam_answers?.exam_questions_answers
                          ?.questionAnswers.length === undefined
                          ? 0
                          : this.props.doExam.exam_answers
                              ?.exam_questions_answers?.questionAnswers.length
                      }
                      // currentPosition={this.props.currentPosition}
                      // setCurrentPosition={this.props.setCurrentPosition}
                      // setApplicationStep={this.props.setApplicationStep}
                      windowStatus={this.state.windowStatus}
                      setProfileWindowStatus={this.setProfileWindowStatus}
                      setQuestionsListWindowStatus={
                        this.setQuestionsListWindowStatus
                      }
                      setSelectedQuestion={this.setSelectedQuestion}
                    />
                    {/* Question details */}
                    <QuestionDetails
                      questionAnswers={
                        this.props.doExam.exam_answers?.exam_questions_answers
                          ?.questionAnswers
                      }
                      answerQuestionFn={this.answerQuestionFn}
                      // currentPosition={this.props.currentPosition}
                      // setCurrentPosition={this.props.setCurrentPosition}
                      // setApplicationStep={this.props.setApplicationStep}
                      selectedQuestion={this.state.selectedQuestion}
                      setSelectedQuestion={this.setSelectedQuestion}
                    />
                  </div>
                </div>
              </div>
            </div>
            <Footer
              selectedQuestion={this.state.selectedQuestion}
              setSelectedQuestion={this.setSelectedQuestion}
              // currentPosition={this.props.currentPosition}
              // setCurrentPosition={this.props.setCurrentPosition}
              // setApplicationStep={this.props.setApplicationStep}
              windowStatus={this.state.windowStatus}
              setProfileWindowStatus={this.setProfileWindowStatus}
              setQuestionsListWindowStatus={this.setQuestionsListWindowStatus}
            />
          </div>
        )}
      </ProctoringCandidateOnline>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
  doExam,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface } => {
  return { todos: todos, auth: auth, doExam: doExam };
};

const DoExam = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  ChangeExamStatus,
})(_DoEam);

export default DoExam;
