import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { course } from "../../src/interfaces/index";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface loadCoursesActions {
  type: ActionTypes.LOAD_COURSES,
  payload: course[]
}

export interface setSelectedCourseActions {
  type: ActionTypes.SELECTED_COURSE,
  payload: course | null
}

export interface courseStoreInterface {
  courses: course[] | null;
  selected_course: course | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadCourses = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<course[]>(`${API.APPLICATION}/course`);
      dispatch<loadCoursesActions>({
        type: ActionTypes.LOAD_COURSES,
        payload: res.data,
      });
      console.log("Courses: ", res.data);
    } catch (error) {
      console.log("Err: ", error);
    }
    callback(false);
  };
};

export const setSelectedCourse = (course: course | null): setSelectedCourseActions => {
  return {
    type: ActionTypes.SELECTED_COURSE,
    payload: course,
  };
}