import React, { useEffect, useState } from "react";
import { MdClose, MdErrorOutline } from "react-icons/md";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import Table from "../../components/Table/Table";
import { StoreState } from "../../reducers";
import {
  PositionInterface,
  PostSchoolInterface,
} from "../../shared/interfaces";
import { fetchPositions } from "../../actions/positionRequests";
import { fetchSchoolPosts, createSchoolPost } from "../../actions/schools";
import {
  choosenDistrict,
  choosenSchool,
  getPositionById,
} from "../../shared/dataTest";
const uuid = (): string => Math.random().toString();

interface AppProps {
  positions: PositionInterface[];
  schoolPosts: PostSchoolInterface[];
  fetchPositions: Function;
  fetchSchoolPosts: Function;
  createSchoolPost: Function;
}

const RequestTeacher: React.FC<AppProps> = ({
  positions,
  fetchPositions,
  schoolPosts,
  fetchSchoolPosts,
  createSchoolPost,
}) => {
  const [requestTeacher, setRequestTeacher] = useState<boolean>(false);
  const [filterByPosition, setFilterByPosition] = useState<string>("all");
  const [positionId, setPositionId] = useState<string>("");
  const [qualification, setQualifacation] = useState<string>("");
  const [requestedPosts, setRequestedPosts] = useState<number>(0);
  const [requestExplanation, setRequestExplanation] = useState<string>("");
  const [loadind, setLoadind] = useState<boolean>(false);
  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>();

  useEffect(() => {
    if (!positions.length) {
      fetchPositions();
    }
  }, [positions.length, fetchPositions]);

  useEffect(() => {
    if (!schoolPosts.length) {
      fetchSchoolPosts();
    }
  }, [schoolPosts.length, fetchSchoolPosts]);

  const tableData = (): JSX.Element[] => {
    let data = schoolPosts.filter(
      (schoolPost) => schoolPost.school_id === choosenSchool
    );

    data =
      filterByPosition !== "" && filterByPosition !== "all"
        ? data.filter(
            (schoolPost) => schoolPost.position_id === filterByPosition
          )
        : data;

    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={7}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((schoolPost, index) => (
      <tr
        key={index + 1}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="p-3 text-left font-medium">
          {getPositionById(positions, schoolPost.position_id)?.position_title}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {
            getPositionById(positions, schoolPost.position_id)?.requirements[0]
              .user_required_docs[0].title
          }
        </td>
        <td className="p-3 text-center text-base font-medium">
          {schoolPost.needed_posts}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {schoolPost.confirmed_posts ? schoolPost.confirmed_posts : "-"}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {schoolPost.occupied_posts ? schoolPost.occupied_posts : "-"}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {schoolPost.confirmed_posts && schoolPost.occupied_posts
            ? Number(schoolPost.confirmed_posts - schoolPost.occupied_posts)
            : "-"}
        </td>
        <td className="py- text-left">
          <div className="flex item-center justify-center">
            <Link
              className="
                btn
                text-blue-700
                ml-2
                border-blue-700
                md:border-2
                hover:bg-blue-700
                hover:text-white
                transition
                ease-out
                duration-500
                flex items-center
              "
              title="View User Profile"
              to={`school-post/${schoolPost.position_id}`}
            >
              Details
            </Link>
          </div>
        </td>
      </tr>
    ));
  };

  const submitRequest = (e: React.FormEvent<HTMLFormElement>): void => {
    e.preventDefault();
    setFeedBack({ type: "", message: "" });

    setLoadind(true);
    const dataForm: PostSchoolInterface = {
      post_at_school_id: uuid(),
      school_id: choosenSchool,
      position_id: positionId,
      district_id: choosenDistrict,
      needed_posts: requestedPosts,
      requested_by: "rtyr",
      request_date: new Date(),
      post_at_school_status: 0,
    };
    createSchoolPost(dataForm);
    setPositionId("");
    setQualifacation("");
    setRequestedPosts(0);
    setRequestExplanation("");
    setFeedBack({ type: "success", message: `Posts has been requested!` });
    setLoadind(false);
  };

  return (
    <div className="mx-32">
      <div className="bg-hero my-5 rounded p-4 flex justify-between items-center relative">
        <div className="flex flex-col">
          <span className="text-white text-4xl">User Management</span>
        </div>
      </div>
      <div className="grid md:grid-cols-5 gap-2 bg-white shadow-md p-2">
        <div className={`md:col-span-5 rounded`}>
          <div className="my-2 flex sm:flex-row flex-col justify-between">
            <div className="flex flex-col md:flex-row">
              <div>
                <select
                  className="border p-2 rounded focus:outline-none"
                  onChange={(e) => setFilterByPosition(e.target.value)}
                >
                  <option value="all">All Position</option>
                  {positions.map((position, index) => (
                    <option key={index + 1} value={position.position_id}>
                      {position.position_title}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            <div className="block relative">
              <div className="pt-6 md:pt-0 md:pl-6">
                <button
                  className="btn bg-primary text-white hover:bg-blue-600"
                  onClick={() => setRequestTeacher(true)}
                  disabled={requestTeacher}
                >
                  Request Teacher
                </button>
              </div>
            </div>
          </div>
          <Table
            columns={[
              { columnTitle: "POSITION", alignText: "left", padding: "p-3" },
              {
                columnTitle: "QUALIFICATION",
                alignText: "center",
                padding: "p-3",
              },
              { columnTitle: "REQUEST", alignText: "center", padding: "p-3" },
              { columnTitle: "APPROVED", alignText: "center", padding: "p-3" },
              { columnTitle: "OCCUPIED", alignText: "center", padding: "p-3" },
              { columnTitle: "VACCANTS", alignText: "center", padding: "p-3" },
              { columnTitle: "", alignText: "left", padding: "p-3" },
            ]}
            rows={tableData}
            pagination={true}
            search={true}
          />
        </div>
      </div>

      {/* Upload Modal */}
      {requestTeacher && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {}}
          displayClose={false}
          backDropClose={false}
          title={
            <div className="flex items-center space-x-5">
              <div className="h-14 w-14 bg-blue-200 rounded-full flex flex-shrink-0 justify-center items-center text-blue-500 text-2xl font-mono">
                T
              </div>
              <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                <p className="text-base text-gray-500 font-medium">
                  You are applying for
                </p>
                <h2 className="uppercase text-blue-600">Requesting Teachers</h2>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="max-w-md mx-auto mb-8">
            {feedBack && feedBack.type !== "" && (
              <div
                className={`alert-${feedBack.type} flex flex-row items-center relative`}
              >
                <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
                  <MdClose
                    className="h-5 w-5 text-gray-700 hover:font-bold"
                    title="Close"
                    onClick={() => setFeedBack({ type: "", message: "" })}
                  />
                </span>
                <MdErrorOutline className="h-10 w-10" />
                <div className="alert-content ml-4">
                  <div className="alert-title font-semibold text-lg">
                    {feedBack.type === "success" ? "Success" : "Error"}
                  </div>
                  <div className="alert-description text-sm">
                    {feedBack.message}
                  </div>
                </div>
              </div>
            )}

            <form onSubmit={submitRequest}>
              <div className="divide-y divide-gray-200">
                <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                  <div className="flex flex-col">
                    <label>Select Position</label>
                    <select
                      className="order p-2 rounded focus:outline-none"
                      // defaultValue={}
                      onChange={(e) => {
                        setPositionId(e.target.value);
                        setQualifacation(
                          getPositionById(positions, e.target.value)
                            .requirements[0].user_required_docs[0].title
                        );
                      }}
                      required
                    >
                      <option value="">Choose Position</option>
                      {positions.map((position, index) => (
                        <option key={index + 11} value={position.position_id}>
                          {position.position_title}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="flex flex-col">
                    <label>Qualification</label>
                    <input
                      type="text"
                      className="input input-border"
                      placeholder="Qualification"
                      value={qualification}
                      disabled
                    />
                  </div>
                  <div className="flex flex-col">
                    <label>Posts (number)</label>
                    <input
                      type="number"
                      className="input input-border"
                      value={requestedPosts}
                      onChange={(e) =>
                        setRequestedPosts(Number(e.target.value))
                      }
                      min={1}
                      placeholder="1"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label>Explanation</label>
                    <textarea
                      name="description"
                      className="input input-border"
                      id="description"
                      placeholder="Explanation"
                      value={requestExplanation}
                      onChange={(e) => setRequestExplanation(e.target.value)}
                      rows={2}
                      required
                    ></textarea>
                  </div>
                </div>
                <div className="pt-4 flex justify-between">
                  <button
                    className="btn bg-gray-400 text-white hover:bg-gray-600"
                    onClick={() => {
                      setPositionId("");
                      setQualifacation("");
                      setRequestedPosts(0);
                      setRequestExplanation("");
                      setFeedBack({ type: "", message: "" });
                      setRequestTeacher(false);
                    }}
                  >
                    Exit
                  </button>
                  <button
                    className="btn bg-primary text-white hover:bg-blue-600"
                    disabled={loadind}
                  >
                    {loadind ? "Loading..." : "Request"}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </Modal>
      )}
      {/* End of Upload Modal */}
    </div>
  );
};

const mapStateToProps = ({
  schools,
  positionRequests,
}: StoreState): {
  schoolPosts: PostSchoolInterface[];
  positions: PositionInterface[];
} => {
  return { schoolPosts: schools.schoolPosts, positions: positionRequests };
};

export default connect(mapStateToProps, {
  fetchPositions,
  fetchSchoolPosts,
  createSchoolPost,
})(RequestTeacher);
