interface AppProps {
  doc: CandidateDetailsInterface;
  docTitle: string;
  docSubTitle: string;
  docFieldTitle: string;
  responseMsg: string;
  loadingRequest: boolean;
  setDoc: React.Dispatch<
    React.SetStateAction<CandidateDetailsInterface | null>
  >;
  setDocTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocSubTitle: React.Dispatch<React.SetStateAction<string>>;
  setResponseMsg: React.Dispatch<React.SetStateAction<string>>;
  setResponse: React.Dispatch<React.SetStateAction<"approved" | "rejected">>;
  handleVerify: () => Promise<void>;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
  error: {
    element: string;
    msg: string;
  };
}
const FileView: React.FC<AppProps> = ({
  doc,
  docTitle,
  docSubTitle,
  docFieldTitle,
  responseMsg,
  loadingRequest,
  error,
  setDoc,
  setDocTitle,
  setDocSubTitle,
  setResponseMsg,
  setResponse,
  handleVerify,
  setError,
}) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [actionTaken, setActionTaken] = useState<"" | "approval" | "rejection">(
    ""
  );
  const [isImageValid, setIsImageValid] = useState<boolean | null>(null);
  useEffect(() => {
    if (isImageValid === null) {
      setLoading(true);
      checkIfImageExists(API.AUTH_FILES + doc.files, (result: boolean) =>
        setIsImageValid(result)
      );
    } else {
      setLoading(false);
    }
  }, [doc.files, isImageValid]);
  if (loading) {
    return <Loading />;
  }

  return (
    <div className="bg-gray-50 h-screen grid grid-cols-1 md:grid-cols-3">
      {/* User Access Select */}
      {/* image content */}
      {doc.files ? (
        <div className="col-span-2 max-h-full md:max-h-screen overflow-y-auto">
          {isImageValid ? (
            <img
              src={API.AUTH_FILES + doc.files}
              alt=""
              className="w-full animate__animated animate__zoomIn"
            />
          ) : (
            <div className="text-center text-red-400 py-8 animate__animated animate__zoomIn">
              <CgImage className="mx-auto w-40 h-40" />
              <span className="font-semibold text-4xl">Invalid Image</span>
            </div>
          )}
        </div>
      ) : (
        "No Files Provided"
      )}
      {/* end image content */}

      {/* footer */}
      <div>
        {doc.files &&
          (!actionTaken ? (
            <FooterInitial
              docTitle={docTitle}
              docSubTitle={docSubTitle}
              docFieldTitle={docFieldTitle}
              docStatus={doc.validation_status}
              setDoc={setDoc}
              setDocTitle={setDocTitle}
              setDocSubTitle={setDocSubTitle}
              setActionTaken={setActionTaken}
              setResponse={setResponse}
            />
          ) : (
            <FooterResponse
              docTitle={docTitle}
              docSubTitle={docSubTitle}
              docStatus={doc.validation_status}
              setActionTaken={setActionTaken}
              actionTaken={actionTaken}
              setResponseMsg={setResponseMsg}
              responseMsg={responseMsg}
              loadingRequest={loadingRequest}
              handleVerify={handleVerify}
              error={error}
              setError={setError}
            />
          ))}
      </div>
      {/* end footer */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default connect(mapStateToProps, {})(FileView);
