import axios from "axios";
import React, { useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentFieldsInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentFields,
  updateDocumentFields,
} from "../../actions/document";
import { MdClose, MdErrorOutline } from "react-icons/md";
import { errorToText } from "../../utils/errors";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PopupAlertData } from "../../components/Alert/Popup";

interface AppProps {
  thisCategory: DocumentCategoryInterface;
  subCategoryId: string;
  subCategoryTitle: string;
  docFieldsId: string;
  docFieldsTitle: string;
  docFieldsDescription: string;
  viewFields: "" | "SUB_CATEGORY" | "CATEGORY";
  actionType: string;
  feedBack:
    | {
        type: "success" | "danger" | "warning" | "";
        message: string;
      }
    | undefined;
  setDocFieldsId: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldsTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldsDescription: React.Dispatch<React.SetStateAction<string>>;
  setActionType: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
    >
  >;
  setFeedBack: React.Dispatch<
    React.SetStateAction<
      | {
          type: "success" | "danger" | "warning" | "";
          message: string;
        }
      | undefined
    >
  >;
  createDocumentFields: Function;
  updateDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const CreateDocumentField: React.FC<AppProps> = ({
  thisCategory,
  subCategoryId,
  subCategoryTitle,
  docFieldsId,
  docFieldsTitle,
  docFieldsDescription,
  viewFields,
  actionType,
  feedBack,
  setDocFieldsId,
  setDocFieldsTitle,
  setDocFieldsDescription,
  setActionType,
  setFeedBack,
  createDocumentFields,
  updateDocumentFields,
  PushPopupAlert,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFeedBack({ type: "", message: "" });
    setLoadind(true);
    try {
      if (actionType === "CREATE_FIELD") {
        const res = await axios.post<{
          data: {
            category_subcategory_Data_id: string;
            category_id: string;
            subcategory_id: string;
            title: string;
            description: string;
            others: [];
          };
        }>(`${API.AUTH}/categorysubcatedata`, {
          category_id: thisCategory.user_detail_category_id,
          subcategory_id: viewFields === "SUB_CATEGORY" ? subCategoryId : "",
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        });
        console.log({ res });
        createDocumentFields({
          _id: res.data.data.category_subcategory_Data_id,
          category_id: res.data.data.category_id,
          subcategory_id: res.data.data.subcategory_id,
          title: res.data.data.title,
          description: res.data.data.description,
          others: res.data.data.others,
        });
        PushPopupAlert({
          title: `${
            viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
          }'s Field has been created!`,
          theme: PopupAlertTheme.success,
        });
        setLoadind(false);
        setDocFieldsTitle("");
        setDocFieldsDescription("");
      } else if (actionType === "UPDATE_FIELD") {
        const formData: {
          category_subcategory_data_id: string;
          category_id: string;
          subcategory_id: string;
          title: string;
          description: string;
          others: [];
        } = {
          category_subcategory_data_id: docFieldsId,
          category_id: thisCategory.user_detail_category_id,
          subcategory_id: subCategoryId,
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        };
        const reducerData: DocumentFieldsInterface = {
          _id: docFieldsId,
          category_id: thisCategory.user_detail_category_id,
          subcategory_id: subCategoryId,
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        };
        console.log({ formData });
        const res = await axios.patch<{ data: DocumentFieldsInterface }>(
          `${API.AUTH}/categorysubcatedata`,
          formData
        );
        if (res.data.data) {
          console.log({ updated: res.data.data });
          updateDocumentFields(reducerData);
          PushPopupAlert({
            title: `${
              viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
            }'s Field has been updated!`,
            theme: PopupAlertTheme.success,
          });
          setDocFieldsId("");
          setDocFieldsTitle("");
          setDocFieldsDescription("");
          setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      PushPopupAlert({
        title: "Updating Field",
        description: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      setLoadind(false);
    }
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div className="divide-y divide-gray-200">
        <div className="my-4 text-base text-gray-700 sm:text-lg">
          <div className="flex flex-row items-center">
            <span className="mr-2">Category:</span>
            <span className="text-xl font-semibold">{thisCategory.title}</span>
          </div>
          {subCategoryTitle && (
            <div className="flex flex-row items-center my-2">
              <span className="mr-2">Sub Category:</span>
              <span className="text-xl font-semibold">{subCategoryTitle}</span>
            </div>
          )}
          <div className="flex flex-col">
            <label>Title</label>
            <input
              type="text"
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Sub Category Title"
              value={docFieldsTitle}
              onChange={(e) => setDocFieldsTitle(e.target.value)}
              disabled={loading}
              required
            />
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Sub Category Description"
              style={{ resize: "none" }}
              value={docFieldsDescription}
              onChange={(e) => setDocFieldsDescription(e.target.value)}
              rows={2}
              disabled={loading}
              required
            ></textarea>
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              setActionType("");
              setDocFieldsId("");
              setDocFieldsTitle("");
              setDocFieldsDescription("");
            }}
            className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
              loading && "cursor-not-allowed"
            }`}
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
              loading && "cursor-wait"
            }`}
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : actionType === "CREATE_SUB"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default connect(null, {
  createDocumentFields,
  updateDocumentFields,
  PushPopupAlert,
})(CreateDocumentField);
