export enum ApplicationStates {
  // When the application has been created, but the
  // Not confirmed
  Initiated = "initiated",

  // When there is missing info like location
  Incomplete = "incomplete",

  // When requirement are not filled up to short listing
  Rejected = "rejected",

  // When docs wait for validation
  Pending = "pending",

  // application match with position requiments
  Shortlisted = "shortlisted",

  // Finishing all position exams
  Pass = "pass",

  // Failed exams
  Failed = "failed",

  // When got placed
  Placed = "placed",
}

export const ApplicationStatesDescription: {key: ApplicationStates, caption: string}[] = [
  { key: ApplicationStates.Initiated, caption: "Initiated" },
  { key: ApplicationStates.Failed, caption: "Failed" },
  { key: ApplicationStates.Incomplete, caption: "Incomplete" },
  { key: ApplicationStates.Pending, caption: "Pending" },
  { key: ApplicationStates.Placed, caption: "Placed" },
  { key: ApplicationStates.Rejected, caption: "Rejected" },
  { key: ApplicationStates.Shortlisted, caption: "Shortlisted" },
  { key: ApplicationStates.Pass, caption: "Passed" },
];

