import { ActionTypes } from "./types";
import * as Interfaces from '../../src/interfaces/index';
import { newAnswer } from "../containers/DoExam/DoExam";
import { ExamInterface, examStatus } from "../interfaces/exam";

/**
 * * ****************************** INTERFACES *****************************
 */

// export interface FetchAnswerAction {
//   type: ActionTypes.GET_USER_ANSWERS;
//   payload: Answer[];
// }

export interface getUserExamAnswers {
  type: ActionTypes.GET_USER_ANSWERS;
}

export interface addUserExamAnswers {
  type: ActionTypes.ADD_ANSWER;
  payload: newAnswer;
}

export interface loadExamInterface {
  type: ActionTypes.LOAD_EXAM;
  payload: {
    user_id: string,
    exam: ExamInterface
  };
}

export interface examQuestionAnswerInterfaceItem {
  user_id: string | null;
  question_id: string | null;
  user_answers: Interfaces.question_answer[];
}

export interface examQuestionAnswerInterface {
  user_id?: string | null;
  questionAnswers: newAnswer[]
}

export interface examAnswer {
  user_id?: string | null;
  questionAnswers: examQuestionAnswerInterface[]
}

export interface newExamAnswerInterface {
  questionAnswers: newAnswer[]
}

export interface DeleteExamAnswersAction {
  type: ActionTypes.deleteAnswers
}

export interface SubmitExamAnswersAction {
  type: ActionTypes.submitAnswersAction;
  payload: string;
}

export interface ChangeExamStatusAction {
  type: ActionTypes.CHANGE_EXAM_STATUS;
  payload: {
    user_id: string, 
    status: examStatus
  };
}

export interface examDataInterface {
  user_id?: string | null;
  examsList: ExamInterface[];
  exam_answers: ExamInterface | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */


export const loadExam = (user_id: string, exam: ExamInterface): loadExamInterface => {
  return {
    type: ActionTypes.LOAD_EXAM,
    payload: {
      user_id: user_id,
      exam: exam
    },
  };
};

export const addExamAnswer = (questionAnswer: newAnswer): addUserExamAnswers => {
  return {
    type: ActionTypes.ADD_ANSWER,
    payload: questionAnswer,
  };
};

export const deleteExamAnswers = (): DeleteExamAnswersAction => {
  return {
    type: ActionTypes.deleteAnswers,
  };
};

export const SubmitExamAnswers = (user_id: string): SubmitExamAnswersAction => {
  return {
    type: ActionTypes.submitAnswersAction,
    payload: user_id,
  };
};

export const ChangeExamStatus = (user_id: string, status: examStatus): ChangeExamStatusAction => {
  return {
    type: ActionTypes.CHANGE_EXAM_STATUS,
    payload: {
      user_id: user_id, 
      status: status
    },
  };
};