import React, { Fragment, ReactNode } from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import {
  Events,
  EventInterface,
  ExamEvents,
  CandidateProctoringInterface,
} from "../../interfaces";
import { eventDefiner, eventDefinerInterface } from "../../actions/proctoring";
import InformationModal from "../../components/InformationModal/InformationModal";

import {
  Auth,
  Socket,
  Proctoring,
  FC_AddMyDataToProctoring,
  FC_MeStartStopExam,
  FC_CandidateExamEvent,
  FC_BlockExam,
} from "../../actions";
import { MdFullscreen, MdFullscreenExit, MdReport } from "react-icons/md";

// props for the component
interface ProctoringCandidateOnlineProps {
  socket: Socket;
  eventDefiner: Function;
  auth: Auth;
  proctoring: Proctoring;
  FC_AddMyDataToProctoring: Function;
  FC_MeStartStopExam: Function;
  FC_CandidateExamEvent: Function;
  children: ReactNode;
  startExam: Function;
  stopExam: Function;
  restartExam: Function;
  FC_BlockExam: Function;
}

interface AppState {
  request_fullScreen: boolean;
  exit_fullScreen: boolean;
  warning: any;
  inform_modal: {
    title: string;
    description: string;
    active: boolean;
  };
}

let joined_room = false;

class _ProctoringCandidateOnline extends React.Component<
  ProctoringCandidateOnlineProps,
  AppState
> {
  constructor(props: ProctoringCandidateOnlineProps) {
    super(props);

    this.state = {
      request_fullScreen: this.props.proctoring.room.start_exam,
      exit_fullScreen: false,
      warning: null,
      inform_modal: {
        title: "",
        description: "",
        active: false,
      },
    };
  }

  componentDidMount() {
    const { email, fname, lname, user_id } = this.props.auth.user;
    // add the user to the list
    if (joined_room === false) {
      this.props.FC_AddMyDataToProctoring({
        active: true,
        email: email,
        fname,
        lname,
        user_id,
        muted: false,
        stopped: false,
        peer: "",
        peer_id: "",
        ref: "",
      } as CandidateProctoringInterface);
      this.props.socket.socket.emit(
        Events.JOIN_ROOM,
        {
          room_id: this.props.proctoring.room.room_id,
          user_id: this.props.auth.user.user_id,
          peer_id: "",
        },
        (users_list: any) => {}
      );
      joined_room = true;
    }

    // Listen events
    this.props.socket.socket.on(ExamEvents.START_EXAM_CLIENT, (event: any) =>
      this.examStart(true)
    );

    // Listen events
    this.props.socket.socket.on(ExamEvents.STOP_EXAM_CLIENT, (event: any) =>
      this.examStart(false)
    );

    // * WARNING CANDIDATE
    this.props.socket.socket.on(
      ExamEvents.CAND_WARNING_EXAM_CLIENT,
      (event: any) => {
        this.setState((prevState: AppState) => ({
          warning: true,
          inform_modal: {
            title: "WARNING",
            description: event.msg,
            active: true,
          },
        }));
        // Add event to the reducer
        this.props.FC_CandidateExamEvent(
          eventDefiner({
            description: event.msg,
            event_type: "WARNING",
            done_by: "Invigirator",
            room_id: this.props.proctoring.room.room_id,
            user_id: event.user_id,
          } as eventDefinerInterface)
        );
      }
    );

    // * STOP EXAM
    this.props.socket.socket.on(
      ExamEvents.CAND_STOP_EXAM_CLIENT,
      (event: any) => {
        if (event.user_id === this.props.auth.user.user_id) {
          this.setState((prevState: AppState) => ({
            inform_modal: {
              title: "STOP EXAM",
              description: event.msg,
              active: true,
            },
          }));
          this.props.FC_BlockExam(true);
          // Add event to the reducer
          this.props.FC_CandidateExamEvent(
            eventDefiner({
              description: event.msg,
              event_type: "STOP EXAM",
              done_by: "Supervisor",
              room_id: event.room_id,
              user_id: event.user_id,
            } as eventDefinerInterface)
          );
        }
      }
    );

    // * CONTINUE EXAM
    this.props.socket.socket.on(
      ExamEvents.CAND_CONTINUE_EXAM_CLIENT,
      (event: any) => {
        if (
          event.user_id === this.props.auth.user.user_id &&
          this.props.proctoring.me.stopped === true
        ) {
          this.setState((prevState: AppState) => ({
            inform_modal: {
              title: "CONTINUE EXAM",
              description: "Invigirator allowed you to continue the exam",
              active: true,
            },
          }));

          this.props.FC_BlockExam(false);
          // Add event to the reducer
          this.props.FC_CandidateExamEvent(
            eventDefiner({
              description: "Invigirator allowed you to continue the exam",
              event_type: "CONTINUE EXAM",
              done_by: "Supervisor",
              room_id: event.room_id,
              user_id: event.user_id,
            } as eventDefinerInterface)
          );
        }
      }
    );

    // * CONTINUE EXAM
    this.props.socket.socket.on(
      ExamEvents.CAND_RESTART_EXAM_CLIENT,
      (event: any) => {
        if (event.user_id === this.props.auth.user.user_id) {
          this.setState((prevState: AppState) => ({
            inform_modal: {
              title: "RESTART EXAM",
              description: event.msg,
              active: true,
            },
          }));
          this.props.FC_BlockExam(false);

          // Add event to the reducer
          this.props.FC_CandidateExamEvent(
            eventDefiner({
              description: event.msg,
              event_type: "RESTART EXAM",
              done_by: "Supervisor",
              room_id: event.room_id,
              user_id: event.user_id,
            } as eventDefinerInterface)
          );
          // * Restart Function
          this.props.restartExam();
        }
      }
    );

    //** EVENTS BROWSER */
    // document.addEventListener("mouseenter", (e) => {
    //   this.eventBrodcaster(
    //     "LEAVING BROWSER",
    //     "The candidate is try to leave the browser"
    //   );
    // });

    // document.addEventListener("mouseleave", (event) =>
    //   this.eventBrodcaster(
    //     "LEAVING BROWSER",
    //     "The candidate is try to leave the browser"
    //   )
    // );

    document.addEventListener("copy", (event) => {
      if (this.props.proctoring.room.start_exam === true) {
        event.preventDefault();
      }
      this.eventBrodcaster(
        "COPY TEXT",
        "The candidate is try to copy the text"
      );
    });

    document.addEventListener("paste", (event) => {
      if (this.props.proctoring.room.start_exam === true) {
        event.preventDefault();
      }
      this.eventBrodcaster("TEXT PASTE", "pasted a text");
    });

    document.addEventListener("visibilitychange", (event) =>
      this.eventBrodcaster("ZOOMING OUT", "Changing the visibility")
    );

    // ! ERROR: causing to stop the candidate once they click on a modal into the browser
    // document.addEventListener("focusout", (event) =>
    //   this.eventBrodcaster(
    //     "LEAVING BROWSER",
    //     "The candidate is try to leave the browser"
    //   )
    // );

    window.addEventListener("blur", () =>
      this.eventBrodcaster(
        "LEAVING BROWSER",
        "The candidate is try to leave the browser"
      )
    );

    // prevent the right-click
    document.addEventListener("contextmenu", (event) => {
      if (this.props.proctoring.room.start_exam === true) {
        event.preventDefault();
      }
    });
  }

  componentWillUnmount = () => {
    this.props.socket.socket.off(ExamEvents.START_EXAM_CLIENT);
    this.props.socket.socket.off(ExamEvents.STOP_EXAM_CLIENT);
    this.props.socket.socket.off(ExamEvents.CAND_CONTINUE_EXAM_CLIENT);
    this.props.socket.socket.off(ExamEvents.CAND_RESTART_EXAM_CLIENT);
    this.props.socket.socket.off(ExamEvents.CAND_WARNING_EXAM_CLIENT);
    this.props.socket.socket.off(ExamEvents.CAND_STOP_EXAM_CLIENT);
  };

  // ***************************************************************************

  /**
   * @description define wheter the exam has started or not
   * @param state boolean
   */
  examStart = (state: boolean) => {
    if (state === true) {
      // start exam
      this.setState({
        request_fullScreen: true,
        exit_fullScreen: false,
      });

      this.props.FC_MeStartStopExam(true);
      this.props.startExam();
    } else {
      // stop exam mode
      this.setState({
        request_fullScreen: false,
        exit_fullScreen: true,
      });

      this.props.FC_MeStartStopExam(false);
      this.props.stopExam();
    }
  };

  closeInfoModal = () =>
    this.setState({
      inform_modal: {
        active: false,
        description: "",
        title: "",
      },
    });

  /**
   * @description Request the fullscreen
   */
  requestFullScreen = () => {
    if (!document.fullscreenElement) {
      document.documentElement.requestFullscreen();
    }
    this.setState({
      request_fullScreen: false,
      exit_fullScreen: false,
    });
  };

  /**
   * @description Exist the fullscreen
   */
  exitFullScreen = () => {
    this.setState({
      request_fullScreen: false,
      exit_fullScreen: false,
    });
    if (document.fullscreenElement) {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      }
    }
  };

  /**
   * @description get all the events and send them to the supervisor
   * @param event
   */
  eventBrodcaster = (type: string, description: string) => {
    if (this.props.proctoring.room.start_exam === true) {
      let event: EventInterface = eventDefiner({
        description: description,
        user_id: this.props.auth.user.user_id,
        done_by: "System",
        room_id: this.props.proctoring.room.room_id,
        event_type: type,
      } as eventDefinerInterface);

      // Add event to the reducer
      this.props.FC_CandidateExamEvent(event);
      this.props.stopExam();

      this.props.FC_BlockExam(true);

      // send the event on the server
      this.props.socket.socket.emit(ExamEvents.CAND_EXAM_EVENT_SERVER, event);
    }
  };

  render() {
    //* candidate stopped screen
    if (
      this.props.proctoring.me.stopped === true &&
      this.props.proctoring.room.start_exam === true
    ) {
      let lastEvent: EventInterface | null = null;
      let events_list = this.props.proctoring.events;
      if (events_list.length >= 1) {
        lastEvent = events_list[events_list.length - 1];
      }
      return (
        <div className="p-5 mx-auto mt-6 text-center bg-red-100 max-w-lg max-w-2/3 w-full rounded-md animate__bounceIn">
          <MdReport className="text-9xl text-red-900 inline" />
          <h1 className="text-2xl text-red-900 font-bold">
            You have beed stopped <br /> to continue the exam
          </h1>

          {lastEvent !== null && (
            <Fragment>
              <div className="mt-2">
                <h5 className="text-lg  text-red-800 inline-block">
                  You violated the rule of:
                </h5>
                <h2 className="text-md text-white font-bold bg-red-600 border-4 py-0.5 px-3 rounded-full inline-block ml-2 mx-auto">
                  {lastEvent?.event_type}
                </h2>
              </div>

              <h3 className="text-md text-red-800 mt-3 font-bold">
                {lastEvent?.description}
              </h3>
            </Fragment>
          )}

          <div className="mt-5 border-t border-red-900 border-opacity-50 text-sm text-red-900 text-opacity-80 pt-3 mx-5">
            <b>The exam will be saved from where you left off,</b> <br />
            You can explain the situation to the supervisor <br /> If your
            reasons are clear you will be gived another chance
          </div>
        </div>
      );
    }

    //* Request for the full screen
    if (this.state.request_fullScreen === true) {
      return (
        <div className="bg-green-100 w-auto mt-6 rounded-md mx-auto text-center p-5 max-w-sm animate__bounceIn">
          <div className="">
            <MdFullscreenExit className="text-9xl inline text-green-600 opacity-50" />
            <h1 className="text-3xl text-green-500 font-bold">
              Go Full-Screen mode{" "}
            </h1>
            <p className="mt-2 text-gray-700">
              You need to go in <b>Full-screen mode</b>, in order to get a good
              experience (Important)
            </p>
            <button
              className=" bg-green-600 hover:bg-green-800 text-white p-3 font-bold text-md rounded-md w-full mt-3"
              onClick={() => this.requestFullScreen()}
            >
              Yes, go full-screen
            </button>
          </div>
        </div>
      );
    }

    //* Exit for the full screen
    if (this.state.exit_fullScreen === true) {
      return (
        <div className="bg-blue-100 w-auto mt-6 rounded-md mx-auto text-center p-5 max-w-sm animate__bounceIn">
          <div className="">
            <MdFullscreen className="text-9xl inline text-blue-600 opacity-50" />
            <h1 className="text-3xl text-blue-600 font-bold">
              Quit Full-Screen mode
            </h1>
            <p className="mt-2 text-gray-700">
              Exam is completed, you can quit the <b>Fullscreen mode</b>
              (optional)
            </p>
            <button
              className=" bg-blue-600 text-white p-3 font-bold text-md rounded-md w-full mt-3"
              onClick={() => this.exitFullScreen()}
            >
              Exit full-screen
            </button>
          </div>
        </div>
      );
    }

    return (
      <Fragment>
        {this.state.inform_modal.active === true && (
          <InformationModal
            title={this.state.inform_modal.title}
            description={this.state.inform_modal.description}
            close={this.closeInfoModal}
          />
        )}

        {this.props.children}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  socket,
  auth,
  proctoring,
}: StoreState): { socket: Socket; proctoring: Proctoring; auth: Auth } => {
  return { socket: socket, auth, proctoring };
};

export const ProctoringCandidateOnline = connect(mapStateToProps, {
  eventDefiner,
  FC_AddMyDataToProctoring,
  FC_MeStartStopExam,
  FC_CandidateExamEvent,
  FC_BlockExam,
})(_ProctoringCandidateOnline);
