/**
 * @description check whether the 2 stae factor is enabled or not
 * @returns null
 */
export const FC_GetTwoFactorAuth = (callbackFunc: Function | null = null) => {
  return async (dispatch: Dispatch) => {
    const res = await axios.get<API_TwoFactorAuth>(`${API.AUTH}/settings/2fa`);

    let status: boolean;
    if (res.data.status === "enabled") {
      status = true;
    } else {
      status = false;
    }
    callbackFunc?.(res.data.status);

    dispatch<GetTwoFactorAuth>({
      type: ActionTypes.SET_TWO_FACTOR_AUTH,
      payload: status,
    });
  };
};

/**
 * @description Register the account to the api
 * @param account
 * @param ResponseFunc return the error from the API
 * @returns
 */
export const FC_RegisterAccount = (
  account: {
    n_id: string;
    phone_number: string;
    email: string;
    password: string;
  },
  ResponseFunc: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.post<API_Register>(
        `${API.AUTH}/users/createacc`,
        account
      );

      console.log({ CODE: res.data?.code });

      dispatch<UserRegisterDetails>({
        type: ActionTypes.USER_REGISTER_DETAILS,
        payload: res.data,
      });

      ResponseFunc(true, "");
    } catch (error) {
      ResponseFunc(false, errorToText(error));
    }
  };
};

