/**
 * @description Register the account to the api
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_RegisterCheckCode = (
  data: {
    n_id: string;
    activation_code: number;
  },
  MsgHandler: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.post<{ message: string }>(
        `${API.AUTH}/users/verifyacc`,
        data
      );

      // send the message
      MsgHandler(true, res.data.message);

      dispatch<CleanUserDetails>({
        type: ActionTypes.CLEAN_USER_DETAILS,
      });
    } catch (error) {
      console.log({ code_error: errorToText(error) });
      MsgHandler(false, errorToText(error));
    }
  };
};

