/**
 * @description Register the account to the api
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_Login = (
  data: {
    email: string;
    password: string;
    verify_type: string;
    two_step_active: boolean;
  },
  CallbackFunc: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      if (data.two_step_active === true) {
        //* Two step Authenttication is activated
        const res = await axios.post<API_LoginDetails>(
          `${API.AUTH}/users/login`,
          data
        );
        // console.log("two step auth active: ", data.two_step_active);
        // console.log(res.data);
        console.log({ CODE: res.data.code });
        dispatch<UserLoginDetails>({
          type: ActionTypes.USER_LOGIN_DETAILS,
          payload: { ...res.data, email: data.email },
        });
      } else {
        //* without authentication
        const res = await axios.post<API_UserLoginDetailsData>(
          `${API.AUTH}/users/login`,
          data
        );
        // console.log(res.data);

        localStorage.setItem(APP_TOKEN_NAME, res.data.user_jwt);
        dispatch<LoginSuccessDetails>({
          type: ActionTypes.USER_LOGIN_SUCCESS_DATA,
          payload: res.data,
        });
      }

      CallbackFunc(true, "");
    } catch (error) {
      console.log({ login: errorToText(error) });
      CallbackFunc(false, errorToText(error));
    }
  };
};

export interface FC_LoginCheckCode__API {
  email: string;
  verification_code: number;
}
/**
 * @description Register the account to the api
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_LoginCheckCode = (
  data: FC_LoginCheckCode__API,
  MsgHandler: Function
) => {
  // console.log({
  //   data,
  // });
  return async (dispatch: Dispatch) => {
    try {
      // const res = await axios.post<API_UserLoginDetailsData>(
      const res = await axios.post<API_UserLoginDetailsData>(
        `${API.AUTH}/users/loginverify`,
        data
      );

      // console.log({
      //   data_res_data: res.data,
      // });

      localStorage.setItem(APP_TOKEN_NAME, res.data.user_jwt);
      // send the message
      dispatch<LoginSuccessDetails>({
        type: ActionTypes.USER_LOGIN_SUCCESS_DATA,
        payload: {
          user_data: res.data.user_data,
          user_jwt: res.data.user_jwt,
          message: "Login success",
        } as API_UserLoginDetailsData,
      });

      MsgHandler(true, "");
    } catch (error) {
      console.log({ error });
      // MsgHandler(false, "Failed to login");
      MsgHandler(false, errorToText(error));
    }
  };
};

/**
 * @description Check if the user is logged in based on the logged in account
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */

export const FC_CheckLoggedIn = () => {
  return async (dispatch: Dispatch) => {
    if (!localStorage[APP_TOKEN_NAME]) {
      dispatch<LogoutUser>({
        type: ActionTypes.LOGOUT,
      });
      return false;
    }
    try {
      setAxiosToken();

      const res = await axios.get<API_UserLoginDetailsData__UserData>(
        `${API.AUTH}/currentuser`
      );

      dispatch<LoginSuccessDetails>({
        type: ActionTypes.USER_LOGIN_SUCCESS_DATA,
        payload: {
          message: "",
          user_jwt: localStorage[APP_TOKEN_NAME],
          user_data: res.data,
        },
      });
    } catch (error) {
      // console.log({ code_error: errorToText(error) });
      dispatch<LogoutUser>({
        type: ActionTypes.LOGOUT,
      });
    }
  };
};

