export const FC_Access = (
  auth: Auth,
  access_item: UserAccessList
): { create: boolean; delete: boolean; update: boolean; view: boolean } => {
  let findAccess = auth.user_access.access.find(
    (itm) => itm.key === access_item
  );
  if (findAccess !== undefined) {
    return findAccess.permitted;
  } else {
    return { create: false, delete: false, update: false, view: false };
  }
};

