import { ActionTypes } from "../actions";
import { SystemActionType } from "../actions/settings";
import {
  ApplicationSessionInterface,
  SystemSettingsInterface,
} from "../shared/interfaces";

export interface SystemSettingsType {
  systemSettings: SystemSettingsInterface[];
  applicationSessions: ApplicationSessionInterface[];
}

// default state
const initialState = {
  systemSettings: [],
  applicationSessions: [],
};

export const systemSettingsReducer = (
  state: SystemSettingsType = initialState,
  action: SystemActionType
) => {
  let index: number = -1;
  let data: SystemSettingsInterface[] = [];
  let _data: ApplicationSessionInterface[] = [];
  switch (action.type) {
    case ActionTypes.GET_SYSTEM_SETTINGS:
      return {
        ...state,
        systemSettings: action.payload,
      };
    case ActionTypes.DISABLE_SYSTEM_SETTINGS:
      data = [...state.systemSettings];
      index = state.systemSettings.findIndex(
        (item) => item.system_setting_id === action.payload.system_setting_id
      );
      if (index >= 0) {
        data[index].status = action.payload.status;
      }
      return {
        ...state,
        systemSettings: data,
      };
    case ActionTypes.GET_APPLICATION_SESSIONS:
      return {
        ...state,
        applicationSessions: action.payload,
      };
    case ActionTypes.CREATE_APPLICATION_SESSIONS:
      return {
        ...state,
        applicationSessions: [...state.applicationSessions, action.payload],
      };
    case ActionTypes.UPDATE_APPLICATION_SESSIONS:
      _data = [...state.applicationSessions];
      // index = -1;
      // index = _data.findIndex(
      //   (item) =>
      //     item.application_session_id === action.payload.application_session_id
      // );
      _data.forEach((item, index) => {
        console.log(index); // 0, 1, 2
        console.log(item); // 9, 2, 5
        if (
          _data[index].application_session_id ===
          action.payload.application_session_id
        ) {
          _data[index].application_session_name =
            action.payload.application_session_name;
          _data[index].period = action.payload.period;
          _data[index].application_start_date =
            action.payload.application_start_date;
          _data[index].application_end_date =
            action.payload.application_end_date;
          _data[index].is_active = action.payload.is_active;
        } else {
          if (action.payload.is_active) {
            _data[index].is_active = false;
          }
        }
      });
      // if (index >= 0) {
      //   _data[index].application_session_name =
      //     action.payload.application_session_name;
      //   _data[index].period = action.payload.period;
      //   _data[index].application_start_date =
      //     action.payload.application_start_date;
      //   _data[index].application_end_date = action.payload.application_end_date;
      //   _data[index].is_active = action.payload.is_active;
      // }
      return {
        ...state,
        applicationSessions: _data,
      };
    case ActionTypes.DISABLE_APPLICATION_SESSION:
      _data = [...state.applicationSessions];
      index = -1;
      index = _data.findIndex(
        (item) =>
          item.application_session_id === action.payload.application_session_id
      );
      if (index >= 0) {
        _data[index].is_active = action.payload.status;
      }
      return {
        ...state,
        applicationSessions: _data,
      };
    default:
      return state;
  }
};
