import { Action, ActionTypes } from "../actions";
import { applicationsListStoreInterface } from "../actions/ApplicationsList";

// default state
const defaultState: applicationsListStoreInterface = {
  applicationsList: null,
  selected_application: null,
  selected_position: null,
  positions_list: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const applicationsListReducer = (state: applicationsListStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOAD_APPLICATIONS_LIST:
      if (state.applicationsList !== null) {
        state.applicationsList.length = 0;
      }
      state.applicationsList = action.payload;
      return state;
    case ActionTypes.LOAD_POSITIONS:
      if (state.positions_list !== null) {
        state.positions_list.length = 0;
      }
      state.positions_list = action.payload;
      return state;
    case ActionTypes.SELECTED_POSITION:
      state.selected_position = action.payload;
      return state;
    default:
      return state;
  }
};
