import { Auth, Action, ActionTypes, AccessItem, UserAccess } from "../actions";
import { APP_TOKEN_NAME } from "../utils/AxiosToken";
// import {CandidateInterface} from '../interfaces/candidatesInterfaces'
// default state
const defaultState = {
  loading: true,
  isAuthenticated: false,
  id: "",
  token: "",
  rules: [],
  two_factor_auth: {
    loaded: false,
    active: false,
    type: "email",
  },
  user: {
    user_id: "",
    n_id: "",
    fname: "",
    lname: "",
    date_of_birth: "",
    district: "",
    email: "",
    phone_number: "",
    stoped: false,
    active: false,
    // more details
    application_number: "",
    cell: "",
    civil_status: "",
    country_of_birth: "",
    date_of_expiry: "",
    date_of_issue: "",
    document_type: "",
    father_names: "",
    issue_number: "",
    mother_names: "",
    photo: "",
    place_of_birth: "",
    place_of_issue: "",
    province: "",
    sector: "",
    sex: "",
    spouse: "",
    status: "",
    village: "",
    village_id: "",
    // users events
    stopped: false,
    ref: null,
    muted: false,
    room_id: "",
    video: true,
    stream: false,
    peer: null,
    peer_id: "",
    type: "",
    id: "",
  },
  document_types: {
    categories: [],
    sub_categories: [],
  },
  documents: [],
  user_access: {
    system_user_role_id: "",
    role: "",
    access: [],
  },
} as Auth;

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const authReducer = (state: Auth = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOGIN_DETAILS:
      return { ...state, ...action.payload };
    case ActionTypes.SET_TWO_FACTOR_AUTH:
      return {
        ...state,
        two_factor_auth: {
          ...state.two_factor_auth,
          loaded: true,
          active: action.payload,
        },
      } as Auth;
    case ActionTypes.USER_REGISTER_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          ...action.payload,
        },
      } as Auth;
    case ActionTypes.CLEAN_USER_DETAILS:
      return {
        ...defaultState,
      };
    case ActionTypes.USER_LOGIN_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          user_id: action.payload.user_id,
          fname: action.payload.fname,
          lname: action.payload.lname,
          email: action.payload.email,
        },
      } as Auth;
    case ActionTypes.USER_LOGIN_SUCCESS_DATA:
      //* Prepare the access data
      let custom_access_list =
        action.payload.user_data.custome_access == null
          ? []
          : (action.payload.user_data.custome_access.access as AccessItem[]);
      let user_access_list = action.payload.user_data.default_access
        .access as AccessItem[];

      //* User access data
      let user_access = {
        system_user_role_id:
          action.payload.user_data.default_access.system_user_role_id,
        role: action.payload.user_data.default_access.role,
        access: user_access_list?.map((access_el: AccessItem) => {
          // check if access item exists
          let item_exists = custom_access_list?.find(
            (itm_el: AccessItem) => itm_el.id === access_el.id
          );

          if (item_exists === undefined) {
            return access_el;
          } else {
            return item_exists;
          }
        }),
      } as UserAccess;
      return {
        ...state,
        loading: false,
        token: action.payload.user_jwt,
        isAuthenticated: true,
        user: {
          ...state.user,
          n_id: action.payload.user_data.n_id,
          fname: action.payload.user_data.fname,
          lname: action.payload.user_data.lname,
          date_of_birth: action.payload.user_data.date_of_birth,
          district: action.payload.user_data.district,
          email: action.payload.user_data.email,
          phone_number: action.payload.user_data.phone_number,
          user_id: action.payload.user_data.user_id,
        },
        user_access: user_access,
      } as Auth;
    case ActionTypes.GET_MY_PROFILE_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          ...action.payload.user_info,
        },
        document_types: {
          categories: action.payload.categeries,
          sub_categories: action.payload.subcategories,
        },
        documents: action.payload.details,
      } as Auth;
    case ActionTypes.UPLOAD_USER_DOCUMENT:
      return {
        ...state,
        documents: [...state.documents, action.payload],
      } as Auth;
    case ActionTypes.DELETE_USER_DOCUMENT:
      return {
        ...state,
        documents: state.documents.filter((el) => el.doc_id !== action.payload),
      } as Auth;
    case ActionTypes.UPDATE_PROFILE_DETAILS:
      return {
        ...state,
        user: {
          ...state.user,
          email: action.payload.email,
          phone_number: action.payload.phone_number,
        },
      } as Auth;
    case ActionTypes.UPDATE_USER_DOCUMENT:
      return {
        ...state,
        documents: state.documents.map((itm) =>
          itm.doc_id === action.payload.doc_id ? action.payload : itm
        ),
      } as Auth;

    case ActionTypes.LOGOUT:
      localStorage.removeItem(APP_TOKEN_NAME);
      return {
        ...defaultState,
        loading: false,
      };
    case ActionTypes.CHANGE_TWHO_FACTOR_TYPE:
      return {
        ...state,
        two_factor_auth: {
          ...state.two_factor_auth,
          type: action.payload,
        },
      } as Auth;
    default:
      return state;
  }
};
