import React from 'react'
import { BiChat } from 'react-icons/bi'
import { IoNotifications } from 'react-icons/io5'
import { MdChevronLeft, MdChevronRight } from 'react-icons/md'
import { psychometric_questions_list } from '../QuestionsList/QuestionsList'
import { exam_question } from '../../../interfaces/exam_question';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
} from "../../../actions";
import { StoreState } from "../../../reducers";

interface FooterInterface {
  // currentPosition: Interfaces.PositionAPI | null;
  // setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  // setApplicationStep: (step: ApplicationStepsTypes) => void;
  windowStatus: {
    profile: boolean,
    questionsList: boolean,
  };
  setQuestionsListWindowStatus: (status: boolean) => void;
  setProfileWindowStatus: (status: boolean) => void;
  selectedQuestion: exam_question | null;
  setSelectedQuestion: (question: exam_question) => void;
  doExam: examDataInterface;
  auth: Auth;
}

const _Footer = (props: FooterInterface) => {
  let selectedIndexNext: number = props.selectedQuestion ? 
  parseInt(props.selectedQuestion.question_id) > 0 && 
  parseInt(props.selectedQuestion.question_id) < psychometric_questions_list.length ? 
  parseInt(props.selectedQuestion.question_id)-1 : -1 : 0;

  let selectedIndexPrevious: number = props.selectedQuestion ? 
  parseInt(props.selectedQuestion.question_id) > 1 ? 
  parseInt(props.selectedQuestion.question_id)-1 : psychometric_questions_list.length : -1;

  return (
    <div className="fixed bottom-0 flex flex-row justify-between items-center mr-2 px-2 bg-gray-700 rounded w-full">
      {console.log("Selected: ", selectedIndexNext)}
      <div className="flex flex-row items-center">
        <div onClick={() => props.setProfileWindowStatus(!props.windowStatus.profile)} 
          className={`cursor-pointer flex justify-center items-center bg-gray-700 my-2 px-1 rounded`}>
          <div className="cursor-pointer text-gray-100 hover:text-blue-700 h-full py-2 pl-2 pr-3 rounded shadow-lg bg-gray-600 hover:bg-blue-200 w-max">
            <div className="animate-bounce bg-red-600 text-white text-xs font-bold p-1 w-max rounded-full flex justify-center items-center shadow-lg" style={{position: 'relative', top: '-10px', right: '-12px'}}>02</div>
            <IoNotifications className="text-2xl -mt-5" />
          </div>
          {props.windowStatus.questionsList === true && 
          <div className="bg-gray-700 text-gray-300 px-1 py-1 rounded text-xs ml-1 font-bold">New events <br/>are available</div>}
        </div>

        <button 
        onClick={() => props.setSelectedQuestion(psychometric_questions_list[selectedIndexPrevious-1])}
        className="btn btn-blue flex justify-center items-center" style={{height: 'fit-content'}}><MdChevronLeft className="text-xl" /> <span className="hidden md:block">Previous</span></button>


        <div className="hidden md:flex flex-col text-gray-400 px-2">
          <span className="font-bold">Question {props.selectedQuestion && props.selectedQuestion.question_id}</span>
          <span className="text-xs">Auto save is activated</span>
        </div>
      </div>
      <div className="flex flex-row gap-1">
        {/* <div className="px-3 py-2 rounded hover:bg-gray-800 shadow-lg cursor-pointer bg-gray-600 text-white flex items-center"><FiVolume2 className="text-2xl" /></div> */}
        {/* <div className="px-3 py-2 rounded hover:bg-gray-800 shadow-lg cursor-pointer bg-gray-600 text-white flex items-center"><FaMicrophone className="text-xl" />&nbsp; Request to speak</div> */}
        <div className="px-3 py-2 rounded hover:bg-gray-800 shadow-lg cursor-pointer bg-gray-600 text-white flex items-center"><BiChat className="text-2xl" />&nbsp; <span className="hidden md:block">Discussion</span></div>
        <button 
        onClick={() => props.setSelectedQuestion(psychometric_questions_list[selectedIndexNext+1])}
        className="btn btn-blue flex items-center"><span className="hidden md:block">Next</span> <MdChevronRight className="text-xl" /></button>
      </div>
    </div>
  )
}


const mapStateToProps = ({
  todos,
  auth,
  doExam,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface } => {
  return { todos: todos, auth: auth, doExam: doExam };
};

const Footer = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers
})(_Footer);

export default Footer;