import React, { Component, Fragment } from 'react'
import { FiExternalLink, FiFilter } from 'react-icons/fi';
import { IoMdCloudUpload } from 'react-icons/io';
import { Link } from "react-router-dom";
import * as Interfaces from "../../interfaces/index";
import searchData from "../../utils/search";

import { connect } from "react-redux";
import {
  Auth, 
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
} from "../../actions";
import { StoreState } from "../../reducers";
import { course } from '../../interfaces/course';
import Loading from '../../components/Loading/Loading';
import Modal, { ModalSize, Themes } from '../../components/Modal/Modal';
import { FaAngleRight } from 'react-icons/fa';
import { searchItem } from '../../containers/Application/ApplicationPositions/ApplicationPositions';
import SchoolLevelsFilter from '../../containers/Application/ApplicationPositions/SchoolLevelsFilter/SchoolLevelsFilter';

interface UploadQuestionState {
  loading: boolean;
  searchLevel: searchItem | null;
  showFilter: boolean;
  uploadingStatus: boolean;
  searchCourseInput: string;
}
interface UploadQuestionProps {
  auth: Auth;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionBank: QuestionBankStoreInterface;
  selected_course_id: string | null;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory: (
    education_category: Interfaces.education_category
  ) => void;
  setSelectedSchoolLevel: (education_category: Interfaces.school_level) => void;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
}

class _UploadQuestion extends Component<UploadQuestionProps, UploadQuestionState> {
  constructor(props: UploadQuestionProps) {
		super(props);

		this.state = {
      loading: false,
      searchLevel: null,
      showFilter: false,
      uploadingStatus: false,
      searchCourseInput: "",
		};
	}

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  }

  setSearchLevel = (
    educationCategory: Interfaces.education_category | null,
    schoolLevel: Interfaces.school_level | null
  ) => {
    this.setState({
      searchLevel: {
        educationCategory: educationCategory,
        schoolLevel: schoolLevel,
      },
    });
    if (educationCategory) {
      this.props.setSelectedEducationCategory(educationCategory);
    }
    if (schoolLevel) {
      this.props.setSelectedSchoolLevel(schoolLevel);
      this.setState({ showFilter: false });
    }
  };

  setSchoolLevelFilterFn = (status: boolean) => {
    this.setState({ showFilter: status });
  };

  filteredCourses = (courses: course[], searched: searchItem | null): course[] => {
    let filtered: course[] = courses;
    if (searched !== null) {
      if (searched.schoolLevel !== null) {
        filtered = courses.filter((itm) => {
          if (itm.school_levels.find(itm1 => itm1 === searched.schoolLevel?.school_level_id)) {
            let status = false;
            for (let item2 of itm.school_levels) {
              if (searched.educationCategory?.school_levels.find(i => i === item2)) {
                status = true;
              }
            }
            return status === true ? itm : null;
          } else {
            return null;
          }
        })
      } else if (searched.educationCategory !== null) {
        filtered = courses.filter((itm) => {
          let status = false;
          for (let item2 of itm.school_levels) {
            if (searched.educationCategory?.school_levels.find(i => i === item2)) {
              status = true;
            }
          }
          return status === true ? itm : null;
        })
      }
    }
    return filtered;
  }

  setUploadingStatus = (status: boolean) => {
    this.setState({ uploadingStatus: status});
  }

  componentDidMount = () => {
    if (this.props.course.courses === null || this.props.course.courses.length === 0) {
      this.props.loadCourses(this.setLoading);
    }
  }

  render() {
    return (
      <Fragment>
        <div className="flex-col">
          <div className="my-4 grid md:grid-cols-5 gap-2">
            <div className={`animate__animated md:col-span-5 md:flex md:flex-col min-h-full bg-white shadow rounded`}>
              <div className="py-2 px-2">
                <div className="flex flex-row gap-2 mb-2 items-center justify-between">
                  <div className="flex flex-col">
                    <div><span className="text-lg text-gray-600 font-bold">Choose a course</span></div>
                    <div className="flex flex-row items-center">
                      {this.state.searchLevel?.educationCategory && <span className="bg-blue-50 rounded-full px-2 text-xs text-blue-600 font-bold">{this.state.searchLevel.educationCategory?.education_category_title}</span>}
                      {this.state.searchLevel?.schoolLevel && <div className="flex flex-row items-center"><FaAngleRight className="" /><span className="bg-blue-50 rounded-full px-2 text-xs text-blue-600 font-bold">{this.state.searchLevel.schoolLevel?.school_level_name}</span></div>}
                    </div>
                  </div>
                  <div onClick={() => { 
                    this.props.schoolLevels.education_categories === null && this.props.loadEducationCategories(this.setLoading);
                    this.props.schoolLevels.school_levels === null && this.props.loadSchoolLevels(this.setLoading);
                    this.setState({ showFilter: true }) }} className="btn cursor-pointer bg-blue-50 hover:bg-blue-100 text-blue-600 flex flex-row gap-1 items-center">
                    <FiFilter className="text-xl" />
                    <span>Filter</span>
                  </div> 
                </div>
                <div>
                  <input onChange={(e: React.ChangeEvent<HTMLInputElement>) => this.setState({ searchCourseInput: e.target.value })} type="text" className="input-white h-10 text-sm border-2 border-blue-500" placeholder="Search question by title or description" />
                </div>
                <div className="mt-4" style={{minHeight: '500px'}}>
                  {this.state.uploadingStatus === true ? <div className="my-2 flex flex-col gap-2">
                    <div className="rounded-xl bg-gray-100 h-8"></div>
                    <div className="rounded-xl bg-gray-100 h-8 w-4/5"></div>
                    <div className="rounded-xl bg-gray-100 h-8 w-5/6"></div>
                    <div className="rounded-xl bg-gray-100 h-8"></div>
                    <div className="rounded-xl bg-gray-100 h-8 w-3/4"></div>
                    <div className="rounded-xl bg-gray-100 h-8"></div>
                  </div> :
                  this.props.course.courses === null ? <Loading /> :
                  this.props.course.courses && this.filteredCourses(this.props.course.courses, this.state.searchLevel).length === 0 ? <span className="text-gray-400 font-extrabold ml-2 animate__animated animate__shakeX">No courses available!</span> :
                  this.props.course.courses && searchData(this.filteredCourses(this.props.course.courses, this.state.searchLevel), this.state.searchCourseInput).map((item, i) => (
                    <div key={i + 1} 
                    onClick={() => this.props.setSelectedCourse(item)} 
                    className={`rounded border ${this.props.selected_course_id !== null && this.props.selected_course_id === item.course_id ? "bg-blue-100 border-blue-300 text-blue-600 animate__animated animate__bounceIn" : "border-gray-200 hover:bg-blue-100 hover:border-blue-600 text-gray-600"}  px-2 py-3 flex flex-row gap-3 items-center my-2 cursor-pointer`}>
                      <span className="text-sm">
                        <div className={`h-8 w-8 rounded-full flex justify-center items-center text-white ${this.props.selected_course_id !== null && this.props.selected_course_id === item.course_id ? "bg-blue-600 text-white" : "font-bold bg-gray-500"}`}>{i + 1}</div>
                      </span>
                      <div className="flex flex-col">
                        <span className={`text-md font-bold`}>{item.course_name}</span>
                        <span className={`text-xs`}>Code: {item.course_code}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
        {this.state.showFilter === true &&
        <Modal
          title="Filter courses by school levels"
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ showFilter: false })}
          backDropClose={true}
          widthSizeClass={ModalSize.medium}
          displayClose={true}
          padding={{title: true,}}
        >
          <SchoolLevelsFilter 
            educationCategories={this.props.schoolLevels.education_categories}
            schoolLevels={this.props.schoolLevels.school_levels}
            searchLevel={this.state.searchLevel}
            setSearchLevel={this.setSearchLevel}
            setSchoolLevelFilterFn={this.setSchoolLevelFilterFn}
          />
        </Modal>}
      </Fragment>
    )
  }
}

const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
}: StoreState): { auth: Auth; course: courseStoreInterface; schoolLevels: schoolLevelsStoreInterface; questionBank: QuestionBankStoreInterface; } => {
  return { auth: auth, course: course, schoolLevels: schoolLevels, questionBank: questionBank };
};

const UploadQuestion = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
})(_UploadQuestion);

export default UploadQuestion;
